import { JDesign } from "./JDesign";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JWall } from "./JWall";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";


export abstract class JGeometry extends JDesign {
    /** 2d */
    private _url2D: string
    /** 2d */
    get url2D() { return this._url2D }
    set url2D(obj: string) { this._url2D = obj }

    /** 3d */
    private _url3D: string
    /** 3d */
    get url3D() { return this._url3D }
    set url3D(obj: string) { this._url3D = obj }

    /** hg */
    private _hgUrl: string;
    /** hg */
    get hgUrl() { return this._hgUrl }
    set hgUrl(obj: string) { this._hgUrl = obj }

    /**  */
    private _floorHeight: number
    /**  */
    get floorHeight() { return this._floorHeight }
    set floorHeight(obj: number) { this._floorHeight = obj }

    /**  */
    private _pos: DC.Pos
    /**  */
    get pos() { return this._pos }
    set pos(obj: DC.Pos) { this._pos = obj }

    /**  */
    private _radian: number
    /**  */
    get radian() { return this._radian }
    set radian(obj: number) { this._radian = obj }

    /**  :mm */
    private _size: DC.Size
    get size() { return this._size }
    set size(obj: DC.Size) { this._size = obj }

    /**  */
    private _mirror: boolean
    /**  */
    get mirror() { return this._mirror }
    set mirror(obj: boolean) { this._mirror = obj }

    /**  */
    private _wallID: string
    /**  */
    get wallID() { return this._wallID }
    set wallID(data) { this._wallID = data }

    /**  */
    private _wallOut: boolean
    /**  */
    get wallOut() { return this._wallOut }
    set wallOut(data) { this._wallOut = data }

    /** cowall */
    private _distancePer: number
    get distancePer() { return this._distancePer }
    set distancePer(obj: number) { this._distancePer = obj }

    constructor(obj: JGeometryInit, public dataService: JdesignDataService, public DUService: JdesignUtil2dService, private geometryType: string) {
        super({ type: geometryType, id: obj.id, flag: obj.flag }, dataService)
        this.mirror = obj.mirror ? obj.mirror : false
        this.size = obj.size ? obj.size : null
        this.pos = obj.pos ? obj.pos : { x: 0, y: 0 }
        this.wallID = obj.wallID ? obj.wallID : null
        this.distancePer = obj.distancePer ? obj.distancePer : 0
        this.url2D = obj.url2D ? obj.url2D : null
        this.url3D = obj.url3D ? obj.url3D : null
        this.radian = obj.radian ? obj.radian : 0
        this.floorHeight = obj.floorHeight
        this.hgUrl = obj.hgUrl ? obj.hgUrl : ''
        this.mode = 'move'
        this.wallOut = obj.wallOut ? obj.wallOut : false
    }

    /**  */
    getRotate(): number {
        let rotate: number = this.radian * 180 / Math.PI
        return rotate
    }

    /** wall */
    getwall(): JWall {
        if (this.wallID == null)
            return null
        else {
            let wall: JWall = this.dataService.walls[this.wallID]
            return wall
        }
    }

    /** size */
    getPixSize(): DC.Size {
        let newSize: DC.Size = {
            width: this.dataService.cmd.getLengthTransPixel(this.size.width, 'mm'),
            height: this.dataService.cmd.getLengthTransPixel(this.size.height, 'mm')
        }
        return newSize
    }

    /**  */
    getPixR(): number {
        let size = this.getPixSize()
        let distance = Math.sqrt(Math.pow(size.width / 2, 2) + Math.pow(size.height / 2, 2))
        return distance
    }
}

export class JGeometryInit {
    mirror?: boolean;
    wallID?: string;
    distancePer?: number;
    size?: DC.Size;
    radian?: number;
    pos?: DC.Pos;
    url2D?: string;
    url3D?: string;
    flag?: number;
    floorHeight?: number;
    wallOut?: boolean;
    hgUrl?: string;
    id?: string
}