import { JdesignDataService } from "../../../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../../../services/dom-update.service";
import { JdesignUtil2dService } from "../../../../../services/jdesign-util2d.service";
import { PopDivService } from "../../../../../services/pop-div.service";
import { CalculatorsService } from "../../../../../services/calculators.service";
import { JParamsModel } from "../../../jdesign/JParamsModel";
import { DesignCommon as DC } from '../../../class/DesignCommon'

/*  */
export class SvgParamsMoveParamsModelType {
    constructor(
        private dataService: JdesignDataService,
        private domService: DomUpdateService,
        private DUService: JdesignUtil2dService,
        private popService: PopDivService,
        private calService: CalculatorsService
    ) {

    }

    /*  */
    linerMove(obj: JParamsModel, presentPos: DC.Pos, prevPos: DC.Pos) {
        let newRadian = obj.radian - obj.movePointData.radian
        let distance = this.DUService.pos.getTwoPosRadianDistance(presentPos, prevPos, newRadian + Math.PI, false)
        console.log(distance)
        distance += parseFloat(obj.j2d.params[obj.movePointData.tag].formula) + obj.movePointData.add
        console.log(distance)
        obj.j2d.params[obj.movePointData.tag].formula = distance
    }
}