import { JDWAbstract, JDWInitType } from "./JDWAbstract";
import { JWall } from "./JWall";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JDW3D } from "../../../three/jDesign3D/JDW3D";


export class JDoor extends JDW3D {

    /**  id   */
    get wallID() { return this._wall }
    set wallID(obj: string) {
        if (this.wallID != null) {
            if (this.oldWallID != this.wallID && this.wallID != obj) {
                let wall: JWall = this.getwall()
                this.oldWallID = wall ? this.wallID : undefined
            }
            let wall: JWall = this.getwall()
            if (wall != undefined) {
                for (let i = 0; i < wall.doorIDs.length; i++) {
                    if (wall.doorIDs[i] == this.id) {
                        wall.doorIDs.splice(i, 1)
                        break;
                    }
                }
            }
        }
        this._wall = obj
        if (obj != null) {
            let wall: JWall = this.getwall()
            let same = false
            for (let i = 0; i < wall.doorIDs.length; i++) {
                if (wall.doorIDs[i] == this.id) {
                    same = true
                    break;
                }
            }
            if (!same) {
                wall.doorIDs.push(this.id)
            }
        }
    }


    constructor(
        obj: JDoorInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService) {
        super({ type: 'door', id: obj.id }, dataService, DUService)
        this.dataInit(obj)
        this.dataService.doors[this.id] = this
    }

    getWallDWIDs() {
        let wall = this.getwall()
        if (wall != undefined)
            return wall.doorIDs
        return undefined
    }


}

export class JDoorInitType extends JDWInitType {

}
