import { PopDivService } from "../../../services/pop-div.service";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../services/dom-update.service";
import { JParamsModel3D } from "../../../three/jDesign3D/JParamsModel3D";

export class ParamsModelPanel {
    constructor(
        private popService: PopDivService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService
    ) {
        popService.panelRight.subscribe((data) => {
            // console.log(data)
            if (data == null) {
                this.popService.panelRightData.emit(null)
            }
            else {
                if (data.type == 'paramsModel') {
                    // this.popService.panelRightData.emit(null)
                    this.setData(data)
                }
            }
        })
    }

    setData(obj: JParamsModel3D) {
        let inputs = []
        if (obj.inputData) {
            for (let i = 0; i < obj.inputData.length; i++) {
                let input = {
                    name: obj.inputData[i].inputName,
                    isEditable: false,
                    value: obj.j2d.params[obj.inputData[i].j2dName].formula,
                    func: (e, params) => {
                        let distance = parseFloat(params.value)
                        obj.j2d.params[obj.inputData[i].j2dName].formula = distance
                        this.domService.allSvg.emit()
                        this.dataService.cache.shortFuncs['saveAction']()
                    }
                }
                inputs.push(input)
            }
        }
        let data = [
            {
                type: "input",
                data: [...inputs]
            }
        ]
        this.popService.panelRightData.emit(data)
    }
}