export class CommonUtil {
    /**
* readyCallback
*/
    loadFile(url: string, readyCallback: (a) => void, progressCallback?, failCallback?) {
        let length: any = 0;
        if (document.implementation && document.implementation.createDocument) {
            var request = new XMLHttpRequest();
            request.onreadystatechange = function () {
                if (request.readyState === 4) {
                    if (request.status === 0 || request.status === 200) {
                        if (request.response) {
                            readyCallback(request.response);
                        } else {
                            if (failCallback) {
                                failCallback({ type: 'error', url: url });
                            } else {
                                console.error("(" + url + ")");
                            }
                        }
                    } else {
                        if (failCallback) {
                            failCallback({ type: 'error', url: url });
                        } else {
                            console.error('' + url + '" (' + request.status + ')');
                        }
                    }
                } else if (request.readyState === 3) {
                    if (progressCallback) {
                        if (length === 0) {
                            length = request.getResponseHeader("Content-Length");
                        }
                        progressCallback({ total: length, loaded: request.responseText.length });
                    }
                }
            };
            request.open("GET", url, true);
            request.send(null);
        } else {
            alert("!");
        }
    }

    /**  */
    quickSort(array) {
        //let array = [8,4,6,2,7,9,3,5,74,5];
        //let array = [0,1,2,44,4,324,5,65,6,6,34,4,5,6,2,43,5,6,62,43,5,1,4,51,56,76,7,7,2,1,45,4,6,7];
        let i = 0;
        let j = array.length - 1;
        let Sort = function (i, j) {

            // 
            if (i == j) {
                return
            };

            let key = array[i];
            let stepi = i; // 
            let stepj = j; // 
            while (j > i) {
                // j <<-------------- 
                if (array[j] >= key) {
                    j--;
                } else {
                    array[i] = array[j]
                    //i++ ------------>>
                    while (j > ++i) {
                        if (array[i] > key) {
                            array[j] = array[i];
                            break;
                        }
                    }
                }
            }

            //  key 
            if (stepi == i) {
                Sort(++i, stepj);
                return;
            }

            //  key
            array[i] = key;

            // 
            Sort(stepi, i);
            Sort(j, stepj);
        }

        Sort(i, j);

        //cb(array)
    }
    /**   */
    bubbleSort(arr) {
        var len = arr.length;
        for (var i = 0; i < len; i++) {
            for (var j = 0; j < len - 1 - i; j++) {
                if (arr[j] > arr[j + 1]) {        // 
                    var temp = arr[j + 1];        // 
                    arr[j + 1] = arr[j];
                    arr[j] = temp;
                }
            }
        }
        return arr;
    }

    /**   relArrayarray*/
    bubbleSortRel(array, relArray, cb) {
        let i = 0,
            len = array.length,
            j, d, e;
        for (; i < len; i++) {
            for (j = 0; j < len; j++) {
                if (array[i] < array[j]) {
                    d = array[j];
                    array[j] = array[i];
                    array[i] = d;
                    e = relArray[j]
                    relArray[j] = relArray[i]
                    relArray[i] = e;
                }
            }
        }
        cb()
    }

    /** ID */
    bubbleSortID(arr) {
        let arrID: number[] = []
        let len = arr.length;
        for (let i = 0; i < len; i++)
            arrID.push(i);
        for (let i = 0; i < len; i++) {
            for (let j = 0; j < len - 1 - i; j++) {
                if (arr[j] > arr[j + 1]) {        // 
                    let temp = arr[j + 1];        // 
                    arr[j + 1] = arr[j];
                    arr[j] = temp;
                    let tempID = arrID[j + 1];
                    arrID[j + 1] = arrID[j];
                    arrID[j] = tempID;
                }
            }
        }
        return arrID
    }

    /**   */
    quickSortS(array, attrs: string[], cb) {
        let newArray = new Array(array.length)
        this.createNewAttrArray(array, attrs, (newArray) => {
            let i = 0;
            let j = newArray.length - 1;
            let Sort = (i, j) => {

                // 
                if (i == j) {
                    return
                };

                let key = newArray[i];
                let c = array[i];
                let stepi = i; // 
                let stepj = j; // 
                while (j > i) {
                    // j <<-------------- 
                    if (newArray[j] >= key) {
                        j--;
                    } else {
                        newArray[i] = newArray[j]
                        array[i] = array[j]

                        //i++ ------------>>
                        while (j > ++i) {
                            if (newArray[i] > key) {
                                newArray[j] = newArray[i];
                                array[j] = array[i]
                                break;
                            }
                        }
                    }
                }

                //  key 
                if (stepi == i) {
                    Sort(++i, stepj);
                    return;
                }

                //  key
                newArray[i] = key;
                array[i] = c;

                // 
                Sort(stepi, i);
                Sort(j, stepj);
            }

            Sort(i, j);
            cb()
        })

    }

    /**   */
    getObjAttr(obj, attr: string) {
        return obj[attr]
    }

    /**  */
    getObjAttrs(obj, attrs: string[]) {
        let newObj = null;
        for (let i = 0; i < attrs.length; i++) {
            if (newObj == null)
                newObj = obj[attrs[0]];
            else
                newObj = newObj[attrs[i]];
        }
        return newObj
    }

    /**   */
    createNewAttrArray(array, attr: string[], cb) {
        let a = []
        for (let i = 0; i < array.length; i++) {
            a.push(this.getObjAttrs(array[i], attr))
        }
        cb(a)
    }

    /** key */
    getArrayKeys(stringArray, name?) {
        let objKeys = []
        for (let key in stringArray) {
            objKeys.push(key)
        }
        return objKeys
    }

    /**  */
    clone(obj) {
        let o;
        switch (typeof obj) {
            case 'undefined': break;
            case 'string': o = obj + ''; break;
            case 'number': o = obj - 0; break;
            case 'boolean': o = obj; break;
            case 'object':
                if (obj === null) {
                    o = null;
                } else {
                    if (obj instanceof Array) {
                        o = [];
                        // let i=0;i<obj.length;i++
                        for (let i = 0; i < obj.length; i++) {
                            o.push(this.clone(obj[i]));
                        }
                    } else {
                        o = {};
                        for (let k in obj) {
                            o[k] = this.clone(obj[k]);
                        }
                    }
                }
                break;
            default:
                o = obj; break;
        }
        return o;
    }

    /**  clone*/
    cloneArr(obj) {
        let newObj = new Array();
        for (let key in obj) {
            newObj[key] = this.clone(obj[key])
        }
        return newObj
    }

}