
import { DesignCommon as DC } from '../class/DesignCommon'

export class PosUtil {
    /**  */
    getCenterPos(posStart: DC.Pos, posEnd: DC.Pos): DC.Pos {
        return {
            x: (posStart.x + posEnd.x) / 2,
            y: (posStart.y + posEnd.y) / 2
        }
    }

    /**  */
    getPosByLinePer(posStart: DC.Pos, posEnd: DC.Pos, per: number) {
        let distance: number = this.getDistance(posStart, posEnd)
        let newDistance = distance * per
        let radian: number = this.getRadian(posStart, posEnd)
        let newPos: DC.Pos = this.getRayPos(posStart, radian, newDistance)
        return newPos
    }



    /** rect */
    getCenterPosByRect(pos: DC.Pos, size: DC.Size): DC.Pos {
        return {
            x: pos.x + (size.width / 2),
            y: pos.y + (size.height / 2)
        }
    }

    /** rect */
    getOriginPosByRect(centerpos: DC.Pos, size: DC.Size): DC.Pos {
        return {
            x: centerpos.x - (size.width / 2),
            y: centerpos.y - (size.height / 2)
        }
    }


    /**  ID*/
    getRectByTwoPos(posStart: DC.Pos, posEnd: DC.Pos) {
        let posA: DC.Pos = { x: posEnd.x, y: posStart.y }
        let posB: DC.Pos = { x: posStart.x, y: posEnd.y }
        return [posStart, posA, posEnd, posB]
    }

    /** (ABc) */
    getTriangleRadianByDistance(distanceA: number, distanceB: number, distanceC: number) {
        let cosC = (Math.pow(distanceA, 2) + Math.pow(distanceB, 2) - Math.pow(distanceC, 2)) / (2 * distanceA * distanceB)
        return Math.acos(cosC)
    }


    /**  */
    getTriangleRadianByPos(posStart: DC.Pos, posCenter: DC.Pos, posEnd: DC.Pos): number {
        let radian0 = this.getRadian(posStart, posCenter)
        let radian1 = this.getRadian(posCenter, posEnd)
        let newRadian = radian0 - radian1 + Math.PI
        return newRadian
    }

    /**  null*/
    getRadian(posStart: DC.Pos, posEnd: DC.Pos) {
        if (!posStart || !posEnd)
            return undefined
        let Y = posEnd.y - posStart.y
        let X = posEnd.x - posStart.x
        if (Y == 0 && X == 0)
            return undefined
        else
            return Math.atan2(Y, X)
    }

    /**
     * 
     * @param rotate 
     * @param isAbs ,,180,180,fasle
     */
    getAbsRotate(rotate: number, isAbs: boolean = false) {
        if (!isAbs) {
            while (rotate < -180) {
                rotate = rotate + 360
            }
            while (rotate > 180) {
                rotate = rotate - 360
            }
            return rotate
        }
        else {
            while (rotate < 0) {
                rotate = rotate + 360
            }
            while (rotate > 360) {
                rotate = rotate - 360
            }
        }
        return rotate
    }

    /**
     * 
     * @param radian 
     * @param isAbs ,(0~360),180,180,false
     */
    getAbsRadian(radian: number, isAbs: boolean = false) {
        if (!isAbs) {
            while (radian < -Math.PI) {
                radian = radian + (Math.PI * 2)
            }
            while (radian >= Math.PI) {
                radian = radian - (Math.PI * 2)
            }
            return radian
        }
        else {
            while (radian < 0) {
                radian = radian + (Math.PI * 2)
            }
            while (radian >= (Math.PI * 2)) {
                radian = radian - (Math.PI * 2)
            }
        }
        return radian
    }

    /**
     * 
     * @param distance 
     * @param radian 
     */
    getPlusPos(distance: number, radian: number) {
        let Y = Math.sin(radian) * distance
        let X = Math.cos(radian) * distance
        let newPos = { x: X, y: Y }
        return newPos
    }


    /**  null*/
    getRotate(posStart: DC.Pos, posEnd: DC.Pos) {
        let radian = this.getRadian(posStart, posEnd)
        if (radian == null)
            return null
        let rotate = radian * 180 / Math.PI
        return rotate
    }

    /**  */
    getPowDistance(posStart: DC.Pos, posEnd: DC.Pos) {
        let powDistance = Math.pow((posEnd.y - posStart.y), 2) + Math.pow((posEnd.x - posStart.x), 2)
        return powDistance;
    }

    /**  */
    getDistance(posStart: DC.Pos, posEnd: DC.Pos) {
        let powDistance = this.getPowDistance(posStart, posEnd)
        let distance = Math.sqrt(powDistance)
        return distance
    }

    /**   pos2,pos3*/
    getPosDistanceDirect(posContrast: DC.Pos, posStart: DC.Pos, posEnd: DC.Pos) {
        /**  */
        let distance = this.getDistance(posContrast, posStart);
        /**  */
        let lineDistance = this.getDistance(posStart, posEnd)
        /**  */
        let newDistance = this.getDistance(posContrast, posEnd)

        if (newDistance > distance && newDistance > lineDistance)
            distance *= -1
        return distance
    }

    /**  [max,min]*/
    getLimitPosArr(...posArr: DC.Pos[]): DC.Pos[] {
        let maxX: number = posArr[0].x, maxY: number = posArr[0].y, minX: number = posArr[0].x, minY: number = posArr[0].y
        let pos: DC.Pos = null
        for (pos of posArr) {
            if (pos.x > maxX)
                maxX = pos.x
            if (pos.x < minX)
                minX = pos.x
            if (pos.y > maxY)
                maxY = pos.y
            if (pos.y < minY)
                minY = pos.y
        }
        return [{ x: maxX, y: maxY }, { x: minX, y: minY }]
    }

    /**  */
    getRotatePos(centerPos: DC.Pos, mainPos: DC.Pos, radian: number): DC.Pos {
        /** x1=x cosB-y sinB ; y1=x sinB+ y cosB */
        let newPos: DC.Pos = {
            x: ((mainPos.x - centerPos.x) * Math.cos(radian)) - ((mainPos.y - centerPos.y) * Math.sin(radian)) + centerPos.x,
            y: ((mainPos.x - centerPos.x) * Math.sin(radian)) + ((mainPos.y - centerPos.y) * Math.cos(radian)) + centerPos.y
        }
        return newPos
    }

    /** , */
    getIsoscelesRightAngle(posStart: DC.Pos, posEnd: DC.Pos): DC.Pos {
        let radian = this.getRadian(posStart, posEnd)
        let centerPos: DC.Pos = this.getCenterPos(posStart, posEnd)
        let distance = this.getDistance(posStart, posEnd)
        let newPos = this.getRayPos(centerPos, radian + (Math.PI / 2), distance)
        return newPos
    }

    /**  */
    getRayPos(pos: DC.Pos, radian: number, distance: number): DC.Pos {
        let newPos: { x, y } = { x: pos.x + (distance * Math.cos(radian)), y: pos.y + (distance * Math.sin(radian)) }
        return newPos
    }

    /**  truedelta, falsefalse*/
    isIntersect(lineAPosStart: DC.Pos, lineAPosEnd: DC.Pos, lineBPosStart: DC.Pos, lineBPosEnd: DC.Pos) {
        let delta: number = (lineAPosEnd.x - lineAPosStart.x) * (lineBPosStart.y - lineBPosEnd.y) - (lineBPosStart.x - lineBPosEnd.x) * (lineAPosEnd.y - lineAPosStart.y);
        return delta
    }

    /**  */
    isSamePos(posA: DC.Pos, posB: DC.Pos, errorValue: number = 0.0001): boolean {

        if (posA == null || posB == null)
            return false
        else if ((posA.x + errorValue > posB.x && posA.x - errorValue < posB.x) && (posA.y + errorValue > posB.y && posA.y - errorValue < posB.y))
            return true
        else
            return false
    }

    /**
     * 
     * @param lineAStart 
     * @param lineAEnd 
     * @param lineBStart 
     * @param lineBEnd 
     * @param minRadian ,,0
     * @param minDistance 
     *  @returns num(0,1,-1)
     */
    isHorizontalLine(lineA: { posArr: DC.Pos[], radian?: number }, lineB: { posArr: DC.Pos[], radian?: number }, minRadian: number, minDistance: number) {
        if (lineA.radian == undefined)
            lineA.radian = this.getRadian(lineA.posArr[0], lineA.posArr[1])
        if (lineB.radian == undefined)
            lineB.radian = this.getRadian(lineB.posArr[0], lineB.posArr[1])
        let obj: { isH: number, distance: number } = { isH: 0, distance: undefined }
        /**  */
        let check = this.isSameRadian(lineA.radian, lineB.radian, minRadian)
        if (check)
            obj.isH = 1
        /**  */
        else {
            check = this.isSameRadian(lineA.radian, lineB.radian + Math.PI, minRadian)
            if (check)
                obj.isH = -1
        }
        /** , */
        if (obj.isH == 0)
            return obj
        let otherDistanceA = this.getRightAngleDistance(lineB.posArr[0], lineA.posArr[0], lineA.posArr[1])
        let otherDistanceB = this.getRightAngleDistance(lineB.posArr[1], lineA.posArr[0], lineA.posArr[1])
        if (otherDistanceA < minDistance && otherDistanceB < minDistance) {
            if (otherDistanceA < otherDistanceB)
                obj.distance = otherDistanceA
            else
                obj.distance = otherDistanceB
        }
        return obj
    }

    /** truenull*/
    getIntersect(lineAPosStart: DC.Pos, lineAPosEnd: DC.Pos, lineBPosStart: DC.Pos, lineBPosEnd: DC.Pos, isStraight: boolean = true) {
        let p: DC.Pos = null;
        let r: number, u: number;
        let delta = this.isIntersect(lineAPosStart, lineAPosEnd, lineBPosStart, lineBPosEnd)
        /**  */
        if (delta != 0) {
            r = ((lineBPosStart.x - lineAPosStart.x) * (lineBPosStart.y - lineBPosEnd.y) - (lineBPosStart.x - lineBPosEnd.x) * (lineBPosStart.y - lineAPosStart.y)) / delta;
            u = ((lineAPosEnd.x - lineAPosStart.x) * (lineBPosStart.y - lineAPosStart.y) - (lineBPosStart.x - lineAPosStart.x) * (lineAPosEnd.y - lineAPosStart.y)) / delta;
            /**  */
            if (!isStraight) {
                /**  */
                if ((r >= 0 && r <= 1) && (u >= 0 && u <= 1)) {
                    p = { x: lineAPosStart.x + r * (lineAPosEnd.x - lineAPosStart.x), y: lineAPosStart.y + r * (lineAPosEnd.y - lineAPosStart.y) }
                }
            }
            /**  */
            else {
                p = { x: lineAPosStart.x + r * (lineAPosEnd.x - lineAPosStart.x), y: lineAPosStart.y + r * (lineAPosEnd.y - lineAPosStart.y) }
            }
        }
        return p
    }

    /**  distance2,distance1  */
    getIntersectPosByMove(lineAPosStart: DC.Pos, lineAPosEnd: DC.Pos, lineBPosStart: DC.Pos, lineBPosEnd: DC.Pos, lineADistance: number, lineBDistance: number = lineADistance) {
        /**  if (distance2 == undefined)
             distance2 = distance1 */
        if (this.isSamePos(lineAPosStart, lineAPosEnd))
            return null
        if (this.isSamePos(lineBPosStart, lineBPosEnd))
            return null
        let line1: DC.Pos[], line2: DC.Pos[]
        line1 = this.getPosTrans(lineAPosStart, lineAPosEnd, lineADistance)
        line2 = this.getPosTrans(lineBPosStart, lineBPosEnd, lineBDistance)
        return this.getIntersect(line1[0], line1[1], line2[0], line2[1], true)
    }


    /**  { pos1:posType, pos2:posType }*/
    getPosTrans(posStart: DC.Pos, posEnd: DC.Pos, distance: number): DC.Pos[] {
        if (posStart.x == posEnd.x && posStart.y == posEnd.y)
            return null
        let distanceX = distance
        let distanceY = distance
        /**  */
        if (posEnd.y < posStart.y && posEnd.x < posStart.x) {
            distanceX = -distance
            distanceY = distance
        }

        else if (posEnd.y <= posStart.y && posEnd.x >= posStart.x) {
            distanceX = -distance
            distanceY = -distance
        }
        else if (posEnd.y > posStart.y && posEnd.x > posStart.x) {
            distanceY = -distance
            distanceX = distance
        }
        let radian = this.getRadian(posStart, posEnd)
        /**  */
        let a1 = posStart.x + (distanceX * Math.abs(Math.sin(radian)));
        let b1 = posStart.y + (distanceY * Math.abs(Math.cos(radian)));
        let a2 = posEnd.x + (distanceX * Math.abs(Math.sin(radian)));
        let b2 = posEnd.y + (distanceY * Math.abs(Math.cos(radian)));
        // let lineArray:Pos[] = { pos1: { x: a1, y: b1 }, pos2: { x: a2, y: b2 } }
        let lineArray: DC.Pos[] = [{ x: a1, y: b1 }, { x: a2, y: b2 }]
        return lineArray;
    }

    /**  */
    getRelativePos(pos: DC.Pos, rect: { top: number, left: number, width: number, height: number, scale: number }) {
        return { x: ((pos.x - rect.width / 2) / rect.scale) + rect.width / 2 - rect.left / rect.scale, y: ((pos.y - rect.height / 2) / rect.scale) + rect.height / 2 - rect.top / rect.scale };
    }

    /**  */
    getRightAngleIntersect(pos: DC.Pos, linePosStart: DC.Pos, linePosEnd: DC.Pos, isStraight: boolean = true) {
        if (this.isSamePos(linePosStart, linePosEnd))
            return linePosStart
        /**  */
        let radian = this.getRadian(linePosStart, linePosEnd)
        /**  */
        let newPos = this.getRayPos(pos, radian + (Math.PI / 2), 10)
        let newLine = this.getlongLine([newPos, pos])
        /**  */
        let intersectPos = this.getIntersect(newLine[0], newLine[1], linePosStart, linePosEnd, isStraight);
        return intersectPos;
    }

    /**   */
    getRightAngleDistance(pos: DC.Pos, linePosStart: DC.Pos, linePosEnd: DC.Pos) {
        let newPos = this.getRightAngleIntersect(pos, linePosStart, linePosEnd)
        let distance = this.getDistance(pos, newPos)
        return distance
    }

    /**  */
    getRectPosByCenter(centerPos: DC.Pos, size: DC.Size, radian: number = 0): DC.Pos[] {
        /** x1=x cosB-y sinB ; y1=x sinB+ y cosB */

        let posLU: DC.Pos = this.getVertexPosByCenter(centerPos, size, 'LU', radian)
        let posLD: DC.Pos = this.getVertexPosByCenter(centerPos, size, 'LD', radian)
        let posRU: DC.Pos = this.getVertexPosByCenter(centerPos, size, 'RU', radian)
        let posRD: DC.Pos = this.getVertexPosByCenter(centerPos, size, 'RD', radian)

        let posArr: DC.Pos[] = [posLU, posRU, posRD, posLD]
        return posArr
    }

    /**  */
    getRectPosByTwoPos(firstPos: DC.Pos, lastPos: DC.Pos, sort: boolean = false): DC.Pos[] {
        let posArr: DC.Pos[] = []
        if (!sort) {
            posArr = [{ x: firstPos.x, y: firstPos.y }, { x: lastPos.x, y: lastPos.y }]
        } else {
            let maxX: number = firstPos.x > lastPos.x ? firstPos.x : lastPos.x
            let minX: number = firstPos.x > lastPos.x ? lastPos.x : firstPos.x
            let maxY: number = firstPos.y > lastPos.y ? firstPos.y : lastPos.y
            let minY: number = firstPos.y > lastPos.y ? lastPos.y : firstPos.y
            posArr = [{ x: minX, y: minY }, { x: maxX, y: maxY }]
        }

        let posA: DC.Pos = { x: posArr[1].x, y: posArr[0].y }
        let posB: DC.Pos = { x: posArr[0].x, y: posArr[1].y }
        let newPosArr: DC.Pos[] = [posArr[0], posA, posArr[1], posB]

        return newPosArr
    }

    /**  
     * :LU,LD,RU,RD
     * */
    getVertexPosByCenter(centerPos: DC.Pos, size: DC.Size, type: string, radian: number = 0): DC.Pos {
        switch (type) {
            case 'LU':
                let posLU: DC.Pos = { x: centerPos.x - (size.width / 2), y: centerPos.y - (size.height / 2) }
                let newPosLU: DC.Pos = this.getRotatePos(centerPos, posLU, radian);
                return newPosLU
            case 'LD':
                let posLD: DC.Pos = { x: centerPos.x - (size.width / 2), y: centerPos.y + (size.height / 2) }
                let newPosLD: DC.Pos = this.getRotatePos(centerPos, posLD, radian);
                return newPosLD
            case 'RU':
                let posRU: DC.Pos = { x: centerPos.x + (size.width / 2), y: centerPos.y - (size.height / 2) }
                let newPosRU: DC.Pos = this.getRotatePos(centerPos, posRU, radian);
                return newPosRU
            case 'RD':
                let posRD: DC.Pos = { x: centerPos.x + (size.width / 2), y: centerPos.y + (size.height / 2) }
                let newPosRD: DC.Pos = this.getRotatePos(centerPos, posRD, radian);
                return newPosRD
            default:
                console.log('')
                return null
        }
    }

    /*  */
    getLines(posArr: DC.Pos[]) {
        if (!posArr || posArr.length < 2)
            return []
        let lines: DC.Pos[][] = [[posArr[posArr.length - 1], posArr[0]]]
        for (let i = 0; i < posArr.length - 1; i++) {
            lines.push([posArr[i], posArr[i + 1]])
        }
        return lines
    }

    /**  mistake */
    isInPosArrByPos(pos: DC.Pos, posArr: DC.Pos[], mistake: number = 0.1) {
        let area: number = this.getArea(...posArr)
        let newArea: number = 0
        for (let i = 0; i < posArr.length - 1; i++) {
            newArea += Math.abs(this.getTriangleArea(pos, posArr[i], posArr[i + 1]))
        }
        newArea += Math.abs(this.getTriangleArea(pos, posArr[posArr.length - 1], posArr[0]))
        let a = newArea - area
        if ((a >= 0 && a < mistake) || (a <= 0 && a > -mistake))
            return true
        else
            return false
    }

    getTriangleAreaByDistance(a: number, b: number, c: number) {
        let p = (a + b + c) / 2
        let area = Math.sqrt(p * (p - a) * (p - b) * (p - c))
        return area
    }

    /** () */
    getCenterPosByPolygon(...posArr: DC.Pos[]): DC.Pos {
        /**  */
        let rectFunc = (): DC.Pos => {
            let maxPos: DC.Pos = { x: posArr[0].x, y: posArr[0].y }
            let minPos: DC.Pos = { x: posArr[0].x, y: posArr[0].y }
            for (let i = 1; i < posArr.length; i++) {
                if (maxPos.x < posArr[i].x)
                    maxPos.x = posArr[i].x
                if (maxPos.y < posArr[i].y)
                    maxPos.y = posArr[i].y
                if (minPos.x > posArr[i].x)
                    minPos.x = posArr[i].x
                if (minPos.y > posArr[i].y)
                    minPos.y = posArr[i].y
            }
            let centerPos: DC.Pos = this.getCenterPos(maxPos, minPos)
            return centerPos
        }
        let centerPos: DC.Pos = rectFunc()
        return centerPos
    }

    /**  */
    getTriangleArea(posA: DC.Pos, posB: DC.Pos, posC: DC.Pos) {
        let area = ((posA.x * (posB.y - posC.y)) + (posB.x * (posC.y - posA.y)) + (posC.x * (posA.y - posB.y))) / 2
        return area
    }

    /**  */
    getTriangleCenterPos(posA: DC.Pos, posB: DC.Pos, posC: DC.Pos) {
        let x = (posA.x + posB.x + posC.x) / 3
        let y = (posA.y + posB.y + posC.y) / 3
        return { x: x, y: y }
    }

    /**  */
    getArea(...pos: DC.Pos[]) {
        let area: number = 0
        /**  */
        if (pos.length < 3) {
            return 0
        }
        else {
            for (let i = 1; i < pos.length - 1; i++) {
                let newArea = this.getTriangleArea(pos[0], pos[i], pos[i + 1])
                area += newArea
            }
            return area
        }
    }

    /**  */
    removeSamePosLine(lineArr: DC.Pos[][]) {
        for (let i = lineArr.length - 1; i >= 0; i--) {
            if (lineArr[i][0].x == lineArr[i][1].x && lineArr[i][0].y == lineArr[i][1].y) {
                lineArr.splice(i, 1)
            }

        }
    }

    /**  */
    isParallelRadian(radianA: number, radianB: number, errorValue: number = 0.0001) {
        let value = radianB - radianA
        value = value % Math.PI
        value = Math.abs(value)
        if (value <= errorValue || value >= Math.PI - errorValue) {
            return true
        }
        return false
    }

    /**  */
    getLineByCenterPos(centerPos: DC.Pos, radius: number, radian: number) {
        let newPosA = this.getRayPos(centerPos, radian, radius)
        let newPosB = this.getRayPos(centerPos, radian + Math.PI, radius)
        return [newPosA, newPosB]
    }

    /**   */
    getPosRaySort(posStart: DC.Pos, ...posArr: DC.Pos[]) {
        let distanceArr: {
            id: number,
            distance: number
        }[] = []
        let arrNum: number[] = []
        let pos: DC.Pos = null
        let distance: number = null
        /**  */
        for (let i = 0; i < posArr.length; i++) {
            distance = this.getPowDistance(posStart, posArr[i])
            distanceArr.push({ distance: distance, id: i })
        }

        /**  */
        let distanceMin: number = null
        let distanceMinNum: number = null
        let loop: number = distanceArr.length
        for (let i = 0; i < loop; i++) {
            distanceMin = distanceArr[0].distance
            distanceMinNum = distanceArr[0].id
            for (let j = 0; j < distanceArr.length; j++) {
                if (distanceArr[j].distance < distanceMin) {
                    distanceMin = distanceArr[j].distance
                    distanceMinNum = distanceArr[j].id
                }
            }
            arrNum.push(distanceMinNum)
            distanceArr.splice(distanceMinNum, 1)
        }
        //console.log(arrNum)
        return arrNum
    }

    /**  */
    getTransformStringByRM(obj: { movePos?: DC.Pos, radian?: number }): string {
        obj.movePos = obj.movePos ? obj.movePos : { x: 0, y: 0 }
        let sinA = 0
        let cosA = 1
        if (obj.radian != undefined) {
            sinA = Math.sin(obj.radian)
            cosA = Math.cos(obj.radian)
        }
        let string: string = this.getTransfromString(cosA, sinA, -sinA, cosA, obj.movePos.x, obj.movePos.y)
        return string

    }

    /**  */
    getTransfromString(cosA, sinA, unsinB, cosB, x, y): string {
        let string: string = "matrix(" + cosA + "," + sinA + "," + unsinB + "," + cosB + "," + x + "," + y + ")"
        return string
    }

    /**  */
    getPointToLinePos(pos: DC.Pos, lineStart: DC.Pos, lineEnd: DC.Pos): DC.Pos {
        if (this.isSamePos(lineStart, lineEnd))
            return lineStart
        let cross = ((lineEnd.x - lineStart.x) * (pos.x - lineStart.x)) + ((lineEnd.y - lineStart.y) * (pos.y - lineStart.y))
        let d = ((lineEnd.x - lineStart.x) * (lineEnd.x - lineStart.x)) + ((lineEnd.y - lineStart.y) * (lineEnd.y - lineStart.y))
        if (cross < 0) {
            return lineStart
        }
        else if (cross > d) {
            return lineEnd
        }
        else {
            let newPos: DC.Pos = this.getRightAngleIntersect(pos, lineStart, lineEnd)
            return newPos
        }
    }

    /**  */
    getPosArrSort(posStart: DC.Pos, ...posArr: DC.Pos[]): DC.Pos[] {
        let newArr: DC.Pos[] = [posArr[0]]
        let distanceArr: number[] = [this.getDistance(posStart, posArr[0])]
        let distance: number = null
        let same: number = 0;
        for (let i = 1; i < posArr.length; i++) {
            distance = this.getDistance(posStart, posArr[i])
            for (let j = 0; j < distanceArr.length; j++) {
                if (distance > distanceArr[j]) {
                    same = j;
                }
            }
            newArr.splice(same, 0, posArr[i])
            distanceArr.splice(same, 0, distance)
            same = 0
        }
        return newArr
    }

    /**  */
    vector2D(vector: DC.Pos) {
        let vec = {
            x: vector.x,
            y: vector.y,
            /**  */
            add: (newVector: DC.Pos) => {
                vec.x += newVector.x;
                vec.y += newVector.y;
                return vec
            },
            /**  */
            sub: (newVector: DC.Pos) => {
                vec.x -= newVector.x;
                vec.y -= newVector.y;
                return vec
            },
            /**  */
            oppsite: (newVector: DC.Pos) => {
                vec.x = -vec.x
                vec.y = -vec.y
                return vec
            },
            /**  */
            Multiply: (newVector: DC.Pos) => {
                return (vec.x * newVector.x) + (vec.y * newVector.y)
            },
            //
            rotate: (radian: number) => {
                var vx = vec.x,
                    vy = vec.y,
                    cosVal = Math.cos(radian),
                    sinVal = Math.sin(radian);
                vec.x = vx * cosVal - vy * sinVal;
                vec.y = vx * sinVal + vy * cosVal;
                return vec
            },
            // 
            scale: (scale) => {
                vec.x *= scale;
                vec.y *= scale;
                return vec
            },
            /**  */
            length: () => {
                return Math.sqrt(Math.pow(vec.x, 2) + Math.pow(vec.y, 2))
            },
            /**  */
            radian: () => {
                if (vec.x == 0 && vec.y == 0)
                    return null
                return Math.atan2(vec.y, vec.x)
            },
            /**  vec-newVector */
            differRadian: (newVector: DC.Pos) => {
                let radian = vec.radian()
                let newRadian = this.vector2D(newVector).radian()
                return radian - newRadian
            },
            /** vecnewVector */
            projection: (newVector: DC.Pos) => {
                let differRadian = vec.differRadian(newVector)
                vec.x = vec.x * Math.cos(differRadian)
                vec.y = vec.y * Math.cos(differRadian)
                return vec
            }
            /**  */

        }
        return vec
    }

    /** 
     * @param errorValue 
     */
    isInLine(lineStart: DC.Pos, lineEnd: DC.Pos, pos: DC.Pos, errorValue: number = 0.001) {
        let newPos = this.getPointToLinePos(pos, lineStart, lineEnd)
        let newDistance = this.getDistance(newPos, pos)
        if (newDistance < errorValue)
            return true
        else
            return false
    }

    /**
     * () 
     * @param radianA 
     * @param radianB 
     * @param errorValue 
     */
    isSameRadian(radianA: number, radianB: number, errorValue: number = 0.0001) {
        let a = this.getAbsRadian(radianA, true)
        let b = this.getAbsRadian(radianB, true)
        if (a > b - errorValue && a < b + errorValue) {
            return true
        }


        return false
    }

    /**  */
    isLineOut(lineStart: DC.Pos, lineEnd: DC.Pos, pos: DC.Pos) {
        let out: boolean = false
        /**  */
        let vector: DC.Pos = {
            x: lineEnd.x - lineStart.x,
            y: lineEnd.y - lineStart.y,
        }
        /**  */
        let newVector: DC.Pos = {
            x: pos.x - lineStart.x,
            y: pos.y - lineStart.y,
        }
        /**  */
        let a = this.vector2D(vector).Multiply(newVector)
        if (a <= 0) {
            out = true
        } else {
            let b = this.vector2D(newVector).sub(vector).Multiply(vector)
            if (b >= 0) {
                out = true
            }
        }
        return out
    }

    /**
     * ()
     * @param posStart 
     * @param posEnd 
     * @param lineArr 
     * @returns  
     */
    getNearLine(posStart: DC.Pos, posEnd: DC.Pos, lineArr: DC.Pos[]) {
        let mainRadian = this.getRadian(posStart, posEnd)
        mainRadian = this.getAbsRadian(mainRadian, true)
        for (let i = 0; i < lineArr.length; i++) {
            let lineRadian = this.getRadian(lineArr[i][0], lineArr[i][1])
        }
    }

    /**
     * 
     * @param lineStart 
     * @param lineEnd  
     * @param radius  
     * @param circlePos 
     * @returns [,]
     */
    getArcLineIntersect(lineStart: DC.Pos, lineEnd: DC.Pos, radius: number, circlePos: DC.Pos) {
        let radian = this.getRadian(lineStart, lineEnd)
        let rayPos = this.getRayPos(circlePos, radian + (Math.PI / 2), 10)
        let intersectPos = this.getIntersect(lineStart, lineEnd, rayPos, circlePos)
        let circleIntersectPowDistance = this.getPowDistance(circlePos, intersectPos)
        let circleRPowDistnace = Math.pow(radius, 2)
        let DifferencePowDistance = circleRPowDistnace - circleIntersectPowDistance
        // if (DifferencePowDistance < 0) {
        //     return null
        // }
        let distance = Math.sqrt(DifferencePowDistance)
        let posA = this.getRayPos(intersectPos, radian, distance)
        let posB = this.getRayPos(intersectPos, radian, -distance)
        return [posA, posB]
    }

    /**
     * 
     * @param mainRadius 
     * @param mainCirclePos 
     * @param otherRadius 
     * @param otherCirclePos 
     * @returns [,]
     */
    getArcArcIntersect(mainRadius: number, mainCirclePos: DC.Pos, otherRadius: number, otherCirclePos: DC.Pos) {
        let radian = this.getRadian(mainCirclePos, otherCirclePos)
        let distance = this.getDistance(mainCirclePos, otherCirclePos)
        let newRadian = this.getTriangleRadianByDistance(mainRadius, distance, otherRadius)
        let newPosA = this.getRayPos(mainCirclePos, radian + newRadian, mainRadius)
        let newPosB = this.getRayPos(mainCirclePos, radian - newRadian, mainRadius)
        return [newPosA, newPosB]
    }

    /**  */
    getSectorArea(lineDistance: number, r: number) {
        let arcDistance = 2 * Math.asin(lineDistance / (2 * r)) * r
        let area = arcDistance * r / 2
        return area
    }

    /**  */
    getArcArea(lineDistance: number, r: number, isBig: boolean = false) {
        let area = this.getSectorArea(lineDistance, r)
        let triangleArea = this.getTriangleAreaByDistance(lineDistance, r, r)
        area -= triangleArea
        if (isBig) {
            area = Math.PI * r * r - area
        }
        return area
    }

    /** (limitRadian)  */
    getLimitRadian(mainRadian: number, limitRadian: number) {
        let newRadian = this.getAbsRadian(mainRadian)
        let otherLimitRadian = limitRadian - Math.PI
        if (
            (newRadian >= limitRadian && newRadian < Math.PI) ||
            newRadian >= -Math.PI && newRadian <= otherLimitRadian
        ) {
            newRadian += Math.PI
        }
        return newRadian
    }

    /*  */
    getTwoPosRadianDistance(presentPos: DC.Pos, prevPos: DC.Pos, radian: number, isAbs: boolean = true) {
        let line = [{ x: 0, y: 0 }]
        line = [line[0], this.getRayPos(line[0], radian, 10)]
        let newPresentPos = this.getRightAngleIntersect(presentPos, line[0], line[1])
        let newPrevPos = this.getRightAngleIntersect(prevPos, line[0], line[1])
        let distance = this.getDistance(newPresentPos, newPrevPos)
        if (isAbs)
            return distance
        let a = line[1].x
        let b = newPresentPos.x - newPrevPos.x
        if (Math.abs(a) < 0.01) {
            a = line[1].y
            b = newPresentPos.y - newPrevPos.y
        }
        if ((a > 0 && b > 0) || (a < 0 && b < 0)) {
            return distance
        }
        return -distance
    }

    /**
     * ()
     * @param line 
     * @param distance ,999999999
     */
    getlongLine(line: DC.Pos[], distance: number = 99999999999) {
        let radian = this.getRadian(line[0], line[1])
        let newPosA = this.getRayPos(line[1], radian, distance)
        let newPosB = this.getRayPos(line[0], radian, -distance)
        return [newPosB, newPosA]
    }

    /**
     * 
     * @param mainLine 
     * @param transLine 
     */
    getSameTrendLine(mainLine: DC.Pos[], transLine: DC.Pos[]) {
        let mainRadian = this.getRadian(mainLine[0], mainLine[1])
        let transRadian = this.getRadian(transLine[0], transLine[1])
        let differRadian = mainRadian - transRadian
        differRadian = this.getAbsRadian(differRadian)
        if (differRadian > Math.PI / 2 || differRadian < -Math.PI / 2) {
            return [transLine[1], transLine[0]]
        }
        return [transLine[0], transLine[1]]
    }

    /** ,, */
    getNearPosArr(mainPos: DC.Pos, vectorPos: DC.Pos, posArr: DC.Pos[]) {

        /**  */
        let startMinPos: DC.Pos = null
        let startInt: number = null
        let startMinDistance: number = null
        /**  */
        let endMinPos: DC.Pos = null
        let endInt: number = null
        let endMinDistance: number = null
        /** , */
        let mainVector: DC.Pos = {
            x: vectorPos.x - mainPos.x,
            y: vectorPos.y - mainPos.y
        }
        let Vector: DC.Pos = null
        let distance: number = null
        for (let i = 0; i < posArr.length; i++) {
            Vector = {
                x: posArr[i].x - mainPos.x,
                y: posArr[i].y - mainPos.y
            }
            let a = this.vector2D(mainVector).Multiply(Vector)
            distance = this.getPowDistance(posArr[i], mainPos)
            /**  */
            if (a < 0) {
                if (startMinDistance == null || distance < startMinDistance) {
                    startMinPos = posArr[i]
                    startMinDistance = distance
                    startInt = i
                }
            }
            /**  */
            else {
                if (endMinDistance == null || distance < endMinDistance) {
                    endMinPos = posArr[i]
                    endMinDistance = distance
                    endInt = i
                }
            }
        }
        return {
            StartPos: startMinPos,
            endPos: endMinPos,
            startInt: startInt,
            endInt: endInt
        }
    }

}
