import { Component, OnInit,Input} from '@angular/core';

@Component({
  selector: '[putModel]',
  templateUrl: './put-model.component.html',
  styleUrls: ['./put-model.component.css']
})
export class PutModelComponent implements OnInit {
  private _model;
  imgInfo:any = null;
  @Input() set putModel(data){
    // console.log(data);
    this._model = data;
  }
  get putModel(){return this._model}
  constructor() { }

  ngOnInit() {
  }
  showInfo(e,img){
    // console.log(img)
    this.imgInfo = img
  }
  hiddeInfo(){
    this.imgInfo = null
  }

}
