import { Component, OnInit,Input } from '@angular/core';
import { PopDivService } from '../../../../services/pop-div.service';

@Component({
  selector: '[imgList]',
  templateUrl: './img-list.component.html',
  styleUrls: ['./img-list.component.css']
})
export class ImgListComponent implements OnInit {
  private _imgData
  imgInfo:any = null
  @Input() set imgList(data){
    this._imgData = data
  }
  get imgList(){return this._imgData}
  constructor(
    private popService:PopDivService
  ) { }

  ngOnInit() {
  }

  close(e){
    this.popService.getMaterial.emit(null)
  }
  showInfo(e,img){
    // console.log(img)
    this.imgInfo = img
  }
  hiddeInfo(){
    this.imgInfo = null
  }
}
