import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy, Output, EventEmitter } from '@angular/core';

@Component({
  selector: '[testSvg]',
  templateUrl: './test-svg.component.html',
  styleUrls: ['./test-svg.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush

})
export class TestSvgComponent implements OnInit {

  testSvgData = {
    data: null,
    ref: this.ref
  }

  @Output() TSEvent: EventEmitter<object> = new EventEmitter<object>()

  constructor(
    private ref: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.TSEvent.emit(this.testSvgData)
  }

}
