import { JdesignDataService } from "../../services/jdesign-data.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { JRoom3D } from "../../three/jDesign3D/JRoom3D";
import { JCowall } from "../../svg/common/jdesign/JCowall";
import { JRoom } from "../../svg/common/jdesign/JRoom";

export class RoomShortFunc {
    constructor(
        protected dataService: JdesignDataService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService
    ) {
        this.dataService.cache.shortFuncs["testFloor"] = (obj) => {
            console.log('')
        }
        /*  */
        this.dataService.cache.shortFuncs["balconyRoom"] = (obj) => {
            this.changeBalcony(obj)
        }

        this.dataService.cache.shortFuncs["changeNormalRoom"] = (obj) => {
            this.changeNormal(obj)
        }
        /*  */
        this.dataService.cache.shortFuncs['setRoomType'] = (room: JRoom, type: string) => {
            room.roomName = type
            this.domService.allSvg.emit()
        }
        /*  */
        // this.dataService.cache.shortFuncs
    }

    /*  */
    changeBalcony(room: JRoom3D) {
        let wallArr = room.getWallArr()
        let cowallArr: JCowall[] = null
        for (let i = 0; i < wallArr.length; i++) {
            cowallArr = wallArr[i].getCowallArr()
            if (cowallArr.length == 1) {
                wallArr[i].mode = 'balcony'
            }
        }
        this.domService.svgRoom.emit()
        this.domService.svgWall.emit()
    }

    /*  */
    changeNormal(room: JRoom3D) {
        let wallArr = room.getWallArr()
        for (let i = 0; i < wallArr.length; i++) {
            wallArr[i].mode = 'normal'
        }
        this.domService.svgRoom.emit()
        this.domService.svgWall.emit()
    }

}