import { JLUWin3D } from "../../../three/jDesign3D/JLUWin3D";
import { JPoint } from "./JPoint";
import { JWall } from "./JWall";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JLUWinTypeInit } from "./JLUWin";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";

export class JLWin extends JLUWin3D {


    constructor(
        obj: JLWinTypeInit,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService,
    ) {
        super("LWin", obj, dataService, DUService)
        this.pointID = obj.pointID ? obj.pointID : undefined
        this.dataService.LWins[this.id] = this
    }

    private _pointID: string
    /** ID */
    get pointID() { return this._pointID }
    set pointID(data) { this._pointID = data }


    getPoint(): JPoint {
        if (!this.pointID)
            return undefined
        return this.dataService.points[this.pointID]
    }

    get leftStartPoint() {
        return this.getPoint()
    }

    get rightStartPoint() {
        return this.getPoint()
    }

    get leftStartPos() {
        return this.pos
    }

    get rightStartPos() {
        return this.pos
    }

    isAbsorb() {
        let point = this.getPoint()
        return point ? true : false
    }

}

export class JLWinTypeInit extends JLUWinTypeInit {
    pointID?: string;
}