import { JCowall } from "../../../common/jdesign/JCowall";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { JWall } from "../../../common/jdesign/JWall";
import { DesignCommon as DC } from '../../../common/class/DesignCommon'

export class TransOutLines {

    constructor(
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService
    ) {

    }

    /**  */
    wallTransCenterLineMsg(wall: JWall): LinePoint {
        let radian = wall.getRadian()
        if (radian == undefined)
            return undefined
        let isVertical: boolean = false
        if (this.DUService.pos.isParallelRadian(radian, 0)) {
            isVertical = false
        }
        else if (this.DUService.pos.isParallelRadian(radian, Math.PI / 2)) {
            isVertical = true
        }
        else {
            return undefined
        }
        let line = wall.getPosArr()
        let distance = wall.getDistance()
        return {
            pos: { x: (line[0].x + line[1].x) / 2, y: (line[0].y + line[1].y) / 2 },
            level: 0,
            length: distance,
            isVertical: isVertical,
            radian: radian,
            line: this.DUService.obj.clone(line),
            wallID: wall.id,
            originLine: this.DUService.obj.clone(line)
        }
    }

    /**  */
    cowallTransInLineMsg(cowall: JCowall): LinePoint {
        let radian = cowall.getRadian()
        if (radian == undefined)
            return undefined
        let isVertical: boolean = false
        if (this.DUService.pos.isParallelRadian(radian, 0)) {
            isVertical = false
        }
        else if (this.DUService.pos.isParallelRadian(radian, Math.PI / 2)) {
            isVertical = true
        }
        else {
            return undefined
        }
        let line = cowall.getInLine()
        let distance = this.DUService.pos.getDistance(line[0], line[1])
        let data: LinePoint = {
            pos: { x: (line[0].x + line[1].x) / 2, y: (line[0].y + line[1].y) / 2 },
            level: 0,
            length: distance,
            isVertical: isVertical,
            radian: radian,
            line: this.DUService.obj.clone(line),
            cowallID: cowall.id,
            originLine: this.DUService.obj.clone(line)
        }
        return data
    }

    /**  */
    wallTransThicknessMsg(wall: JWall) {
        let radian = wall.getRadian()
        if (radian == undefined)
            return undefined
        let isVertical: boolean = false
        if (this.DUService.pos.isParallelRadian(radian, 0)) {
            isVertical = false
        }
        else if (this.DUService.pos.isParallelRadian(radian, Math.PI / 2)) {
            isVertical = true
        }
        else {
            return undefined
        }
        let wallPosArr = wall.getPosArr()
        let pointPos = { x: (wallPosArr[0].x + wallPosArr[1].x) / 2, y: (wallPosArr[0].y + wallPosArr[1].y) / 2 }
        radian = radian - Math.PI
        let distance = wall.thickness
        let line = [this.DUService.pos.getRayPos(pointPos, radian, distance), this.DUService.pos.getRayPos(pointPos, radian, -distance)]
        let data = {
            pos: pointPos,
            level: 0,
            length: distance,
            isVertical: !isVertical,
            radian: radian,
            line: this.DUService.obj.clone(line)
        }
        return data
    }

    /**  */
    wallTransLineMsg(wall: JWall, isOut: boolean) {
        let cowalls = wall.getCowallArr()
        let radian = wall.getRadian()
        let isVertical: boolean = false
        if (this.DUService.pos.isParallelRadian(radian, 0)) {
            isVertical = false
        }
        else if (this.DUService.pos.isParallelRadian(radian, Math.PI / 2)) {
            isVertical = true
        }
        else {
            return undefined
        }
        let line = isOut ? wall.getOutLine() : wall.getInLine()
        let distance = this.DUService.pos.getDistance(line[0], line[1])
        let data: LinePoint = {
            pos: { x: (line[0].x + line[1].x) / 2, y: (line[0].y + line[1].y) / 2 },
            level: 0,
            length: distance,
            isVertical: isVertical,
            radian: radian,
            line: this.DUService.obj.clone(line),
            wallID: wall.id,
            originLine: this.DUService.obj.clone(line)
        }
        return data
    }
}

export class LinePoint {
    pos: DC.Pos;
    /**  */
    length: number;
    isVertical: boolean;
    line: DC.Pos[];
    radian: number;
    wallID?: string;
    cowallID?: string;
    level?: number;
    isOut?: boolean;
    originLine?: DC.Pos[]
}