import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { JRoom } from '../../common/jdesign/JRoom';
import { DomUpdateService } from '../../../services/dom-update.service';

@Component({
  selector: '[rooms]',
  templateUrl: './rooms.component.html',
  styleUrls: ['./rooms.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RoomsComponent implements OnInit {

  constructor(
    private dataService: JdesignDataService,
    private DUService: JdesignUtil2dService,
    private domService: DomUpdateService,
    private ref: ChangeDetectorRef
  ) {
    this.domService.svgRoom.subscribe(() => {
      this.ref.detectChanges()
    })
  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('rooms is ok!!')
    }
  }

  /* */
  getRoomArr() {
    let rooms: JRoom[] = this.DUService.cal.getStringArrByNum(this.dataService.rooms)
    return rooms
  }

}
