import { JdesignDataService } from "../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../services/jdesign-util2d.service";
import { PopDivService } from "../../services/pop-div.service";
import { LeftPanel } from "./left.panel";
import { LeftBuildPanel } from "./leftBuild.panel";
import { HttpService } from "../../services/http.service";
import { TopDataPanel } from "./topData.panel";
import { RightDataPanel } from "./rightData.panel";
import { WallPanel } from "./right-panel/wall.panel";
import { DomUpdateService } from "../../services/dom-update.service";
import { PlatFondPanel } from "./right-panel/platfond.panel";
import { FloorPanel } from "./right-panel/floor.panel";
import { CornerWindowPanel } from "./right-panel/cornerWindow.panel";
import { DoorPanel } from "./right-panel/door.panel";
import { LwindowPanel } from "./right-panel/Lwindow.panel";
import { WindowPanel } from "./right-panel/window.panel";
import { FrogPanel } from "./right-panel/frog.panel";
import { BottomData } from "./bottom-panel/bottom.panel";
import { DomSanitizer } from "../../../../node_modules/@angular/platform-browser";
import { UwindowPanel } from "./right-panel/Uwindow.panel";
import { CalculatorsService } from "../../services/calculators.service";
import { ParamsModelPanel } from "./right-panel/paramsModel.panel";

export class DesignStyleDataPanel {
    constructor(
        protected dataService: JdesignDataService,
        protected popService: PopDivService,
        protected calService: CalculatorsService,
        private httpService: HttpService,
        private DUService: JdesignUtil2dService,
        private domService: DomUpdateService,
        private sanitizer: DomSanitizer,
    ) {
        let getRightData = new RightDataPanel(httpService);
        let mainLeft = new LeftPanel(popService, httpService, DUService, dataService);
        let buildLeft = new LeftBuildPanel(popService, dataService, calService, DUService, domService);
        let topData = new TopDataPanel(popService, dataService, this.domService, sanitizer);
        let bottomData = new BottomData(popService, dataService, domService)
        let wallData = new WallPanel(popService, dataService, domService);
        let roomData = new PlatFondPanel(popService, dataService);
        let floorData = new FloorPanel(popService, dataService, domService)
        let cornerWindowData = new CornerWindowPanel(popService, dataService, domService);
        let doorData = new DoorPanel(popService, dataService, domService);
        let windowData = new WindowPanel(popService, dataService, domService)
        let LwindowData = new LwindowPanel(popService, dataService, domService);
        let UwindowData = new UwindowPanel(popService, dataService, domService);
        let paramsModelData = new ParamsModelPanel(popService, dataService, domService)
        let frogData = new FrogPanel(popService, dataService)
    }
}