import { JdesignDataService } from "../../services/jdesign-data.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { PopDivService } from "../../services/pop-div.service";
import { JdesignUtil2dService } from "../../services/jdesign-util2d.service";
import { JUWin } from "../../svg/common/jdesign/JUWin";
import { JLUWin } from "../../svg/common/jdesign/JLUWin";
import { JLUWin3D } from "../../three/jDesign3D/JLUWin3D";
import { JLWin } from "../../svg/common/jdesign/JLWin";

export class LUWinShortFunc {
    constructor(
        protected dataService: JdesignDataService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService,
        protected popService: PopDivService,
        protected DUService: JdesignUtil2dService
    ) {
        this.dataService.cache.shortFuncs["createLWin"] = () => {
            this.createNewLWin()
        }
        this.dataService.cache.shortFuncs['createUWin'] = () => {
            this.createNewUWin()
        }
        this.dataService.cache.shortFuncs['delLUWin'] = (obj: JLUWin3D) => {
            this.delObj(obj)
        }
        this.dataService.cache.shortFuncs['isLUWin'] = (obj: JLUWin3D) => {
            return this.isLUWin(obj)
        }
    }

    createNewLWin() {
        let j2d = this.DUService.obj.clone(this.dataService.cache.baseData.j2d['LWin'])
        j2d = this.DUService.parseJ2D.getobjData(j2d)
        let LWin = this.calService.LWin.createNew({
            j2d: j2d,
            radian: 0,
            floorHeight: 1000,
            height3D: 500
        })
        this.dataService.cache.common.choose = LWin
        this.setNormal()
    }

    createNewUWin() {
        let j2d = this.DUService.obj.clone(this.dataService.cache.baseData.j2d['UWin'])
        j2d = this.DUService.parseJ2D.getobjData(j2d)
        let UWin = this.calService.UWin.createNew({
            j2d: j2d,
            centerRadian: 0,
            radian: 0,
            floorHeight: 1000,
            height3D: 500
        })
        this.dataService.cache.common.choose = UWin
        this.setNormal()
    }

    setNormal() {
        this.dataService.cache.common.mode = 'normal'
        this.dataService.cache.common.blockON = true
        this.dataService.cache.common.firstPos = null
        this.dataService.cmd.cowall.hide = true
        this.domUpdate()
    }

    isLUWin(obj: JLUWin3D) {
        switch (obj.type) {
            case "LWin": case "UWin":
                return true
            default:
                return false
        }

    }

    delObj(obj: JLUWin3D) {
        switch (obj.type) {
            case "LWin":
                delete this.dataService.LWins[obj.id]
            case "UWin":
                delete this.dataService.UWins[obj.id]
        }
        this.domUpdate()
    }


    /** dom */
    domUpdate() {
        this.domService.allSvg.emit()
        this.popService.inputBoxs.emit([])
        this.popService.buttons.emit(null)
    }
}