import { JDWAbstract, JDWInitType } from "./JDWAbstract";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { JWall } from "./JWall";
import { JDW3D } from "../../../three/jDesign3D/JDW3D";

export class JWin extends JDW3D {

    /** ID */
    // private _wall: string = null;
    /**  id   */
    set wallID(obj: string) {

        if (this.wallID != null) {
            if (this.oldWallID != this.wallID && this.wallID != obj) {
                let wall: JWall = this.getwall()
                this.oldWallID = wall ? this.wallID : undefined
            }
            let wall: JWall = this.getwall()
            if (wall != undefined) {
                for (let i = 0; i < wall.winIDs.length; i++) {
                    if (wall.winIDs[i] == this.id) {
                        wall.winIDs.splice(i, 1)
                        break;
                    }
                }
            }
        }
        this._wall = obj
        if (obj != undefined) {
            let wall: JWall = this.getwall()
            let same = false
            for (let i = 0; i < wall.winIDs.length; i++) {
                if (wall.winIDs[i] == this.id) {
                    same = true
                    break;
                }
            }
            if (!same) {
                wall.winIDs.push(this.id)
            }
        }
        //this.strongUpdate()
    }

    get wallID() { return this._wall }

    getWallDWIDs() {
        let wall = this.getwall()
        if (wall != undefined)
            return wall.winIDs
        return undefined
    }

    private _outThickness: number
    /** ()  */
    get outThickness() { return this._outThickness }
    set outThickness(data) { this._outThickness = data }

    private _outBoxThickness: number
    /** () */
    get outBoxThickness() { return this._outBoxThickness }
    set outBoxThickness(data) { this._outBoxThickness = data }

    private _isBay: boolean
    /**  */
    get isBay() { return this._isBay }
    set isBay(data) { this._isBay = data }

    constructor(
        obj: JWinInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService
    ) {
        super({ type: 'win', id: obj.id }, dataService, DUService)
        this.dataInit(obj)
        this.outThickness = obj.outThickness ? obj.outThickness : 0
        this.outBoxThickness = obj.outBoxThickness ? obj.outBoxThickness : 0
        this.isBay = obj.isBay ? obj.isBay : false
        this.dataService.wins[this.id] = this
    }


}

export class JWinInitType extends JDWInitType {
    outThickness?: number;
    isBay?: boolean;
    outBoxThickness?: number;
}