import { Injectable, EventEmitter } from '@angular/core';
import { popRightData, panelTopData, PanelTopFuncData } from '../data/pop.data';
import { HttpClient } from '@angular/common/http';



@Injectable()
export class PopDivService {

  private _inputBoxsCache: {
    pos: { x: number, y: number };
    func: (params: any) => void;
    value: number | string;
    focus?: boolean;
    index: number;
  }[] = []

  private _buttonsCache: {
    outArea: any;
    content: any[]
  } = null

  private _transPos: (pos: { x: number, y: number }) => { x: number, y: number }
  get transPos() { return this._transPos }
  set transPos(data) { this._transPos = data }

  constructor(
    private http: HttpClient
  ) {
    this.inputBoxs.subscribe((data) => {
      if (data == null || data.length == 0) {
        this.inputBoxsCache = []
      }
    })

    this.buttons.subscribe((data) => {
      if (data == null)
        this.buttonsCache = null
    })
  }

  /**  */
  rightClick: EventEmitter<object> = new EventEmitter<object>()
  /**  */
  panelTop: EventEmitter<object> = new EventEmitter<object>()
  /**  */
  inputBoxs: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  message: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  backImg: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  buttons: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  thumbnail: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  panelRight: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  panelRightData: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  loadingPage: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  leftBtn: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  leftMenuType: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  leftProduct: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  leftMenuList: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  panelLeftDraw: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  panelLeftSofaData: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  panelLeft: EventEmitter<object> = new EventEmitter<object>()
  /** */
  sendLeftData: EventEmitter<object> = new EventEmitter<object>()
  /** */
  clickLeftData: EventEmitter<object> = new EventEmitter<object>()
  /** */
  getLeftData: EventEmitter<object> = new EventEmitter<object>()
  /*** */
  hoverLeftData: EventEmitter<object> = new EventEmitter<object>()
  /** */
  getMaterial: EventEmitter<object> = new EventEmitter<object>()
  /*  */
  testInputBtn: EventEmitter<object> = new EventEmitter<object>()
  /*****/
  panelBottom: EventEmitter<object> = new EventEmitter<object>();
  /***/
  cueInfo:EventEmitter<object> = new EventEmitter<object>();


  /**  */
  get rigthClickData() {
    return popRightData
  }

  /**  */
  get panelTopData() {
    return panelTopData
  }



  /** panelTopFuncData */
  getPanelTopFuncData() {
    return PanelTopFuncData
  }


  /** inputBox */
  get inputBoxsCache() {
    return this._inputBoxsCache
  }
  set inputBoxsCache(data) {
    this._inputBoxsCache = data
  }

  /** inputBox */
  setInputBoxs() {
    if (this.inputBoxsCache.length == 0)
      return this.inputBoxs.emit([])
    let newDatas = []
    let data = null
    for (let i = 0; i < this.inputBoxsCache.length; i++) {
      data = {
        position: this.transPos(this.inputBoxsCache[i].pos),
        index: this.inputBoxsCache[i].index,
        value: this.inputBoxsCache[i].value,
        css: {
          "width": 50,
          "height": 18
        },
        isAutoFocus: this.inputBoxsCache[i].focus == undefined ? false : this.inputBoxsCache[i].focus,
        func: this.inputBoxsCache[i].func,
        focus: this.inputBoxsCache[i].focus
      }
      newDatas.push(data)
    }
    return this.inputBoxs.emit(newDatas)
  }

  /*  */
  get buttonsCache() { return this._buttonsCache }
  set buttonsCache(data) { this._buttonsCache = data }

  /*  */
  setButtons() {
    if (this.buttonsCache == null)
      return this.buttons.emit(null)

    let newData = {
      outArea: this.buttonsCache.outArea,
      content: this.buttonsCache.content,
      // index:this.buttonsCache.
    }
    newData.outArea.position = this.transPos(newData.outArea.position)
    return this.buttons.emit(newData)
  }


}
