import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { retry } from 'rxjs/operators';

@Injectable()
export class HttpService {


  private _dataUrls: UrlType
  get dataUrls() { return this._dataUrls }
  set dataUrls(data) { this._dataUrls = data }


  constructor(private http: HttpClient) { }



  /**
   * 
   * @param url 
   * @param retryInt 
   * @param isMain ,false,,mainUrl
   */
  getRes(url: string, retryInt: number = 3, isMain: boolean = false) {
    url = isMain ? url : this.getRealUrl(url)
    return this.http.get(url).pipe(retry(retryInt))
  }

  /*  */
  getRealUrl(url: string, mainUrl?: string) {
    mainUrl = mainUrl ? mainUrl : this.dataUrls.mainUrl
    url = url[0] == '/' ? mainUrl + url : mainUrl + '/' + url
    return url
  }

}

export class UrlType {
  mainUrl: string;
  cmdUrl: string;
  loadUrl: string;
  panelLeftMenuUrl: string;
  panelTopStyleUrl: string;
  panelRightStyleUrl: string;
  modelMainUrl: string;
  furnitureModelUrl: string;
  doorWinTypeUrl: string;
  iconUrlsUrl: string;
  threeAssetsMapUrl: string;
  threeWallMap: string;
  threeFootLineMap: string;
  threeWinFrameMap: string;
  threeDoorFrameMap: string;
  threePlasterCorniceMap: string;
  threeDoorFramePic: string;
  threeWallPlastLinePic: string;
  threeWallFootLinePic: string;
  threeRoomFloorPic: string;
  roomType: string;
  threeRoomRoofPic: string;
  threeWinMarblePic: string;
  j2dUrl: string;
  j2dMap: string;
}
