import { JDesign } from './JDesign'
import { DesignCommon as DC } from '../class/DesignCommon'
import { JCowall } from './JCowall';
import { JWall } from './JWall';
import { JRoom } from './JRoom';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';

/**  */
export class JPoint extends JDesign {

    /** cowallID*/
    private _cowallIDs: string[];
    /** cowallID*/
    get cowallIDs() {
        return this._cowallIDs
    }
    set cowallIDs(obj: string[]) {
        this._cowallIDs = obj
        //this.strongUpdate()
    }

    private _cornWinID: string
    /** IDs */
    get cornWinID() { return this._cornWinID }
    set cornWinID(data) { this._cornWinID = data }


    /**  */
    private _pos: DC.Pos;
    /**  */
    get pos() { return this._pos }
    set pos(obj: DC.Pos) {
        this._pos = obj
    }

    constructor(
        obj: JPointInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService
    ) {
        super({ type: 'point', id: obj.id }, dataService)
        this.pos = obj.pos != undefined ? obj.pos : null
        this.cowallIDs = obj.cowallIDs != undefined ? obj.cowallIDs : []
        this.cornWinID = obj.cornWinID ? obj.cornWinID : null
        this.dataService.points[this.id] = this
    }

    /** cowall */
    getRayCowallArr() {
        let cowallArr = this.getCowallArr()
        for (let i = cowallArr.length - 1; i >= 0; i--) {
            if (cowallArr[i].getPointStart().id != this.id) {
                cowallArr.splice(i, 1)
            }
        }
        return cowallArr
    }

    /** cowall */
    getCowallArr() {
        let cowallArr: JCowall[] = []
        if (this.cowallIDs == undefined || this.cowallIDs == null)
            return []
        for (let i = 0; i < this.cowallIDs.length; i++) {
            let obj: JCowall = this.dataService.cowalls[this.cowallIDs[i]]
            cowallArr.push(obj)
        }
        return cowallArr
    }

    /**  */
    getCornWin() {
        if (this.cornWinID == null)
            return null
        else
            return this.dataService.cornerWins[this.cornWinID]
    }

    /** wall */
    getWallArr() {
        let cowallArr: JCowall[] = this.getCowallArr()
        let wallArr: JWall[] = []
        let wallIDArr: string[] = []
        let wall: JWall = null
        for (let i = 0; i < cowallArr.length; i++) {
            wallIDArr.push(cowallArr[i].edgeID)
        }
        /**  */
        wallIDArr = this.DUService.cal.unique(wallIDArr)
        for (let i = 0; i < wallIDArr.length; i++) {
            wall = this.dataService.walls[wallIDArr[i]]
            wallArr.push(wall)
        }

        return wallArr
    }

    /** ID */
    getRoomsID() {
        let cowallArr: JCowall[] = this.getCowallArr()
        let roomsID: string[] = []
        for (let i = 0; i < cowallArr.length; i++) {
            if (cowallArr[i].roomID != null || cowallArr[i].roomID != undefined)
                roomsID.push(cowallArr[i].roomID)
        }
        roomsID = this.DUService.cal.unique(roomsID)
        return roomsID
    }

    /**  */
    getRoomArr() {
        let roomsID = this.getRoomsID()
        let roomArr: JRoom[] = []
        for (let i = 0; i < roomsID.length; i++) {
            roomArr.push(this.dataService.rooms[roomsID[i]])
        }

        return roomArr
    }

    /** set */
    setTrans(plusPos: DC.Pos, RadianInterval?: number) {
        if (RadianInterval == undefined)
            this.pos = { x: this.pos.x + plusPos.x, y: this.pos.y + plusPos.y }
        else {
            let pos: DC.Pos = { x: this.pos.x + plusPos.x, y: this.pos.y + plusPos.y }
            let pointArr: JPoint[] = this.getNearPointArr()
            let point: JPoint = null
            for (point of pointArr) {
                let radian: number = this.DUService.pos.getRadian(pos, point.pos)
                if (Math.abs(radian % (Math.PI / 2)) < RadianInterval) {
                    console.log(radian)
                }
            }
            this.pos = { x: this.pos.x + plusPos.x, y: this.pos.y + plusPos.y }
        }

    }

    /**  */
    getNearPointArr(): JPoint[] {
        let wallArr: JWall[] = this.getWallArr()
        let nearPointArr: JPoint[] = []
        let pointArr: JPoint[] = null
        for (let i = 0; i < wallArr.length; i++) {
            pointArr = wallArr[i].getPointArr()
            if (pointArr[0].id == this.id) {
                nearPointArr.push(pointArr[1])
            }
            else {
                nearPointArr.push(pointArr[0])
            }
        }
        return nearPointArr
    }

    /**  */
    // relevantUpdate() {
    //     let cowallArr = this.getCowallArr()
    //     for (let cowall of cowallArr)
    //         cowall.strongUpdate()

    //     let wallArr = this.getWallArr()
    //     for (let wall of wallArr)
    //         wall.strongUpdate()

    //     let roomArr = this.getRoomArr()
    //     for (let room of roomArr) {
    //         //console.log(room.id)
    //         room.strongUpdate()
    //     }

    // }

    /**  */
    // stopPosUpdate() {
    //     this._posUpdateON = false
    // }

    /**  */
    // startPosUpdate() {
    //     this._posUpdateON = true
    //     // this.strongUpdate()

    //     // let pointArr: JPoint[] = this.getNearPointArr()
    //     // this.relevantUpdate()
    //     // for (let point of pointArr)
    //     //     point.relevantUpdate()
    // }
}

export class JPointInitType {
    id?: string;
    cowallIDs?: string[];
    flag?: number;
    pos?: DC.Pos;
    cornWinID?: string;
}