import { Component, OnInit,Input } from '@angular/core';

@Component({
  selector: '[test]',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent implements OnInit {
  private _testData
  style:any;
  @Input() set test(data){
    // console.log(data)
    if(data != null){
      if(data.pos != null){
        this.style = {
          "left":data.pos.x + 'px',
          "top":data.pos.y + 'px'
        }
      }
      else{
        this.style = {
          "display":"none"
        }
      }
      this._testData = data;
    }
  }
  get test(){return this._testData;}
  constructor() { }

  ngOnInit() {
  }

}
