import { JDesign } from "./JDesign";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JPoint } from "./JPoint";
import { JRoom } from "./JRoom";
import { JWall } from "./JWall";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";

export class JCowall extends JDesign {
    /** point */
    private _pointIDs: string[];
    /** point point */
    get pointIDs() { return this._pointIDs }
    set pointIDs(obj) {
        /**  */
        if (this.pointIDs != undefined && this.pointIDs != []) {
            let oldPointArr: JPoint[] = this.getPointArr()
            for (let i = 0; i < oldPointArr.length; i++) {
                for (let j = oldPointArr[i].cowallIDs.length - 1; j >= 0; j--) {
                    if (oldPointArr[i].cowallIDs[j] == this.id) {
                        oldPointArr[i].cowallIDs.splice(j, 1)
                        break;
                    }
                }
            }
        }

        this._pointIDs = obj

        /**  */
        if (obj != undefined && obj.length != 0) {

            let pointArr: JPoint[] = this.getPointArr()
            for (let i = 0; i < pointArr.length; i++) {
                pointArr[i].cowallIDs.push(this.id)
                pointArr[i].cowallIDs = this.DUService.cal.unique(pointArr[i].cowallIDs)
            }
        }

    }

    /**  */
    private _roomID: string;
    /**  floorplan */
    get roomID() { return this._roomID }
    set roomID(obj) {

        let remove: boolean = false
        if (this._roomID == undefined || this._roomID == null) {
            remove = true
        }

        this._roomID = obj

        /** ,floorplan */
        if (obj == null || obj == undefined) {
            this.dataService.floorplan.cowallIDs.push(this.id)
            this.dataService.floorplan.cowallIDs = this.DUService.cal.unique(this.dataService.floorplan.cowallIDs)
            remove = false
        }

        /** ,floorplan */
        if (remove) {
            this.DUService.cal.removeByValue(this.dataService.floorplan.cowallIDs, this.id)
        }
    }


    /** cowallID */
    private _nextID: string;
    /** cowallID */
    get nextID() { return this._nextID }
    set nextID(obj) {
        this._nextID = obj
        // this.strongUpdate()
    }

    /** cowallID */
    private _prevID: string;
    /** cowallID */
    get prevID() { return this._prevID }
    set prevID(obj) {
        this._prevID = obj
        //this.strongUpdate()
    }

    /** wallID */
    private _edgeID: string;
    /** wallID */
    get edgeID() { return this._edgeID }
    set edgeID(obj) {
        this._edgeID = obj
        // let wall: JWall = this.getWall()
        // if (wall.coedgeID == undefined || wall.coedgeID == null) {
        //     wall.coedgeID = this.id
        // } else {
        //     let partnerCowall = wall.getCowall()
        //     this.partnerID = partnerCowall.id
        //     partnerCowall.partnerID = this.id
        // }
        // this.strongUpdate()
    }

    /** wallcowall */
    private _partnerID: string;
    /** wallcowall cowall */
    get partnerID() { return this._partnerID }
    set partnerID(obj) {
        this._partnerID = obj
        if (obj != null) {
            let cowall: JCowall = this.dataService.cowalls[obj]
            if (cowall != undefined && cowall != null && cowall.partnerID != this.id) {
                cowall.partnerID = this.id
            }


        }

        //this.strongUpdate()
    }

    /**  */
    private _reversed: boolean;
    /**  */
    get reversed() { return this._reversed }
    set reversed(obj) {
        this._reversed = obj
    }

    /**  */
    private _labelDisplay: boolean = true
    /**  */
    get labelDisplay() { return this._labelDisplay }
    set labelDisplay(data) { this._labelDisplay = data }

    constructor(
        obj: JCowallInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService
    ) {
        super({ type: 'cowall', id: obj.id }, dataService)

        this.reversed = obj.reversed != undefined ? obj.reversed : false
        this.edgeID = obj.edgeID != undefined ? obj.edgeID : null
        this.nextID = obj.nextID != undefined ? obj.nextID : null
        this.prevID = obj.prevID != undefined ? obj.prevID : null
        this.partnerID = obj.partnerID != undefined ? obj.partnerID : null
        this.roomID = obj.roomID != undefined ? obj.roomID : null
        this.pointIDs = obj.pointIDs != undefined ? obj.pointIDs : []

        this.dataService.cowalls[this.id] = this
    }


    /**  */
    getWall(): JWall {
        let wall: JWall = this.dataService.walls[this.edgeID]
        return wall
    }

    /**  */
    isSamePos(): boolean {
        let posArr: DC.Pos[] = this.getPosArr()
        if (posArr[0].x == posArr[1].x && posArr[0].y == posArr[1].y) {
            return true
        }
        else {
            return false
        }
    }

    /**  */
    isPointByPrev(): boolean {
        let cowall: JCowall = this.getPrevCowall()
        return cowall.isSamePos()
    }

    /**  */
    isPointBynext(): boolean {
        let cowall: JCowall = this.getNextCowall()
        return cowall.isSamePos()
    }


    /**  */
    getOppsiteCowall(): JCowall {
        if (this.partnerID == null) {
            return null
        }
        else {
            let cowall: JCowall = this.dataService.cowalls[this.partnerID]
            return cowall
        }
    }

    /**  */
    getRoom(): JRoom {
        if (this.roomID == null)
            return null
        let room: JRoom = this.dataService.rooms[this.roomID]
        return room
    }

    /**  */
    getPointArr(): JPoint[] {
        let pointStart: JPoint = this.getPointStart()
        let pointEnd: JPoint = this.getPointEnd()
        let pointArr: JPoint[] = [pointStart, pointEnd]
        return pointArr
    }

    /**  */
    getPosArr(): DC.Pos[] {
        let pointArr: JPoint[] = this.getPointArr()
        let posArr: DC.Pos[] = [pointArr[0].pos, pointArr[1].pos]
        return posArr
    }

    /**  */
    getPointStart(): JPoint {
        if (this.reversed) {
            let point: JPoint = this.dataService.points[this.pointIDs[1]]
            return point
        }
        else {
            let point: JPoint = this.dataService.points[this.pointIDs[0]]
            return point
        }
    }

    /**  */
    getPointEnd(): JPoint {
        if (!this.reversed) {
            let point: JPoint = this.dataService.points[this.pointIDs[1]]
            return point
        }
        else {
            let point: JPoint = this.dataService.points[this.pointIDs[0]]
            return point
        }
    }

    /** 
     * @param reCal ,false,
     */
    getRadian(reCal: boolean = false): number {
        if (reCal) {
            let posArr: DC.Pos[] = this.getPosArr()
            let radian: number = this.DUService.pos.getRadian(posArr[0], posArr[1])
            return radian
        }
        let wall = this.getWall()
        if (wall.coedgeID == this.id)
            return wall.getRadian()
        else
            return wall.getRadian() + Math.PI

    }

    /**  */
    getRotate(): number {
        let radian: number = this.getRadian()
        let rotate: number = radian * 180 / Math.PI
        return rotate
    }

    /**  */
    getPrevCowall(): JCowall {
        let cowall: JCowall = this.dataService.cowalls[this.prevID]
        return cowall
    }

    /**  */
    getNextCowall(): JCowall {
        let cowall: JCowall = this.dataService.cowalls[this.nextID]
        return cowall
    }

    /**  */
    getDistance(): number {
        let posArr: DC.Pos[] = this.getPosArr()
        let distance: number = this.DUService.pos.getDistance(posArr[0], posArr[1])
        return distance
    }

    /**  */
    getInLine(): DC.Pos[] {
        let wall = this.getWall()
        if (!wall.outLine)
            return undefined
        if (wall.coedgeID == this.id) {
            return wall.getInLine()
        }
        else {
            return wall.getOutLine()
        }
    }

    /**  */
    getDrawInLine(): DC.Pos[] {
        let wall = this.getWall()
        if (!wall.drawOutLine)
            return undefined
        if (wall.coedgeID == this.id) {
            return wall.getDrawInLine()
        }
        else {
            return wall.getDrawOutLine()
        }
    }

    /** */
    getInLineDistance() {
        let posArr = this.getInLine()
        return this.DUService.pos.getDistance(posArr[0], posArr[1])
    }

    /**  */
    getOutLine(): DC.Pos[] {
        let wall = this.getWall()
        if (wall.outLine == null)
            return null
        if (wall.coedgeID == this.id) {
            return wall.getOutLine()
        }
        else {
            return wall.getInLine()
        }
    }

    /**  */
    getDrawOutLine(): DC.Pos[] {
        let wall = this.getWall()
        if (!wall.drawOutLine)
            return undefined
        if (wall.coedgeID == this.id) {
            return wall.getDrawOutLine()
        }
        else {
            return wall.getDrawInLine()
        }
    }

    /**  */
    getOutLineDistance() {
        let posArr = this.getOutLine()
        return this.DUService.pos.getDistance(posArr[0], posArr[1])
    }

    /**  */
    getVector() {
        let posArr = this.getPosArr()
        let vector = { x: posArr[1].x - posArr[0].x, y: posArr[1].y - posArr[0].y }
        return vector
    }

}

export class JCowallInitType {
    id?: string;
    roomID?: string;
    flag?: number;
    prevID?: string;
    nextID?: string;
    partnerID?: string;
    edgeID?: string;
    pointIDs?: string[];
    reversed?: boolean;
}