import { PopDivService } from "../../../services/pop-div.service";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../services/dom-update.service";
import { JCornerWin3D } from "../../../three/jDesign3D/JCornerWin3D";

export class CornerWindowPanel {
    constructor(
        private popService: PopDivService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService
    ) {
        popService.panelRight.subscribe((data) => {
            if (data == null) {
                this.popService.panelRightData.emit(null)
            }
            else {
                if (data.type == 'cornerWin') {
                    this.setCornerWindowData(data)
                }
            }
        })
    }
    // z
    setCornerWindowData(obj: JCornerWin3D) {
        console.log(obj.floorHeight)
        // console.log(this.dataService.json.marblePics)
        let data = [
            {
                type: "input",
                data: [
                    {
                        name: "a",
                        isEditable: false,
                        value: obj.LWidth,
                        func: (e, params) => {
                            obj.LWidth = parseInt(params.value)
                            obj.act3D.end()
                            this.dataService.cache.cornerWin.moveEnd(obj)
                            this.dataService.cache.common.choose = obj
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "b",
                        isEditable: false,
                        value: obj.LDepth,
                        func: (e, params) => {
                            obj.LDepth = parseInt(params.value)
                            obj.act3D.end()
                            this.dataService.cache.cornerWin.moveEnd(obj)
                            this.dataService.cache.common.choose = obj
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "",
                        isEditable: false,
                        value: obj.height3D,
                        func: (e, params) => {
                            obj.height3D = parseInt(params.value)
                            obj.act3D.end()
                            this.dataService.cache.cornerWin.moveEnd(obj)
                            this.dataService.cache.common.choose = obj
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "c",
                        isEditable: false,
                        value: obj.DDepth,
                        func: (e, params) => {
                            obj.DDepth = parseInt(params.value)
                            obj.act3D.end()
                            this.dataService.cache.cornerWin.moveEnd(obj)
                            this.dataService.cache.common.choose = obj
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "d",
                        isEditable: false,
                        value: obj.DWidth,
                        func: (e, params) => {
                            obj.DWidth = parseInt(params.value)
                            obj.act3D.end()
                            this.dataService.cache.cornerWin.moveEnd(obj)
                            this.dataService.cache.common.choose = obj
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "",
                        isEditable: false,
                        value: obj.floorHeight,
                        func: (e, params) => {
                            obj.floorHeight = parseInt(params.value);
                            obj.act3D.end()
                            this.dataService.cache.cornerWin.moveEnd(obj)
                            this.dataService.cache.common.choose = obj
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    }
                ]
            },
            {
                type: "img",
                data: {
                    img: this.dataService.icon.getIco(''),
                    detail: "a,b,c,d"
                }
            },
            {
                type: "slider",
                data: [
                    {
                        name: "",
                        img: this.callBackCornerWinImgArr(obj, this.dataService.json.marblePics)
                    }
                ]
            }
        ]
        this.popService.panelRightData.emit(data)
    }

    private callBackCornerWinImgArr(obj: JCornerWin3D, arr) {
        let argument = {};
        let imgArr = []
        for (let i = 0; i < arr.length; i++) {
            if (i == obj.getBaywindowStillMatIndex()) {
                argument = {
                    path: arr[i],
                    details: '',
                    borderClass: true,
                    func: (e, params) => {
                        obj.SetBaywindowStillTexture(params.path);
                        obj.SetBaywindowStillMatIndex(i)
                        this.dataService.cache.shortFuncs['saveAction']()
                    }
                }
            }
            else {
                argument = {
                    path: arr[i],
                    details: '',
                    func: (e, params) => {
                        obj.SetBaywindowStillTexture(params.path);
                        obj.SetBaywindowStillMatIndex(i)
                        this.dataService.cache.shortFuncs['saveAction']()
                    }
                }
            }
            imgArr.push(argument);
        }
        return imgArr;
    }

}