import { Injectable } from '@angular/core';
import { newPosUtil, newCalUtil, newSvgUtil, newConsoleUtil, newObjUtil, newAbsorbUtil, newParseJ2D, /*newObjUtil*/ } from '../data/util2d.data';

@Injectable()
export class JdesignUtil2dService {

  constructor() {
  }

  /**  */
  get pos() { return newPosUtil }

  /**  */
  get cal() { return newCalUtil }

  /** svg */
  get svg() {
    return newSvgUtil
  }

  /**  */
  get out() {
    return newConsoleUtil
  }

  /*  */
  get obj() {
    return newObjUtil
  }

  /*  */
  get absorb() {
    return newAbsorbUtil
  }

  get parseJ2D() {
    return newParseJ2D
  }

}
