import { DesignCommon as DC } from '../class/DesignCommon'

export namespace SaveType {

    export class JDesign {
        id: string;
        flag: number;
        type: string;
        mode: string;
        constructor() {
            this.id = null
            this.flag = null;
            this.type = null;
            this.mode = null;
        }
    }

    export class JPoint extends JDesign {
        pos: DC.Pos;
        cowallIDs: string;

        constructor() {
            super()
            this.pos = null
            this.cowallIDs = null
        }
    }

    export class JWall extends JDesign {
        coedgeID: string;
        thickness: number;
        doorIDs: string[];
        winIDs: string[]
        outLine: DC.Pos[][]
        wallType: number;
        furnitureIDs: string[];
        arcDistance: number;
        arcPer: number;
        cornWinIDs: string[]
        constructor() {
            super()
            this.coedgeID = null
            this.thickness = null
            this.doorIDs = null
            this.winIDs = null
            this.outLine = null
            this.wallType = null
            this.furnitureIDs = null
            this.arcDistance = null
            this.arcPer = null
            this.cornWinIDs = null
            // this.windowsID = null
        }
    }

    export class JCowall extends JDesign {
        pointIDs: string[];
        roomID: string;
        nextID: string;
        prevID: string;
        edgeID: string;
        partnerID: string;
        reversed: boolean;
        constructor() {
            super()
            this.pointIDs = null
            this.roomID = null
            this.nextID = null
            this.prevID = null
            this.edgeID = null
            this.partnerID = null
            this.reversed = null
        }
    }

    export class JRoom extends JDesign {
        parentID: string;
        cowallIDs: string[];
        floorID: string;
        roofID: string;
        roomName: string;
        displayPos: DC.Pos;
        isAutoMoveAreaPos: boolean;
        constructor() {
            super()
            this.parentID = null
            this.cowallIDs = null
            this.floorID = null
            this.roofID = null
            this.roomName = null
            this.displayPos = null
            this.isAutoMoveAreaPos = null
        }
    }

    export class JLUWin extends JDesign {
        pos: DC.Pos;
        j2d: any;
        radian: number;
        height3D: number;
        floorHeight: number;
        constructor() {
            super()
            this.pos = null
            this.j2d = null
            this.radian = null
            this.height3D = null
            this.floorHeight = null
        }
    }

    export class JLWin extends JLUWin {
        pointID: string;
        constructor() {
            super()
            this.pointID = null
        }
    }

    export class JUWin extends JLUWin {
        centerWallID: string;
        constructor() {
            super()
            this.centerWallID = null
        }
    }

    export class JFloor extends JDesign {
        roomID: string;
        radian: number;
        size: DC.Size;
        imgUrl: string;

        constructor() {
            super()
            this.roomID = null
            this.radian = null
            this.size = null
            this.imgUrl = null
        }
    }

    export class JRoof extends JDesign {
        roomID: string;
        constructor() {
            super()
            this.roomID = null
        }
    }

    export class JFloorplan extends JDesign {
        roomsID: string[];
        cowallIDs: string[];
        backImg: any;
        constructor() {
            super()
            this.roomsID = null
            this.cowallIDs = null
            this.backImg = null
        }
    }

    export class JDWAbstract extends JDesign {
        wallID: string;
        pos: DC.Pos;
        size: DC.Size
        mirror: boolean;
        out: boolean;
        distancePer: number;
        oldWallID: string;
        radian: number;
        url3D: string;
        svgImg: string;
        style: string;
        plankType: string;
        floorHeight: number;
        modelUrl: string;
        isHaveFrame: boolean

        constructor() {
            super()
            this.wallID = null
            this.pos = null
            this.size = null
            this.mirror = null
            this.out = null
            this.distancePer = null
            this.oldWallID = null
            this.radian = null
            this.url3D = null
            this.svgImg = null
            this.plankType = null
            this.floorHeight = null
            this.modelUrl = null
            this.isHaveFrame = null
            this.style = null
        }

    }

    export class JDoor extends JDWAbstract {

    }

    export class JWin extends JDWAbstract {
        isBay: boolean = null
        outBoxThickness: number = null
        outThickness: number = null
        // constructor() {
        //     super()
        //     this.isBay = false
        // }
    }

    export class JCornerWin extends JDesign {
        pos: DC.Pos = null;
        LThickness: number = null;
        LWidth: number = null;
        LDepth: number = null;
        LRadian: number = null;
        DThickness: number = null;
        DWidth: number = null;
        DDepth: number = null;
        DRadian: number = null;
        pointID: string = null;
        outLine: DC.Pos[] = null;
        outThickness: number = null
        outWallThickness: number = null
        height3D: number = null
        floorHeight: number = null
        constructor() {
            super()
        }
    }

    export class JCamera extends JDesign {
        radian: number;
        headUrl: string;
        headSize: DC.Size;
        rotateUrl: string;
        rotateSize: DC.Size;
        pos: DC.Pos;
        constructor() {
            super()
            this.radian = null
            this.headUrl = null
            this.headSize = null
            this.rotateSize = null
            this.rotateUrl = null
            this.pos = null
        }
    }

    export class JGeometry extends JDesign {
        url2D: string;
        url3D: string;
        pos: DC.Pos;
        radian: number;
        size: DC.Size;
        mirror: boolean;
        distancePer: number;
        floorHeight: number;
        wallID: string;
        wallOut: boolean;
        hgUrl: string
        constructor() {
            super()
            this.url2D = null
            this.url3D = null
            this.pos = null
            this.radian = null
            this.size = null
            this.mirror = null
            this.distancePer = null
            this.wallID = null
            this.wallOut = null
            this.hgUrl = null
        }
    }

    export class JFurniture extends JGeometry {

    }

    export class JBuild extends JGeometry {

    }



    export class JCmd {
        wall: any;
        cowall: any;
        point: any;
        // floorplan: any;
        back: any;
        floor: any;
        // common: any;
        door: any;
        win: any;
        furniture: any;
        cornerWin: any;
        room: any;

        constructor() {
            this.wall = null
            this.cowall = null
            this.point = null
            // this.floorplan = null
            this.back = null
            this.floor = null
            // this.common = null
            this.win = null
            this.door = null
            this.furniture = null
            this.cornerWin = null
            this.room = null
        }

    }

    export class JCache {
        wall: any;
        room: any;
        cowall: any;
        floorplan: any;
        common: any;
        door: any;
        win: any;
        doorWin: any;
        furniture: any;
        build: any;

        constructor() {
            this.wall = null
            this.room = null
            this.cowall = null
            this.floorplan = null
            this.common = null
            this.door = null
            this.win = null
            this.doorWin = null
            this.furniture = null
            this.build = null
        }

    }


}