import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { DomUpdateService } from '../../../services/dom-update.service';

@Component({
  selector: '[wins]',
  templateUrl: './wins.component.html',
  styleUrls: ['./wins.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WinsComponent implements OnInit {

  constructor(
    private DUService: JdesignUtil2dService,
    public dataService: JdesignDataService,
    private domService: DomUpdateService,
    private ref: ChangeDetectorRef
  ) {
    this.domService.svgWin.subscribe(() => {
      this.ref.detectChanges()
    })
  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('wins is ok!!')
    }
  }

}
