import { JDesign } from "./JDesign";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";

export class JCamera extends JDesign {

    /**  */
    private _radian: number
    /**  */
    get radian() { return this._radian }
    set radian(data) { this._radian = data }

    /**  */
    private _pos: { x: number, y: number }
    /**  */
    get pos() { return this._pos }
    set pos(data) { this._pos = data }

    constructor(
        obj: JCameraInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService
    ) {
        super({ type: 'camera', id: obj.id }, dataService)
        this.pos = obj.pos ? obj.pos : null
        this.radian = obj.radian ? obj.radian : 0
    }
}

export class JCameraInitType {
    id?: string;
    radian?: number;
    pos?: { x: number, y: number };
}