import { JdesignDataService } from "../../../services/jdesign-data.service";
import { HttpService, UrlType } from '../../../services/http.service';
import { waterfall, series, each } from 'async'
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { JIcon } from "../jdesign/JIcon";
import { JCmd } from "../jdesign/JCmd";

export class ReadJsonEvent {

    urlJsonUrl: string = './assets/url.json'

    constructor(
        private dataService: JdesignDataService,
        private httpService: HttpService,
        private DUService: JdesignUtil2dService
    ) {

    }

    /**  */
    readData(cb) {
        waterfall([
            (cb) => { this.readUrl(cb) },
            (data, cb) => { this.readCmd(data, cb) },
            (data, cb) => { this.readIco(data, cb) },
            (data, cb) => { this.readWallPaper(data, cb) },
            (data, cb) => { this.readFootLine(data, cb) },
            (data, cb) => { this.readRoomType(data, cb) },
            (data, cb) => { this.readPlasterCornice(data, cb) },
            (data, cb) => { this.readDoorFrame(data, cb) },
            (data, cb) => { this.readWinFrame(data, cb) },
            (data, cb) => { this.readDoorFramePic(data, cb) },
            (data, cb) => { this.readPlasterLinePic(data, cb) },
            (data, cb) => { this.readFootLinePic(data, cb) },
            (data, cb) => { this.readRoofPic(data, cb) },
            (data, cb) => { this.readMarblePic(data, cb) },
            (data, cb) => { this.readFloorPic(data, cb) },
            (data, cb) => { this.readJ2d(data, cb) },
            // (data, cb) => { this.readUWinJ2d(data, cb) }
        ], (err, result) => { this.readEnd(err, result); cb() })
    }

    /**  */
    readUrl(cb) {
        this.httpService.getRes(this.urlJsonUrl, 3, true).subscribe((data: UrlType) => {
            this.httpService.dataUrls = data
            this.dataService.cache.common.mainUrl = data.mainUrl
            // console.log('url')
            cb(undefined, data)
        })
    }

    /** cmdJSon */
    readCmd(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.cmdUrl).subscribe((cmdData: any) => {
            this.DUService.obj.changeObjUrlByflag(cmdData, dataUrl.mainUrl, "**")
            this.dataService.cmd = new JCmd(cmdData)
            // console.log('cmd')
            cb(undefined, dataUrl)
        })
    }

    /* j2d */
    readJ2d(dataUrl: UrlType, cb) {
        let newUrl = dataUrl.j2dUrl + dataUrl.j2dMap
        let url = this.httpService.getRealUrl(newUrl)
        let func = (data: any[]) => {
            if (data.length > 0) {
                let otherUrl = dataUrl.j2dUrl + "/" + data[0]["url"]
                this.httpService.getRes(otherUrl).subscribe((obj) => {
                    this.dataService.cache.baseData.j2d[data[0]["name"]] = obj
                    data.splice(0, 1)
                    func(data)
                })
            }
            else {
                cb(undefined, dataUrl)
            }
        }
        let otherUrl = dataUrl.j2dUrl + "/" + dataUrl.j2dMap
        this.httpService.getRes(otherUrl).subscribe((data) => {
            func(<any[]>data)
        })
    }

    /** icoJson */
    readIco(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.iconUrlsUrl).subscribe((icoData) => {
            this.dataService.icon = new JIcon(dataUrl.mainUrl, icoData)
            // console.log('ico')
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readWallPaper(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeWallMap).subscribe((paperData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(paperData, url)
            this.dataService.json.wallPaper = paperData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readFurnitureModel(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.furnitureModelUrl).subscribe((furnitureData) => {

        })
    }

    /**  */
    readFootLine(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.threeFootLineMap).subscribe((footLineDatas: any[]) => {
            each(footLineDatas, (footLineData, cb) => {
                this.setVertexData(footLineData, cb)
            }, (err) => {
                this.dataService.json.footLine = footLineDatas
                cb(undefined, dataUrl)
            })
        })
    }

    /**  */
    readPlasterCornice(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.threePlasterCorniceMap).subscribe((plasterCorniceDatas: any[]) => {
            each(plasterCorniceDatas, (plasterCorniceData, cb) => {
                this.setVertexData(plasterCorniceData, cb)
            }, (err) => {
                this.dataService.json.plasterCornice = plasterCorniceDatas
                cb(undefined, dataUrl)
            })
        })
    }

    /**  */
    readDoorFrame(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.threeDoorFrameMap).subscribe((doorFrameMaps: any[]) => {
            each(doorFrameMaps, (doorFrameMap, cb) => {
                this.setVertexData(doorFrameMap, cb)
            }, (err) => {
                this.dataService.json.doorFrame = doorFrameMaps
                cb(undefined, dataUrl)
            })
        })
    }

    /**  */
    readWinFrame(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.threeWinFrameMap).subscribe((winFrameMaps: any[]) => {
            each(winFrameMaps, (winFrameMap, cb) => {
                this.setVertexData(winFrameMap, cb)
            }, (err) => {
                this.dataService.json.winFrame = winFrameMaps
                cb(undefined, dataUrl)
            })
        })
    }

    /**  */
    setVertexData(data, cb) {
        this.httpService.getRes(data.vertexDataURL, 3, true).subscribe((vertexData: any) => {
            // console.log(vertexData.vertexData)
            if (this.checkShun(vertexData.vertexData))
                vertexData.vertexData.reverse()
            data["vertexData"] = vertexData.vertexData
            cb()
        })
    }

    /**  */
    readDoorFramePic(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeDoorFramePic).subscribe((picData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(picData, url)
            this.dataService.json.doorFramePics = picData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readPlasterLinePic(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeWallPlastLinePic).subscribe((picData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(picData, url)
            this.dataService.json.plasterLinePics = picData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readFootLinePic(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeWallFootLinePic).subscribe((picData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(picData, url)
            this.dataService.json.footLinePics = picData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readRoofPic(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeRoomRoofPic).subscribe((picData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(picData, url)
            this.dataService.json.roofPics = picData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readMarblePic(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeWinMarblePic).subscribe((picData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(picData, url)
            this.dataService.json.marblePics = picData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readFloorPic(dataUrl: UrlType, cb) {
        let url = this.httpService.getRealUrl(dataUrl.threeAssetsMapUrl)
        this.httpService.getRes(dataUrl.threeRoomFloorPic).subscribe((picData: any[]) => {
            this.DUService.obj.changeObjUrlByattrs(picData, url)
            this.dataService.json.floorPics = picData
            cb(undefined, dataUrl)
        })
    }

    /**  */
    readRoomType(dataUrl: UrlType, cb) {
        this.httpService.getRes(dataUrl.roomType).subscribe((typeData: any[]) => {
            this.dataService.json.roomType = typeData
            cb(undefined, dataUrl)
        })
    }



    /**  */
    readEnd(err, result) {
        console.log('json')
        // console.log(result)
    }

    /**
     * 
     *  @param _allPoints [[x,y],[x,y],[x,y]]
     */
    checkShun(_allPoints) {
        //------------------------------------------
        let area = 0;
        for (var i = 0; i < _allPoints.length; i++) {
            let j = (i + 1) % _allPoints.length;
            area += _allPoints[i][0] * _allPoints[j][1];
            area -= _allPoints[j][0] * _allPoints[i][1];
        }
        if (area > 0) {
            return false;
        } else {
            return true;
        }
    }

}

