import { JBuild } from "../../svg/common/jdesign/JBuild";
import { THREEDataServer } from "../classes/THREEDataServer";
import { ModelType, CustomUserData, ProfileModelData, MyDirect } from '../classes/dataStruct';
import { ProfileModel } from "../classes/ProfileModel";
import { PopDivService } from '../../services/pop-div.service';
import { JdesignDataService } from '../../services/jdesign-data.service';



declare var THREE: any;

export class JBuild3D extends JBuild {
    private jDataServer: JdesignDataService;
    private buildTexture = null;             //
    private paramsValues = {
        materialIndex: [],                         //
        kickLineProfileShapeIndex: [],             //
        kickLineProfileMatIndex: [],               //
        plasterCorniceProfileShapeIndex: [],       //
        plasterCorniceProfileMatIndex: [],         //
    }
    private profileModels: ProfileModelData[] = [];              //
    private allWallTypes = [                                     //
        MyDirect.front,
        MyDirect.back,
        MyDirect.right,
        MyDirect.left
    ];

    public SetDataServerAndOthers(_dataServer: THREEDataServer, _jDataServer: JdesignDataService, _popServer: PopDivService) {
        if (this.dataServer != undefined) {
            return;
        }
        this.jDataServer = _jDataServer;
        this.FatherSetDataServerAndOthers(_dataServer, _popServer);
        //paramsValues--------------------------------------
        this.paramsValues.materialIndex[ModelType.build] = 0;
        this.paramsValues.kickLineProfileShapeIndex[ModelType.build] = 0;
        this.paramsValues.kickLineProfileMatIndex[ModelType.build] = 0;
        this.paramsValues.plasterCorniceProfileShapeIndex[ModelType.build] = 0;
        this.paramsValues.plasterCorniceProfileMatIndex[ModelType.build] = 0;
        //profileModels---------------------------------
        for (let i = 0; i < this.allWallTypes.length; i++) {
            this.profileModels[this.allWallTypes[i]] = new ProfileModelData();
        }
        //------------------------------------------
        this.SetProfileData();
        this.OnModelMove.push(this.OnBuildBeMove.bind(this));
    }

    private OnBuildBeMove() {
        this.UpdateProfileTransform();
    }


    private UpdateProfileTransform() {
        if (this.profileModels[MyDirect.front].kickLine != null) {
            this.profileModels[MyDirect.front].kickLine.profileMesh.position.x = this.pos.x;
            this.profileModels[MyDirect.front].kickLine.profileMesh.position.z = this.pos.y;
            this.profileModels[MyDirect.front].kickLine.profileMesh.rotation.y = -this.radian;
        }
    }

    private SetProfileData() {
        //--------------------------------------------
        this.profileModels[MyDirect.front].profileDir = [new THREE.Vector3(-this.size.width / 2, 0.0, -this.size.height / 2), new THREE.Vector3(this.size.width / 2, 0.0, -this.size.height / 2)];
        this.profileModels[MyDirect.front].cutPlaneDir = [
            new THREE.Vector3(-1, 0, -1),
            new THREE.Vector3(1, 0, -1),
        ];
        //--------------------------------------------
        this.profileModels[MyDirect.back].profileDir = [new THREE.Vector3(-this.size.width / 2, 0.0, this.size.height / 2), new THREE.Vector3(this.size.width / 2, 0.0, this.size.height / 2)];
        this.profileModels[MyDirect.back].cutPlaneDir = [
            new THREE.Vector3(-1, 0, 1),
            new THREE.Vector3(1, 0, 1),
        ];
    }

    public MakeBuild() {
        //-----------------------------
        this.furnitureFarther = new THREE.Object3D();
        this.dataServer.sceneCommonObj.scene.add(this.furnitureFarther);
        //-------------------------------
        let _buildModel = this.dataServer.classesObj.boxMaker.MakeBox(this.size.width, this.size.height3D, this.size.height, true, true);
        this.LoadTextureByIndex(0);

        _buildModel.material.map = this.buildTexture;
        _buildModel.material.color.r = 1.0;
        _buildModel.material.color.g = 1.0;
        _buildModel.material.color.b = 1.0;
        _buildModel.material.needsUpdate = true;


        this.furnitureFarther.add(_buildModel);
        //-------------------------------
        this.selector = this.dataServer.classesObj.boxMaker.MakeBox(this.size.width, this.size.height3D, this.size.height, true, true);
        this.selector.material = new THREE.MeshBasicMaterial({ color: 0xffff00, visible: false });
        this.dataServer.sceneCommonObj.scene.add(this.selector);
        //---------------------------------------
        let _customUserData: CustomUserData = new CustomUserData();
        _customUserData.longObj = this;
        _customUserData.modelType = ModelType.build;

        this.selector.userData.customUserData = _customUserData;
        this.dataServer.classesObj.rayHitModelList.AddRayHitModel(this.selector);
        //-------------------------------------------
        this.wireFrame = this.dataServer.classesObj.boxMaker.MakeBox(this.size.width, this.size.height3D, this.size.height, true, true);
        this.wireFrame.material = new THREE.MeshBasicMaterial({ color: 0xffff00, wireframe: true, visible: false });
        this.dataServer.sceneCommonObj.scene.add(this.wireFrame);
        //----------------------------
        this.dataServer.classesObj.modelTransformer.SetRotatable(true);
        this.dataServer.classesObj.modelTransformer.SetVerticalMovable(false);
        //--------------------------------------
        this.dataServer.classesObj.modelTransformer.SetEnable(false);
        //----------------------------------
        this.dataServer.classesObj.modelTransformer.ClearControlModel();

    }


    private LoadTextureByIndex(_textureIndex) {
        let _initTextureUrl = this.jDataServer.json.wallPaper[_textureIndex];

        this.buildTexture = this.dataServer.loaders.textureLoader.load(_initTextureUrl);
        this.buildTexture.wrapS = THREE.RepeatWrapping;
        this.buildTexture.wrapT = THREE.RepeatWrapping;
    }


    //==============================================
    public GetMaterialSelectIndex() {
        return this.paramsValues.materialIndex[ModelType.build];
    }

    //==============================================
    public SetMaterialSelectIndex(_index) {
        this.paramsValues.materialIndex[ModelType.build] = _index;
    }

    //=========================================
    public GetKickLineProfileIndex() {
        return this.paramsValues.kickLineProfileShapeIndex[ModelType.build];
    }

    //=========================================
    public SetKickLineProfileIndex(_index) {
        this.paramsValues.kickLineProfileShapeIndex[ModelType.build] = _index;
    }

    //=========================================
    public GetPlasterCorniceProfileIndex() {
        return this.paramsValues.plasterCorniceProfileShapeIndex[ModelType.build];
    }

    //=========================================
    public SetPlasterCorniceProfileIndex(_index) {
        this.paramsValues.plasterCorniceProfileShapeIndex[ModelType.build] = _index;
    }

    //============================================
    public GetKickLineProfileMatIndex() {
        return this.paramsValues.kickLineProfileMatIndex[ModelType.build];
    }

    //=============================================
    public SetKickLineProfileMatIndex(_index) {
        this.paramsValues.kickLineProfileMatIndex[ModelType.build] = _index;
    }

    //=============================================
    public GetPlasterCorniceProfileMatIndex() {
        return this.paramsValues.plasterCorniceProfileMatIndex[ModelType.build];
    }

    //=============================================
    public SetPlasterCorniceProfileMatIndex(_index) {
        this.paramsValues.plasterCorniceProfileMatIndex[ModelType.build] = _index;
    }

    //===========================================================================
    public MakeKickLine(_profileDatas: any) {
        for (let i = 0; i < this.allWallTypes.length; i++) {
           
            if(this.profileModels[this.allWallTypes[i]].profileDir==undefined){
                continue;
            }
            this.profileModels[this.allWallTypes[i]].kickLineProfilePoints = _profileDatas;
            //ProfileModel---------------
            if (this.profileModels[this.allWallTypes[i]].kickLine == null) {
                this.profileModels[this.allWallTypes[i]].kickLine = new ProfileModel(this.dataServer, ModelType.kickLine, this.jDataServer);
            }
            this.profileModels[this.allWallTypes[i]].kickLine.MakeStraightProfile(
                this.paramsValues.kickLineProfileMatIndex[this.allWallTypes[i]],
                0,
                this.profileModels[this.allWallTypes[i]].kickLineSize,
                this.profileModels[this.allWallTypes[i]].profileDir,
                this.profileModels[this.allWallTypes[i]].cutPlaneDir,
                this.profileModels[this.allWallTypes[i]].kickLineProfilePoints,
                []
            );
        }



        this.UpdateProfileTransform();


    }
}