import { DoorWinCal } from "./doorWin.cal";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { JWin, JWinInitType } from "../jdesign/JWin";
import { JWin3D } from "../../../three/jDesign3D/JWin3D";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JWall } from "../jdesign/JWall";
import { JBayWin3D } from "../../../three/jDesign3D/JBayWin3D";

export class WinCal extends DoorWinCal {

    constructor(
        protected dataService: JdesignDataService,
        protected DUService: JdesignUtil2dService,
        protected calService: CalculatorsService
    ) {
        super(dataService, DUService, calService)
    }

    /** path */
    getNormalPath(win: JWin, pos?: DC.Pos) {
        pos = pos == undefined ? win.pos : pos
        let size = win.getPixSize()
        let rectPosArr: DC.Pos[] = this.DUService.pos.getRectPosByCenter(pos, size)
        /**  */
        if (win.outThickness != 0) {
            let radian = this.DUService.pos.getRadian(rectPosArr[3], rectPosArr[0])
            rectPosArr[0] = this.DUService.pos.getRayPos(rectPosArr[3], radian, win.outThickness + size.height)
            rectPosArr[1] = this.DUService.pos.getRayPos(rectPosArr[2], radian, win.outThickness + size.height)
        }
        return rectPosArr
    }

    /** path */
    getNormalPathString(win: JWin, pos?: DC.Pos) {
        let path = this.getNormalPath(win, pos)
        let pathString = this.DUService.svg.getPath(true, ...path)
        return pathString
    }

    /**  */
    createNewNormal(obj: JWinInitType) {
        this.dataService.cache.floorplan.createAndMove = true
        obj.style = 'normal'
        let win = new JWin3D(obj, this.dataService, this.DUService)
        this.dataService.cache.win.createFunc(win)
        return win
    }

    /**  */
    createBayWin(size?: DC.Size, outThickness?: number, outBoxThickness?: number) {
        this.dataService.cache.floorplan.createAndMove = true
        size = size ? size : { width: 1600, height: 240, height3D: 1500 }
        outThickness = outThickness ? outThickness : 600
        outBoxThickness = outBoxThickness ? outBoxThickness : 60
        let win = new JBayWin3D({ style: "normal", size: size, outThickness: outThickness, isBay: true, outBoxThickness: outBoxThickness, plankType: "bayWin" }, this.dataService, this.DUService)
        this.dataService.cache.win.createFunc(win)
        return win
    }

    /**  */
    del(win: JWin) {
        win.destory3D()
        delete this.dataService.wins[win.id]
    }

    protected wallChange(win: JWin, wall: JWall) {
        wall.winIDs.push(win.id)
        wall.winIDs = this.DUService.cal.unique(wall.winIDs)
    }

    /**  */
    listAbosrb() {
        this.commmonListAbosrb('win')
    }

}
