/*
 
 
*/

import { HGmodelUtils } from './HGmodelUtils';
import { StructModelRawDatas } from './HGStruct';
declare var THREE: any;

export class HGHardware {
    private modelRawDatas: StructModelRawDatas = {
        vertices: [],     //
        faces: [],        //Face
        uvs: [[]]           //uv
    }
    public mesh;         // 
    constructor(_width: number, _height: number, _deep: number, _textureVertical: boolean) {
        //
        let topFaceVertices = [
            [0, _height, 0],
            [0, _height, _deep],
            [_width, _height, _deep],
            [_width, _height, 0],
        ]
        HGmodelUtils.MakeFaceFrom4Points(this.modelRawDatas, topFaceVertices, _textureVertical);
        //
        let verticalFaceFrontVertices = [
            [0, 0, 0],
            [0, _height, 0],
            [_width, _height, 0],
            [_width, 0, 0],
        ]
        HGmodelUtils.MakeFaceFrom4Points(this.modelRawDatas, verticalFaceFrontVertices, _textureVertical);
        //
        let verticalFaceRightVertices = [
            [0, 0, _deep],
            [0, _height, _deep],
            [0, _height, 0],
            [0, 0, 0]
        ]
        HGmodelUtils.MakeFaceFrom4Points(this.modelRawDatas, verticalFaceRightVertices, _textureVertical);
        //
        let verticalFaceLeftVertices = [
            [_width, 0, 0],
            [_width, _height, 0],
            [_width, _height, _deep],
            [_width, 0, _deep]
        ]
        HGmodelUtils.MakeFaceFrom4Points(this.modelRawDatas, verticalFaceLeftVertices, _textureVertical);
        //
        let verticalFaceBackVertices = [
            [_width, 0, _deep],
            [_width, _height, _deep],
            [0, _height, _deep],
            [0, 0, _deep]
        ]
        HGmodelUtils.MakeFaceFrom4Points(this.modelRawDatas, verticalFaceBackVertices, _textureVertical);

        //
        let verticalFaceBottomertices = [
            [_width, 0, 0],
            [_width, 0, _deep],
            [0, 0, _deep],
            [0, 0, 0]
        ]
        HGmodelUtils.MakeFaceFrom4Points(this.modelRawDatas, verticalFaceBottomertices, _textureVertical);



        let theGeometry = HGmodelUtils.MakeGeometryFromRawData(this.modelRawDatas);
        this.mesh = new THREE.Mesh(theGeometry, new THREE.MeshPhongMaterial({
             visible:false,
             wireframe:true
        }));
        //
        this.mesh.material.formerColor=new THREE.Color();
        this.mesh.material.formerColor.copy(this.mesh.material.color);
    }
}