import { JDesign } from "./JDesign";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JCowall } from "./JCowall";
import { JWall } from "./JWall";
import { JPoint } from "./JPoint";
import { DesignCommon as DC } from "../class/DesignCommon"
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { JFloor } from "./JFloor";
import { JRoof } from "./JRoof";

export class JRoom extends JDesign {
    /**  */
    private _parentID: string;
    /**  */
    get parentID() { return this._parentID }
    set parentID(obj) {
        this._parentID = obj
        //this.strongUpdate()
    }

    /** cowallID */
    private _cowallIDs: string[];
    /** cowallID(cowall) */
    get cowallIDs() { return this._cowallIDs }
    set cowallIDs(obj) {
        this._cowallIDs = obj
        for (let i = 0; i < obj.length; i++) {
            let cowall: JCowall = this.dataService.cowalls[obj[i]]
            if (cowall)
                cowall.roomID = this.id
        }
        //this.strongUpdate()
    }

    /** floor */
    private _floorID: string;
    /** floor */
    get floorID() { return this._floorID }
    set floorID(obj: string) {
        this._floorID = obj
    }

    /**  */
    private _roofID: string;
    /**  */
    get roofID() { return this._roofID }
    set roofID(data) {
        this._roofID = data
    }

    /**  */
    private _roomName: string;
    /**  */
    get roomName() { return this._roomName }
    set roomName(data) { this._roomName = data }

    /**  */
    private _areaCache: number;
    /**  */
    get areaCache() { return this._areaCache }
    set areaCache(data) { this._areaCache = data }

    private _displayPos: DC.Pos;
    /**  */
    get displayPos() { return this._displayPos }
    set displayPos(data) { this._displayPos = data }

    /*  */
    private _isAutoMoveAreaPos: boolean = true
    get isAutoMoveAreaPos() { return this._isAutoMoveAreaPos }
    set isAutoMoveAreaPos(data) { this._isAutoMoveAreaPos = data }

    constructor(obj: JRoomInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService
    ) {
        super({ type: 'room', id: obj.id }, dataService)
        this.parentID = obj.parentID != undefined ? obj.parentID : null
        this.cowallIDs = obj.cowallIDs != undefined ? obj.cowallIDs : []
        this.floorID = obj.floorID != undefined ? obj.floorID : null
        this.roofID = obj.roofID != undefined ? obj.roofID : null
        this.roomName = obj.roomName != undefined ? obj.roomName : ''
        this.displayPos = obj.displayPos != undefined ? obj.displayPos : undefined
        this.isAutoMoveAreaPos = obj.isAutoMoveAreaPos != undefined ? obj.isAutoMoveAreaPos : true
        this.dataService.rooms[this.id] = this
        this.dataService.floorplan.roomsID.push(this.id)
        // if (obj.floorID != undefined)
        //     this.floorID = obj.floorID
        //this.floor = new JFloor()
    }

    /** cowall */
    getCowallArrBySort() {
        if (this.cowallIDs.length == 0)
            return []
        let cowallArr: JCowall[] = []
        let newCowall: JCowall = this.dataService.cowalls[this.cowallIDs[0]]
        cowallArr.push(newCowall)
        let startID: string = newCowall.prevID
        while (startID != newCowall.id) {
            newCowall = this.dataService.cowalls[newCowall.nextID]
            // console.log(newCowall)
            cowallArr.push(newCowall)
        }
        return cowallArr
    }

    /** cowall */
    getCowallArr() {
        let cowallArr: JCowall[] = []
        for (let i = 0; i < this.cowallIDs.length; i++) {
            cowallArr.push(this.dataService.cowalls[this.cowallIDs[i]])
        }
        return cowallArr
    }

    /** wall, */
    getWallArr() {
        let cowalls: JCowall[] = this.getCowallArrBySort()
        if (cowalls.length == 0)
            return []
        let wallArr: JWall[] = []
        let wall: JWall = null
        for (let i = 0; i < cowalls.length; i++) {
            wall = cowalls[i].getWall()
            wallArr.push(wall)
        }
        return wallArr
    }

    /** , */
    getPointArr() {
        let cowalls: JCowall[] = this.getCowallArrBySort()
        if (cowalls.length == 0)
            return []
        let pointArr: JPoint[] = []
        let point: JPoint = null
        for (let i = 0; i < cowalls.length; i++) {
            point = cowalls[i].getPointStart()
            pointArr.push(point)
        }
        return pointArr
    }

    /** , */
    getPosArr() {
        let points: JPoint[] = this.getPointArr()
        if (points.length == 0)
            return []
        let posArr: DC.Pos[] = []
        for (let i = 0; i < points.length; i++) {
            posArr.push(points[i].pos)
        }
        return posArr
    }


    /**  */
    getFloor() {
        let floor: JFloor = this.dataService.floors[this.floorID]
        return floor
    }

    /**  */
    getRoof() {
        let roof: JRoof = this.dataService.roofs[this.roofID]
        return roof
    }

    /**  */
    getInLine() {
        let cowallArr = this.getCowallArrBySort()
        cowallArr.push(cowallArr[0])
        let posArr: DC.Pos[] = []
        for (let i = 0; i < cowallArr.length - 1; i++) {
            let inLine = cowallArr[i].getInLine()
            if (!inLine)
                return []
            posArr.push(inLine[0])
            if (this.DUService.pos.isParallelRadian(cowallArr[i].getRadian(), cowallArr[i + 1].getRadian()) && cowallArr[i].getWall().thickness != cowallArr[i + 1].getWall().thickness) {
                posArr.push(inLine[1])
            }
        }
        return posArr
    }

    /** cowalls */
    resetCowallIDs(cowall: JCowall) {
        let cowallIDs: string[] = []
        let endID = cowall.id
        cowallIDs.push(cowall.id)
        let nextCowall = cowall.getNextCowall()
        while (nextCowall.id != endID) {
            cowallIDs.push(nextCowall.id)
            nextCowall = nextCowall.getNextCowall()
        }
        this.cowallIDs = cowallIDs
    }

}

export class JRoomInitType {
    id?: string;
    parentID?: string;
    cowallIDs?: string[];
    flag?: number;
    floorID?: string;
    roofID?: string;
    roomName?: string;
    displayPos?: DC.Pos;
    isAutoMoveAreaPos?: boolean;
}
