import { PopDivService } from "../../services/pop-div.service";
import { JdesignDataService } from "../../services/jdesign-data.service";
import { JBuildInitType } from "../../svg/common/jdesign/JBuild";
import { CalculatorsService } from "../../services/calculators.service";
import { JdesignUtil2dService } from "../../services/jdesign-util2d.service";
import { DomUpdateService } from "../../services/dom-update.service";

export class LeftBuildPanel {
  private _build_data
  private _initData
  constructor(
    private popService: PopDivService,
    protected dataService: JdesignDataService,
    protected calService: CalculatorsService,
    protected DUService: JdesignUtil2dService,
    protected domService: DomUpdateService
  ) {
    this.buildDataInit();
    this.popService.getLeftData.subscribe((data) => {
      this.getData(data)
    })
  }
  buildDataInit() {
    this._initData = {
      mouseleave: (e) => { },
      title: "",
      type: "room",
      importBack: {
        name: "",
        icon: "./assets/img/freeicon/back.png",
        newFunc: (e) => {
          // console.log(222)
          this.dataService.cache.shortFuncs['openBackImg'](e)
        }
      },
      drawData: [
        {
          title: "",
          data: [
            {
              name: "", icon: "./assets/img/freeicon/free.png", borderClass: false, func: (e, params) => {
                for (let i = 0; i < this._initData.drawData[0].data.length; i++) {
                  this._initData.drawData[0].data[i].borderClass = false
                }
                this._initData.drawData[0].data[0].borderClass = true;
                this.dataService.cache.shortFuncs['setDrawWall']();
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/middleWall.png", borderClass: false, func: (e, params) => {
                for (let i = 0; i < this._initData.drawData[0].data.length; i++) {
                  this._initData.drawData[0].data[i].borderClass = false
                }
                this._initData.drawData[0].data[1].borderClass = true;
                this.dataService.cache.shortFuncs["setDrawInWall"]()
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/area.png", borderClass: false, func: (e, params) => {
                for (let i = 0; i < this._initData.drawData[0].data.length; i++) {
                  this._initData.drawData[0].data[i].borderClass = false
                }
                this._initData.drawData[0].data[2].borderClass = true;
                this.dataService.cache.shortFuncs["setDrawRoom"]()
              }
            }
          ]
        },
        {
          title: "",
          data: [
            {
              name: "", icon: "./assets/img/freeicon/men.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs['addOneDoor']()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/doors.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs['addTwoDoor']()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/largeSmall.png", func: (e, params) => {
                this.dataService.cache.shortFuncs['addSonDoor']()
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/moveDoor.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs['addPullDoor']()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/wind.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs["addNormalWin"]()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/piaowindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs["addBayWin"]()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "L", icon: "./assets/img/freeicon/LWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs['createLWin']()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/scrollWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs["addCornerWin"]()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "U", icon: "./assets/img/freeicon/UWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs['createUWin']()
                  this._initData.mouseleave = () => { }
                }

              }
            },
            {
              name: "", icon: "./assets/img/freeicon/underWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  this.dataService.cache.shortFuncs["addFloorWin"]()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "()", icon: "./assets/img/freeicon/scrollWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  let data = this.DUService.obj.clone(this.dataService.cache.baseData.j2d['cornerWin'])
                  let originData = this.DUService.obj.clone(data)
                  let obj = this.calService.paramModel.createNew({
                    j2d: this.DUService.parseJ2D.getobjData(data),
                    originJ2d: this.DUService.parseJ2D.getobjData(originData),
                    absorbData: {
                      objID: undefined,
                      objType: 'point',
                      type: "pointAnd2Walls",
                      minDistance: this.dataService.cmd.floorplan.absorb.minDistance * 2, calParams: {
                        "leftThickness": "leftBoxDepth",
                        'rightThickness': "rightBoxDepth",
                        "radian": "radian"
                      },
                      realPosX: "{{x}}",
                      realPosY: "{{y}}",
                    },
                    inputData: [
                      { inputName: "", j2dName: "leftWidth" },
                      { inputName: "", j2dName: "leftThickness" },
                      { inputName: "", j2dName: "rightWidth" },
                      { inputName: "", j2dName: "rightThickness" },
                      { inputName: "", j2dName: "leftBoxThickness" },
                      { inputName: "", j2dName: "rightBoxThickness" }
                    ]
                  })
                  this.dataService.cache.common.choose = obj
                  this.setNormal()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "L()", icon: "./assets/img/freeicon/scrollWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  let data = this.DUService.obj.clone(this.dataService.cache.baseData.j2d['LWin'])
                  let originData = this.DUService.obj.clone(data)
                  let obj = this.calService.paramModel.createNew({
                    j2d: this.DUService.parseJ2D.getobjData(data),
                    originJ2d: this.DUService.parseJ2D.getobjData(originData),
                    absorbData: {
                      objID: undefined,
                      objType: 'point',
                      type: "pointAnd2Walls",
                      minDistance: this.dataService.cmd.floorplan.absorb.minDistance * 2, calParams: {
                        "leftThickness": "leftThickness",
                        'rightThickness': "rightThickness",
                        "radian": "radian"
                      },
                      realPosX: "{{x}}",
                      realPosY: "{{y}}",
                    },
                    inputData: [
                      { inputName: "", j2dName: "leftWidth" },
                      { inputName: "", j2dName: "rightWidth" }
                    ]
                  })
                  this.dataService.cache.common.choose = obj
                  this.setNormal()
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "U()", icon: "./assets/img/freeicon/scrollWindow.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  let data = this.DUService.obj.clone(this.dataService.cache.baseData.j2d['UWin'])
                  let originData = this.DUService.obj.clone(data)
                  let obj = this.calService.paramModel.createNew({
                    j2d: this.DUService.parseJ2D.getobjData(data),
                    originJ2d: this.DUService.parseJ2D.getobjData(originData),
                    absorbData: {
                      objID: undefined,
                      objType: 'wall',
                      type: "wallAnd2walls",
                      minDistance: this.dataService.cmd.floorplan.absorb.minDistance * 2, calParams: {
                        "centerThickness": "centerThickness",
                        'centerWidth': "centerWidth",
                        "leftThickness": "leftThickness",
                        "leftRadian": "leftRadian",
                        'rightThickness': "rightThickness",
                        "rightRadian": "rightRadian"

                      },
                      realPosX: "{{x}}",
                      realPosY: "{{y}}",
                    },
                    inputData: [
                      { inputName: "", j2dName: "leftWidth" },
                      { inputName: "", j2dName: "rightWidth" }
                    ]
                  })
                  this.dataService.cache.common.choose = obj
                  this.setNormal()
                  this._initData.mouseleave = () => { }
                }
              }
            }
          ]
        },
        {
          title: "",
          data: [
            {
              name: "", icon: "./assets/img/freeicon/zhu.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  let obj: JBuildInitType = {
                    size: { width: 500, height: 500, height3D: 2800 },
                    floorHeight: 0,
                    url2D: "./assets/img/test/zhuzi.png"
                  }
                  this.dataService.cache.shortFuncs["addBuild"](obj)
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/frog.png", func: (e, params) => {
                this._initData.mouseleave = (e) => {
                  let obj: JBuildInitType = {
                    size: { width: 500, height: 500, height3D: 2800 },
                    floorHeight: 0,
                    url2D: "./assets/img/test/yandao.jpg"
                  }
                  this.dataService.cache.shortFuncs["addBuild"](obj)
                  this._initData.mouseleave = () => { }
                }
              }
            },
            {
              name: "", icon: "./assets/img/freeicon/doorDong.png", func: (e, params) => {
                this.dataService.cache.shortFuncs["addNoDoor"]()
              }
            }
          ]
        }
      ]
    }
    this.popService.clickLeftData.emit(this._initData);
    this.dataService.cache.hotKeys.push({
      keys: ["r"],
      downFunc: () => {
        for (let i = 0; i < this._initData.drawData[0].data.length; i++) {
          this._initData.drawData[0].data[i].borderClass = false
        }
        this._initData.drawData[0].data[2].borderClass = true;
        this.dataService.cache.shortFuncs["setDrawRoom"]();
      },
    })
    this.dataService.cache.hotKeys.push({
      keys: ["l"],
      downFunc: () => {
        for (let i = 0; i < this._initData.drawData[0].data.length; i++) {
          this._initData.drawData[0].data[i].borderClass = false
        }
        this._initData.drawData[0].data[1].borderClass = true;
        this.dataService.cache.shortFuncs["setDrawInWall"]();
      },
    })
    this.dataService.cache.hotKeys.push({
      keys: ["Control", "l"],
      downFunc: () => {
        for (let i = 0; i < this._initData.drawData[0].data.length; i++) {
          this._initData.drawData[0].data[i].borderClass = false
        }
        this._initData.drawData[0].data[0].borderClass = true;
        this.dataService.cache.shortFuncs["setDrawWall"]();
      },
    })
  }

  getData(data) {
    if (data != null) {
      if (data.type == 'room') {
        this._build_data = this._initData;
      }
      else if (data.type == 'matter') {
        this._build_data = data
      }
      else if (data.type == 'mine') {
        this._build_data = {
          type: 'mine'
        }
      }
      this.popService.clickLeftData.emit(this._build_data);
    }
  }

  setNormal() {
    this.dataService.cache.common.mode = 'normal'
    this.dataService.cache.common.blockON = true
    this.dataService.cache.common.firstPos = null
    this.dataService.cmd.cowall.hide = true
    this.domUpdate()
  }

  domUpdate() {
    this.domService.allSvg.emit()
    this.popService.inputBoxs.emit([])
    this.popService.buttons.emit(null)
  }
}