import { PopDivService } from "../../services/pop-div.service";
import { JdesignDataService } from "../../services/jdesign-data.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { JDWAbstract } from "../../svg/common/jdesign/JDWAbstract";
import { JDesign } from "../../svg/common/jdesign/JDesign";

export class DWShortFunc {
    constructor(
        protected popService: PopDivService,
        protected dataService: JdesignDataService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService
    ) {
        /*  */
        this.dataService.cache.shortFuncs['delDW'] = (obj) => {
            this.delDW(obj)
        }
        /*  */
        this.dataService.cache.shortFuncs["isDW"] = (obj: JDesign) => {
            if (obj.type == "door" || obj.type == "win")
                return true
            return false
        }
        /*  */

    }

    /*  */
    delDW(DW: JDWAbstract) {
        // DW.wallID = null
        if (DW.type == 'door') {
            let door = this.dataService.doors[DW.id]
            this.calService.door.delWall(door)
            this.calService.door.del(door)
        }
        else if (DW.type == 'win') {
            let win = this.dataService.wins[DW.id]
            this.calService.win.delWall(win)
            this.calService.win.del(win)
        }
        this.dataService.cache.common.choose = this.dataService.floorplan
        this.popService.inputBoxs.emit([])
        this.popService.buttons.emit(null)
        this.domService.allSvg.emit()
    }

}