import { JdesignDataService } from "../../services/jdesign-data.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { JWall } from "../../svg/common/jdesign/JWall";
import { PopDivService } from "../../services/pop-div.service";

export class WallShortFunc {
    constructor(
        protected dataService: JdesignDataService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService,
        protected popService: PopDivService
    ) {
        /*  */
        this.dataService.cache.shortFuncs["cutWall"] = (wall: JWall) => {
            this.cutWall(wall)
        }
        /*  */
        this.dataService.cache.shortFuncs["delWall"] = (wall: JWall) => {
            this.delWall(wall)
        }
        /*  */
        this.dataService.cache.shortFuncs["transArcWall"] = (wall: JWall) => {
            this.transArcWall(wall)
        }
        /*  */
        this.dataService.cache.shortFuncs["isWall"] = (wall: JWall) => {
            return wall.type == 'wall'
        }
        /*  */
        this.dataService.cache.shortFuncs["isArcWall"] = (wall: JWall) => {
            return wall.arcDistance != 0
        }
        /*  */
        this.dataService.cache.shortFuncs["isStraightWall"] = (wall: JWall) => {
            return wall.arcDistance == 0
        }
        /* */
        this.dataService.cache.shortFuncs['getWallType'] = (obj: JWall) => {
            return obj.wallType
        }
        /*  */
        this.dataService.cache.shortFuncs['setWallType'] = (obj: JWall, num: number) => {
            obj.wallType = num
            this.domService.allSvg.emit()
        }
        /*  */
        this.dataService.cache.shortFuncs['fixWallThickness'] = (obj: JWall, thickness: number) => {
            obj.thickness = thickness
            this.dataService.cache.floorplan.dataEmit.emit()
            // this.domService.svgWall.emit()
            // this.domService.svgCowall.emit()
            this.domService.allSvg.emit()
            this.dataService.cache.shortFuncs['setWallInput'](obj)
            this.dataService.cache.cowall.update3D.emit()
        }

        /*  */
        this.dataService.cache.shortFuncs['getWallNearLen'] = (obj: JWall) => {
            let points = obj.getPointArr()
            let len = (points[0].getWallArr().length - 1) + (points[1].getWallArr().length - 1)
            return len
        }

    }

    /*  */
    delWall(wall: JWall) {
        this.dataService.cache.common.choose = this.dataService.floorplan
        this.calService.createRoom.createRoomByDelWall(wall)
        this.dataService.cache.floorplan.dataEmit.emit()
        this.initData()
    }

    /**  */
    cutWall(wall: JWall) {
        try {
            this.calService.wall.cut.cutByPer(wall, 0.5)
        }
        catch (e) {
            console.log(e)
        }
        this.dataService.cache.floorplan.dataEmit.emit()
        this.domService.allSvg.emit()
        this.dataService.cache.cowall.update3D.emit()
    }

    /*  */
    transArcWall(wall: JWall) {
        if (wall.arcDistance == 0) {
            let distance = wall.getDistance()
            distance = distance / 3
            wall.transArc(distance)
            // this.calService.wall.transArcWall(wall, distance)
        }
        else {
            wall.arcDistance = 0
        }
        this.dataService.cache.floorplan.dataEmit.emit()
        this.initData()
    }

    initData() {
        // this.domService.svgPoint.emit()
        // this.domService.svgWall.emit()
        // this.domService.svgCowall.emit()
        // this.domService.svgRoom.emit()
        this.domService.allSvg.emit()
        this.popService.inputBoxs.emit([])
        this.popService.buttons.emit(undefined)
        this.dataService.cache.wall.update3D.emit()
        this.dataService.cache.cowall.update3D.emit()
    }

}