import { PopDivService } from "../../services/pop-div.service";
import { JdesignDataService } from "../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { BackShortFunc } from "./back.shortFunc";
import { CornWinShortFunc } from "./cornWin.shortFunc";
import { DoorShortFunc } from "./door.shortFunc";
import { WinShortFunc } from "./win.shortFunc";
import { DWShortFunc } from "./DW.shortFunc";
import { FurnitureShortFunc } from "./furniture.shortFunc";
import { PointShortFunc } from "./point.shortFunc";
import { RoomShortFunc } from "./room.shortFunc";
import { WallShortFunc } from "./wall.shortFunc";
import { CommonShortFunc } from "./common.shortFunc";
import { CameraShortFuncs } from "./camera.shortFuncs";
import { BuildShortFunc } from "./build.shortFunc";
import { LUWinShortFunc } from "./LUWin.shortFunc";


export class DesignShortFunc {
    constructor(
        protected popService: PopDivService,
        protected dataService: JdesignDataService,
        protected DUService: JdesignUtil2dService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService
    ) {
        let back = new BackShortFunc(popService, dataService, calService, domService)
        let cornWin = new CornWinShortFunc(dataService, calService, domService, popService)
        let door = new DoorShortFunc(dataService, calService, domService, popService)
        let win = new WinShortFunc(dataService, calService, domService, popService)
        let DW = new DWShortFunc(popService, dataService, calService, domService)
        let furniture = new FurnitureShortFunc(dataService, calService, domService, popService)
        let point = new PointShortFunc(dataService, DUService, calService, domService)
        let room = new RoomShortFunc(dataService, calService, domService)
        let wall = new WallShortFunc(dataService, calService, domService, popService)
        let common = new CommonShortFunc(dataService, calService, domService, popService)
        let camera = new CameraShortFuncs(dataService, domService, DUService)
        let build = new BuildShortFunc(dataService, calService, domService, popService)
        let LWin = new LUWinShortFunc(dataService, calService, domService, popService, DUService)
    }
}