import { DesignCommon as DC } from '../../../class/DesignCommon'
import { EventAbstract } from '../EventAbstract';
import { MouseEventService } from '../../../services/mouse-event.service';
import { JdesignDataService } from '../../../../../services/jdesign-data.service';
import { DomUpdateService } from '../../../../../services/dom-update.service';
import { JdesignUtil2dService } from '../../../../../services/jdesign-util2d.service';
import { PopDivService } from '../../../../../services/pop-div.service';
import { CalculatorsService } from '../../../../../services/calculators.service';
import { JParamsModel } from '../../../jdesign/JParamsModel';
import { SvgMoveParamsModelCal } from './svgMoveParamsModelCal';
import { SvgParamsMoveParamsModelType } from './SvgParamsMoveParamsModelType';


/*  */
export class SvgMoveParamsModel extends EventAbstract {
    private _distancePos: DC.Pos = { x: 0, y: 0 }
    private _pos: DC.Pos = null
    private _cal = new SvgMoveParamsModelCal(this.dataService, this.domService, this.DUService, this.popService, this.calService)
    private _paramsMoveType = new SvgParamsMoveParamsModelType(this.dataService, this.domService, this.DUService, this.popService, this.calService)
    private _prevPos: DC.Pos = undefined
    constructor(
        public mouseEventService: MouseEventService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService,
        private DUService: JdesignUtil2dService,
        private popService: PopDivService,
        private calService: CalculatorsService
    ) {
        super(mouseEventService)
    }

    isCondition() {
        return this.dataService.cache.common.mode == 'normal' && this.dataService.cache.common.choose != null && this.dataService.cache.common.choose.type == "paramsModel"
    }


    mouseDown(e) {

    }

    mouseUp(e) {
        this._pos = undefined
        this._prevPos = undefined
        this._distancePos = { x: 0, y: 0 }
        this.dataService.cache.common.choose = this.dataService.cache.common.choose
        this.dataService.cache.common.blockON = false
        this.dataService.cache.floorplan.createAndMove = false
        this.dataService.cache.common.blockON = false
        this.dataService.cache.common.firstPos = null
        this.domService.allSvg.emit()
        this.calService.undoRedo.save()
    }

    mouseMove(e) {
        let originPos = { x: e.offsetX, y: e.offsetY }
        let obj = <JParamsModel>this.dataService.cache.common.choose
        if (this.dataService.cache.common.firstPos == null) {
            this._pos = {
                x: originPos.x + this._distancePos.x,
                y: originPos.y + this._distancePos.y
            }
            if (obj.mode != 'paramsMove') {
                this.transMove(obj, this._pos, originPos)
            }
            else {
                this.parmasMove(obj, this._pos, originPos)
            }
            this.domService.allSvg.emit()
            this._prevPos = { x: originPos.x, y: originPos.y }
        }
        /** , */
        else {
            this._distancePos = {
                x: obj.pos.x - originPos.x,
                y: obj.pos.y - originPos.y
            }
            this.dataService.cache.common.firstPos = null
        }
    }

    /*  */
    transMove(obj: JParamsModel, transPos: DC.Pos, originPos: DC.Pos) {
        switch (obj.absorbData.type) {
            case "pointAnd2Walls":
                this._cal.pointAnd2Walls(obj, transPos)
                break;
            case "wallAnd2walls":
                this._cal.wallAnd2walls(obj, transPos)
                break;
            default:
                this._cal.defaultMove(obj, transPos)
                break
        }
    }

    /*  */
    parmasMove(obj: JParamsModel, transPos: DC.Pos, originPos: DC.Pos) {
        if (this._prevPos) {
            switch (obj.movePointData.type) {
                case "linearMove":
                    this._paramsMoveType.linerMove(obj, originPos, this._prevPos)
                    break;
            }
        }

    }


}