import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { DomUpdateService } from '../../../services/dom-update.service';
import { RightMenuService } from '../../../services/right-menu.service';

@Component({
  selector: '[points]',
  templateUrl: './points.component.html',
  styleUrls: ['./points.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PointsComponent implements OnInit {

  constructor(
    private DUService: JdesignUtil2dService,
    private dataService: JdesignDataService,
    private domUpdateService: DomUpdateService,
    private ref: ChangeDetectorRef
  ) {
    this.domUpdateService.svgPoint.subscribe(() => {
      this.ref.detectChanges()
    })
  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('points is ok!!')
    }

  }

  getPointArr() {
    return this.DUService.cal.getStringArrByNum(this.dataService.points)
  }


}
