import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { DesignCommon as DC } from '../../../common/class/DesignCommon'
import { JdesignDataService } from '../../../../services/jdesign-data.service';
import { JdesignUtil2dService } from '../../../../services/jdesign-util2d.service';

@Component({
  selector: '[labelLines]',
  templateUrl: './label-lines.component.html',
  styleUrls: ['./label-lines.component.scss']
})
export class LabelLinesComponent implements OnInit {

  private _data: LabelType
  private _transformMsg: TransformMsg = null
  domDatas: DomType[] = []
  defualtDasharray: string = '50,50'
  moveTransfrom: string = this.DUService.pos.getTransformStringByRM({})
  rotateTransform: string = this.DUService.pos.getTransformStringByRM({})

  @Input() set labelLines(data) {
    this._data = data
    this.domDatas = []
    if (data) {
      this.moveTransfrom = this.DUService.pos.getTransformStringByRM(data.movePos ? { movePos: data.movePos } : {})
      this.rotateTransform = this.DUService.pos.getTransformStringByRM(data.moveRadian ? { radian: data.moveRadian } : {})
      for (let i = 0; i < this._data.data.length; i++) {
        this.domDatas.push(this.getDomData(this._data.data[i]))
      }
    }

  }
  get labelLines() { return this._data }

  @Output() labelData = new EventEmitter();

  constructor(private DUService: JdesignUtil2dService) { }

  ngOnInit() {
  }

  /* html */
  getDomData(lineData: LineType): DomType {
    let textTransform = this.getTextTransform(lineData.line, lineData.radian)
    let a = this.isShow(lineData.line)
    if (!a)
      return undefined
    let data: DomType = {
      path: this.getPath(lineData.line),
      textTransform: lineData.text ? textTransform : undefined,
      transformStart: this.labelLines.img ? this.getTransformStart(lineData.line) : undefined,
      transformEnd: this.labelLines.img ? this.getTransformEnd(lineData.line) : undefined,
      text: lineData.text,
      auxiliaryLines: lineData.auxiliary ? this.getauxiliarypaths(lineData.auxiliary) : [],
      dasharray: lineData.dasharray ? lineData.dasharray : this.defualtDasharray
    }
    return data
  }

  /* paths */
  getauxiliarypaths(lines: DC.Pos[][]) {
    let paths: string[] = []
    for (let i = 0; i < lines.length; i++) {
      paths.push(this.DUService.svg.getPath(false, ...lines[i]))
    }
    return paths
  }

  /** path */
  getPath(line: DC.Pos[]) {
    let newPosArr: DC.Pos[] = []
    for (let i = 0; i < line.length; i++) {
      newPosArr.push({
        x: line[i].x,
        y: line[i].y
      })
    }
    let path: string = this.DUService.svg.getPath(false, ...newPosArr)

    /** transform */
    this.setTransformMsg(newPosArr)

    return path
  }

  /** transfromMsg */
  setTransformMsg(line: DC.Pos[]) {
    let radian: number = this.DUService.pos.getRadian(line[0], line[1])
    let sinA = Math.sin(radian)
    let cosA = Math.cos(radian)
    this._transformMsg = {
      radian: radian,
      sin: sinA,
      cos: cosA
    }
  }

  /** transform */
  getTransformStart(line: DC.Pos[]) {
    let transform = this.DUService.pos.getTransfromString(
      this._transformMsg.cos, this._transformMsg.sin, -this._transformMsg.sin, this._transformMsg.cos, line[0].x , line[0].y )
    return transform
  }

  /** transform */
  getTransformEnd(line: DC.Pos[]) {
    let transform = this.DUService.pos.getTransfromString(
      this._transformMsg.cos, this._transformMsg.sin, -this._transformMsg.sin, this._transformMsg.cos, line[1].x , line[1].y)
    return transform
  }

  /**  */
  getTextTransform(line: DC.Pos[], radian?: number) {
    let cosA = radian ? Math.cos(radian) : 1
    let sinA = radian ? Math.sin(radian) : 0
    let centerPos: DC.Pos = this.DUService.pos.getCenterPos(line[0], line[1])
    let transform = this.DUService.pos.getTransfromString(cosA, sinA, -sinA, cosA, centerPos.x , centerPos.y)

    this.labelData.emit({
      pos: centerPos
    })

    return transform
  }

  /**  */
  isShow(line: DC.Pos[]) {
    return !this.DUService.pos.isSamePos(line[0], line[1])
  }

  /* class */
  getTextClass() {
    let className = ''
    if (!this.labelLines.isRemoveStyle)
      className = "label_text font_size text_shadow "
    if (this.labelLines.className != undefined) {
      className += this.labelLines.className
    }
    return className
  }

  getTextStyle() {
    let style = []
    if (this.labelLines.fontSize != undefined) {
      style['font-size'] = this.labelLines.fontSize + 'px'
      style['font-weight'] = 0
    }
    return style
  }


}

class LineType {
  line: DC.Pos[];
  /*  */
  text?: string;
  /*  */
  radian?: number;
  /* ===== */
  /*  */
  auxiliary?: DC.Pos[][];
  /*  */
  dasharray?: string;
}

class LabelType {
  img?: string;
  imgWidth?: number;
  imgHeight?: number;
  data: LineType[]
  movePos?: DC.Pos;
  moveRadian?: number;
  lineThickness: number;
  className?: string;
  fontSize?: number;
  fontWeight?: number;
  isRemoveStyle?: boolean;
}

class DomType {
  path: string;
  textTransform: string;
  transformStart: string;
  transformEnd: string;
  text: string;
  auxiliaryLines: string[];
  dasharray: string;
}

class TransformMsg {
  radian: number;
  cos: number;
  sin: number;
}
