import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { DomUpdateService } from '../../../services/dom-update.service';

@Component({
  selector: '[geometrys]',
  templateUrl: './geometrys.component.html',
  styleUrls: ['./geometrys.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GeometrysComponent implements OnInit {

  constructor(
    private DUService: JdesignUtil2dService,
    public dataService: JdesignDataService,
    private domService: DomUpdateService,
    private ref: ChangeDetectorRef
  ) {
    this.domService.svgGeometry.subscribe(() => {
      this.ref.detectChanges()
    })
  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('geometrys is ok!!')
    }
  }

  /**  */
  getData() {
    let furnitures = this.dataService.doFurnitures().arr()
    let builds = this.dataService.doBuilds().arr()
    let arr = [...furnitures, ...builds]
    return arr
  }

}
