import { JLUWin3D } from "../../../three/jDesign3D/JLUWin3D";
import { JWall } from "./JWall";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JLUWinTypeInit } from "./JLUWin";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";

export class JUWin extends JLUWin3D {

    constructor(
        obj: JUWinTypeInit,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService,
    ) {
        super("UWin", obj, dataService, DUService)
        this.centerWallID = obj.centerWallID ? obj.centerWallID : undefined
        this.centerRadian = obj.centerRadian ? obj.centerRadian : 0
        this.dataService.UWins[this.id] = this
    }

    private _centerWallID: string
    /** id */
    get centerWallID() { return this._centerWallID }
    set centerWallID(data) { this._centerWallID = data }

    private _centerRadian: number
    /*  */
    get centerRadian() { return this._centerRadian }
    set centerRadian(data) { this._centerRadian = data }


    /**  */
    getCenterWall(): JWall {
        if (this.centerWallID)
            return this.dataService.walls[this.centerWallID]
        return undefined
    }

    get leftStartPoint() {
        let wall = this.getCenterWall()
        if (!wall)
            return undefined
        return wall.getPoint(0)
    }

    get rightStartPoint() {
        let wall = this.getCenterWall()
        if (!wall)
            return undefined
        return wall.getPoint(1)
    }

    getLeftWall() {
        let wall = this.getCenterWall()
        if (wall) {
            let cowall = wall.getCowall()
            let prevCowall = cowall.getPrevCowall()
            let prevWall = prevCowall.getWall()
            return prevWall
        }
        return undefined
    }

    getRightWall() {
        let wall = this.getCenterWall()
        if (wall) {
            let cowall = wall.getCowall()
            let nextCowall = cowall.getNextCowall()
            let nextWall = nextCowall.getWall()
            return nextWall
        }
        return undefined
    }

    /**  */
    getLeftRadian(): number {
        let point = this.leftStartPoint
        if (!point)
            return -Math.PI / 2
        let wall = this.getLeftWall()
        if (!wall)
            return -Math.PI / 2
        return this.getRadian(wall, point)
    }
    /**  */
    getRightRadian(): number {
        let point = this.rightStartPoint
        if (!point)
            return -Math.PI / 2
        let wall = this.getRightWall()
        if (!wall)
            return -Math.PI / 2
        return this.getRadian(wall, point)
    }

    getCenterLine(distance: number) {
        let rayPos = this.DUService.pos.getRayPos(this.pos, this.centerRadian, distance)
        return [{ x: this.pos.x, y: this.pos.y }, rayPos]
    }

    isAbsorb() {
        let wall = this.getCenterWall()
        return wall ? true : false
    }

}


export class JUWinTypeInit extends JLUWinTypeInit {
    centerWallID?: string;
    centerRadian?: number;
}