import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JPoint, JPointInitType } from "../jdesign/JPoint";
import { JWall } from "../jdesign/JWall";

/**  */
export class PosCal {

    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService
    ) {

    }

    /**  */
    getPosToPointMin(pos: DC.Pos, points: JPoint[]) {
        let minDistance: number = null
        let distance: number = null
        let minPoint: JPoint = null
        let key: string = null

        for (key in points) {

            distance = this.DUService.pos.getDistance(pos, points[key].pos)
            if (minDistance == null || minDistance > distance) {
                minDistance = distance
                minPoint = points[key]
            }
        }
        return {
            distance: minDistance,
            point: minPoint
        }
    }

    /**  */
    getPosToPointRadian(pos: DC.Pos, points: JPoint[]): { radian: number, point: JPoint }[] {
        let datas: { radian: number, point: JPoint }[] = []
        let radian: number = null

        let key: string = null

        for (key in points) {


            radian = this.DUService.pos.getRadian(pos, points[key].pos)
            datas.push({ radian: radian, point: points[key] })
        }
        return datas
    }

    /**  */
    getPosToPointRadianMin(pos: DC.Pos, radians: number[], points: JPoint[]) {
        let datas = this.getPosToPointRadian(pos, points)
        let distance: number = null
        let minInt: number = null
        let minDistance: number = null
        for (let i = 0; i < datas.length; i++) {
            for (let j = 0; j < radians.length; j++) {
                distance = Math.abs(radians[j] - datas[i].radian)
                if (minDistance == null || minDistance > distance) {
                    minInt = i
                    minDistance = distance
                }
            }
        }

        if (minInt == null)
            return null
        return datas[minInt]


    }

    /**
     * ()
     * @param posStart 
     * @param posEnd 
     * @param radian ()
     */
    getMoveSideDistance(posStart: DC.Pos, posEnd: DC.Pos, radian: number) {
        let vector = { x: posEnd.x - posStart.x, y: posEnd.y - posStart.y }
        let leftRayPos = this.DUService.pos.getRayPos({ x: 0, y: 0 }, radian, 10)
        let a = this.DUService.pos.vector2D(vector).Multiply(leftRayPos)
        let distance: number = this.DUService.pos.vector2D(vector).projection(leftRayPos).length()
        /**  */
        if (a < 0) {
            distance *= -1
        }
        return distance
    }

    /**  */
    getPosToPointHorizontal(pos: DC.Pos, points: JPoint[]) {
        return this.getPosToPointRadianMin(pos, [0, Math.PI, -Math.PI], points)
    }

    /**  */
    getPosToPointVertical(pos: DC.Pos, points: JPoint[]) {
        return this.getPosToPointRadianMin(pos, [Math.PI / 2, Math.PI * 1.5, -Math.PI / 2, -Math.PI * 1.5], points)
    }

    /**  */
    getPosRightAngleToWall(pos: DC.Pos, walls: JWall[]) {

        let sameInt: number = null
        let minDistance: number = null
        let minWall: JWall = null
        let distance: number = null
        let linePosArr: DC.Pos[] = []
        let key: string = null

        for (key in walls) {

            linePosArr = walls[key].getPosArr()
            if (!this.DUService.pos.isSamePos(linePosArr[0], linePosArr[1])) {
                distance = this.DUService.pos.getRightAngleDistance(pos, linePosArr[0], linePosArr[1])
                if (minDistance == null || distance < minDistance) {
                    minDistance = distance
                    minWall = walls[key]
                }
            }
        }

        return {
            wall: minWall,
            distance: minDistance,
        }
    }

    /**  */
    getPosToWall(pos: DC.Pos, walls: JWall[]) {

        let sameInt: number = null
        let minDistance: number = null
        let minWall: JWall = null
        let distance: number = null
        let interectPos: DC.Pos = null
        let linePosArr: DC.Pos[] = []
        let key: string = null

        for (key in walls) {

            linePosArr = walls[key].getPosArr()
            if (!this.DUService.pos.isSamePos(linePosArr[0], linePosArr[1])) {
                interectPos = this.DUService.pos.getPointToLinePos(pos, linePosArr[0], linePosArr[1])
                distance = this.DUService.pos.getDistance(pos, interectPos)
                if (minDistance == null || distance < minDistance) {
                    minDistance = distance
                    minWall = walls[key]
                }
            }
        }

        return {
            wall: minWall,
            distance: minDistance,
        }
    }


}