    /**
     * =============================
     * @param _value 
     * @param _min 
     * @param _max 
     */
    public static Clamp(_value, _min, _max) {
        let _returnValue = Math.max(_min, _value);
        _returnValue = Math.min(_max, _returnValue);
        return _returnValue;
    }

    /**
     * vector2vector3
     * @param _array1 vector2vector31
     * @param _array2 vector2vector32
     * @param properties vector2["x","y"]["width","height"]
     */
    public static IfArrayTheSame(_array1, _array2, properties) {
        if (_array1.length != _array2.length) {
            return false;
        } else {
            let returnValue = true;
            for (let i = 0; i < _array1.length; i++) {
                for (let j = 0; j < properties.length; j++) {
                    if (_array1[i][properties[j]] != _array2[i][properties[j]]) {
                        returnValue = false;
                        break;
                    }
                }
            }
            return returnValue
        }
    }

