import { JdesignDataService } from "../../services/jdesign-data.service";
import { RightMenuService } from "../../services/right-menu.service";
import { PopDivService } from "../../services/pop-div.service";
import { DWRightMenu } from "./DW.rightMenu";
import { JWin } from "../../svg/common/jdesign/JWin";

export class WinRightMenu extends DWRightMenu {
    constructor(
        protected dataService: JdesignDataService,
        protected popService: PopDivService,
        protected rightService: RightMenuService,
    ) {
        super( dataService, popService, rightService)
        this.rightService.win.subscribe((obj: { e: any, win?: JWin }) => {
            if (obj != null)
                return this.winMenu(obj.e, obj.win)
            return this.winMenu(null)
        })
    }

    /**  */
    winMenu(e: MouseEvent, win?: JWin) {
        if (e == null) {
            this.popService.rightClick.emit(null)
            return false
        }

        let data = {
            position: {
                x: e.clientX,
                y: e.clientY
            },
            body: [
            ]
        }
        data.body.push(...this.getMenu(e, win))
        this.popService.rightClick.emit(data)
        return true
    }
}