import { PopDivService } from "../../../services/pop-div.service";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JRoof3D } from "../../../three/jDesign3D/JRoof3D";
import { JDoor3D } from "../../../three/jDesign3D/JDoor3D";
import { DomUpdateService } from "../../../services/dom-update.service";

export class DoorPanel {

    constructor(
        private popService: PopDivService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService
    ) {
        popService.panelRight.subscribe((data) => {
            // console.log(data)
            if (data == null) {
                this.popService.panelRightData.emit(null)
            }
            else {
                if (data.type == 'door') {
                    this.setDoorData(data)
                }
            }
        })
    }
    setDoorData(obj: JDoor3D) {
        let data = [
            {
                type: "input",
                data: [
                    {
                        name: "",
                        isEditable: false,
                        value: obj.size.width,
                        func: (e, params) => {
                            obj.size.width = parseInt(params.value)
                            this.dataService.cache.doorWin.move(obj, obj.pos)
                            this.popService.inputBoxs.emit(null)
                            this.dataService.cache.doorWin.displayLabel = false
                            this.domService.allSvg.emit()
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "",
                        isEditable: false,
                        value: obj.size.height3D,
                        func: (e, params) => {
                            obj.size.height3D = parseInt(params.value)
                            this.dataService.cache.doorWin.move(obj, obj.pos)
                            this.popService.inputBoxs.emit(null)
                            this.dataService.cache.doorWin.displayLabel = false
                            this.domService.allSvg.emit()
                            this.dataService.cache.shortFuncs['saveAction']()
                        }
                    },
                    {
                        name: "",
                        isEditable: false,
                        value: obj.floorHeight,
                        func: (e, params) => {
                            obj.floorHeight = parseInt(params.value)
                            this.dataService.cache.doorWin.move(obj, obj.pos)
                            this.popService.inputBoxs.emit(null)
                            this.dataService.cache.doorWin.displayLabel = false
                            this.domService.allSvg.emit()
                            console.log(obj.floorHeight)
                            this.dataService.cache.shortFuncs['saveAction']()
                            // this.dataService.cache.shortFuncs['saveAction']()
                        }
                    }
                ]
            }
        ]
        this.popService.panelRightData.emit(data)
    }

    private callBackRoofImgArr(obj: JRoof3D, arr) {
        let argument = {};
        let imgArr = []
        for (let i = 0; i < arr.length; i++) {
            if (i == obj.GetMaterialSelectIndex()) {
                argument = {
                    path: arr[i],
                    details: '',
                    borderClass: true,
                    func: (e, params) => {
                        obj.SetModelTexture(params.path);
                        obj.SetMaterialSelectIndex(i)
                    }
                }
            }
            else {
                argument = {
                    path: arr[i],
                    details: '',
                    func: (e, params) => {
                        obj.SetModelTexture(params.path);
                        obj.SetMaterialSelectIndex(i)
                    }
                }
            }
            imgArr.push(argument);
        }
        return imgArr;
    }


}