import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { JLWinTypeInit, JLWin } from "../jdesign/JLWin";
import { JLWin3D } from "../../../three/jDesign3D/JLWin3D";
import { JPoint } from "../jdesign/JPoint";

export class LWinCal {
    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService,
    ) {

    }

    createNew(obj: JLWinTypeInit) {
        let LWin = new JLWin3D(obj, this.dataService, this.DUService)
        this.dataService.cache.LWin.createFunc(LWin)
        return LWin
    }

    /**  */
    unionPoint(obj: JLWin, point: JPoint) {
        obj.pointID = point.id
        obj.pos = point.pos
    }

    /**  */
    disconnectPoint(obj: JLWin) {
        obj.pointID = undefined
    }

    /*  */
    getLeftAndRightWall(obj: JLWin) {
        let point = obj.getPoint()
        if (!point)
            return undefined
        let walls = point.getWallArr()
        let radianA = walls[0].getRadian()
        let radianB = walls[1].getRadian()
        radianA = this.calService.point.getWallOrCowallRadianByPoint(point, walls[0])
        radianB = this.calService.point.getWallOrCowallRadianByPoint(point, walls[1])
        let newRadian = radianA - radianB
        newRadian = this.DUService.pos.getAbsRadian(newRadian, false)
        if (newRadian >= 0) {
            return [walls[0], walls[1]]
        }
        else {
            return [walls[1], walls[0]]
        }
    }

    /*  */
    listAbosrb() {
        for (let key in this.dataService.LWins) {
            let LWin = this.dataService.LWins[key]
            let point = LWin.getPoint()
            if (point) {
                let walls = point.getWallArr()
                if (walls.length != 2 || walls[0].arcDistance != 0 || walls[0].arcDistance != 0) {
                    this.disconnectPoint(LWin)
                }
                else {
                    this.unionPoint(LWin, point)
                }
            }
            else {
                this.disconnectPoint(LWin)
            }
        }
    }
}