import { JdesignDataService } from "../../../services/jdesign-data.service";
import { EventEmitter } from '@angular/core';

export abstract class JDesign {
    /** ID */
    private _id: string;
    /** ID */
    get id() { return this._id }
    set id(obj) { this._id = obj }

    /**  */
    private _flag: number;
    /**  */
    get flag() { return this._flag }
    set flag(obj: number) { this._flag = obj }

    /**  */
    private _calFlag: number;
    /**  */
    get calFlag() { return this._calFlag }
    set calFlag(data) { this._calFlag = data }

    /**  */
    private _type: string;
    /**  */
    get type() { return this._type }
    set type(obj: string) { this._type = obj }

    /**  */
    private _mode: string
    /**  */
    get mode() { return this._mode }
    set mode(obj: string) {
        this._mode = obj
    }

    /**  */
    private _updataArr: Function[] = [];
    /**  */
    get updateArr() { return this._updataArr }
    set updateArr(obj: Function[]) { this._updataArr = obj }

    /** 3D */
    private _updateArr3D: Function[] = [];
    /** 3D */
    get updateArr3D() { return this._updateArr3D }
    set updateArr3D(obj: Function[]) { this._updateArr3D = obj }

    /**  */
    private _choose: boolean = false;
    /**  */
    get choose() { return this._choose }
    set choose(obj: boolean) { this._choose = obj }

    /**  */
    private _chooseFuncArr: Function[] = [];
    /**  */
    get chooseFuncArr() { return this._chooseFuncArr }
    set chooseFuncArr(obj: Function[]) { this._chooseFuncArr = obj }

    /**  */
    private _lateChooseFuncArr: Function[] = [];
    /**  */
    get lateChooseFuncArr() { return this._lateChooseFuncArr }
    set lateChooseFuncArr(data) { this._lateChooseFuncArr = data }

    /**  */
    private _noChooseFuncArr: Function[] = [];
    /**  */
    get noChooseFuncArr() { return this._noChooseFuncArr }
    set noChooseFuncArr(obj: Function[]) { this._noChooseFuncArr = obj }

    /**  */
    private _lateNoChooseFuncArr: Function[] = [];
    /**  */
    get lateNoChooseFuncArr() { return this._lateNoChooseFuncArr }
    set lateNoChooseFuncArr(data) { this._lateNoChooseFuncArr = data }

    private _urgentUpdate: EventEmitter<object> = new EventEmitter<object>()
    /**  */
    get urgentUpdate() { return this._urgentUpdate }
    set urgentUpdate(data) { this._urgentUpdate = data }

    /** dom */
    private _domMsg: any
    /** dom */
    get domMsg() { return this._domMsg }
    set domMsg(obj: any) {
        this._domMsg = obj
    }

    /**  */
    private _destoryFuncArr: any[] = []
    get destoryFuncArr() { return this._destoryFuncArr }
    set destoryFuncArr(obj: any[]) { this._destoryFuncArr = obj }

    /** 3d */
    private _act3D: { start: () => void, move: () => void, end: () => void, change: () => void } = {
        start: () => { }, move: () => { }, end: () => { }, change: () => { }
    }
    get act3D() { return this._act3D }
    set act3D(data) { this._act3D = data }


    constructor(obj: JDesignType, public dataService: JdesignDataService) {
        this.type = obj.type
        this.flag = obj.flag ? obj.flag : 0
        this.id = obj.id ? obj.id : obj.type + this.dataService.cmd.floorplan
            .nextID
        this.mode = obj.mode ? obj.mode : null
        this.dataService.cmd.floorplan.nextID++
        this.calFlag = obj.calFlag ? obj.calFlag : 0
    }

    /**  */
    strongUpdate() {
        let func = null
        for (func of this.updateArr) {
            func(this)
        }
    }

    /** 3D */
    strongUpdate3D() {
        let func = null
        for (func of this.updateArr3D) {
            func(this)
        }
    }

    /**  */
    chooseUpdate() {
        let func = null
        if (this.choose) {
            for (func of this.chooseFuncArr) {
                func(this)
            }
            for (func of this.lateChooseFuncArr) {
                func(this)
            }
        }
        else {
            for (func of this.noChooseFuncArr) {
                func(this)
            }
            for (func of this.lateNoChooseFuncArr) {
                func(this)
            }
        }
    }

    /**  */
    destory() {
        let func = null
        for (func of this.destoryFuncArr) {
            func(this)
        }
    }

    /** 3Dx */
    destory3D() {

    }
}

export class JDesignType {
    type: string;
    flag?: number;
    id?: string;
    mode?: string;
    calFlag?: number;
}