import { MouseEventService } from "../services/mouse-event.service";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../services/dom-update.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { PopDivService } from "../../../services/pop-div.service";
import { PanelTopEvent } from "./panelTop.event";
import { ElementRef } from "@angular/core";
import { AnimationEventListen } from "./animation.event.listen";
import { RightMenuService } from "../../../services/right-menu.service";
import { SvgMouseEvent } from "../act/mouse/svg.mouse.event";
import { FloatDivEvent } from "./floatDiv.event";
import { SvgMouseListen } from "./svg.mouse.listen";
import { PanelLeftEvent } from "./panelLeft.event";
import { HttpService } from "../../../services/http.service";
import { DesignStyleDataRightMenu } from "../../../designStyleData/rightMenu/designStyleData.rightMenu";
import { PanelTestInputEvent } from "./panelTestInput.event";
import { SvgAbsorbEvent } from "../act/absorb/svg.absorb.event";
import { SvgInputEvent } from "../act/input/svg.input.event";
import { DesignShortFunc } from "../../../designStyleData/shortFunc/design.shortFunc";
import { DomSanitizer } from "../../../../../node_modules/@angular/platform-browser";

export class SvgEvent {

    constructor(
        private mouseEventService: MouseEventService,
        private rightEventService: RightMenuService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService,
        private popService: PopDivService,
        private el: ElementRef,
        private divName: string,
        private httpService: HttpService,
        private sanitizer: DomSanitizer
    ) {
        let a = this.DUService.obj.getQueryStringSuper('isSimplify')
        if (a != 1) {
            this.setEvent()
        }

    }

    /**  */
    setEvent() {
        this.calService.undoRedo.save()
        /**  */
        let shortFuncEvent = new DesignShortFunc(this.popService, this.dataService, this.DUService, this.calService, this.domService)
        /**  */
        let svgAbosrbEvent = new SvgAbsorbEvent(this.dataService, this.DUService, this.calService)
        /**  */
        let svgInputEvent = new SvgInputEvent(this.dataService, this.DUService, this.calService, this.popService)
        /**  */
        let mouseEvent = new SvgMouseEvent(this.mouseEventService, this.dataService, this.domService, this.DUService, this.calService, this.popService, svgAbosrbEvent, svgInputEvent)
        /**  */
        // let panelTopEvent = new PanelTopEvent(this.popService, this.dataService, this.domService, this.calService, undoRedoEvent, this.httpService)
        /**  */
        // let panelLeftEvent = new PanelLeftEvent(this.popService, this.dataService, this.domService, this.calService, undoRedoEvent, this.httpService, this.DUService)
        /**  */
        let panelTestInputEvent = new PanelTestInputEvent(this.DUService, this.dataService, this.popService)
        /**  */
        let floatDivEvent = new FloatDivEvent(this.popService, this.dataService, this.domService, this.calService, this.DUService)
        floatDivEvent.changeThumbnail()
        /**   */
        let rightMenus = new DesignStyleDataRightMenu(this.dataService, this.popService, this.rightEventService, this.calService, this.httpService, this.DUService, this.domService, this.sanitizer)
        /**  */
        let svg = <Document>this.el.nativeElement.getElementsByClassName(this.divName)[0]
        let mouseListen = new SvgMouseListen(svg, this.mouseEventService, this.dataService)
        /**  */
        let svgEventListen = new AnimationEventListen(this.dataService)
        /**  */
        // this.setWheel(svg)
        this.floorplanDataEmit()
    }

    /**  */
    setWheel(dom) {
        dom.addEventListener('wheel', (e: WheelEvent) => {
            if (e.deltaY > 0) {
                this.dataService.cmd.floorplan.zoom = Math.max(1, Math.min(3, this.dataService.cmd.floorplan.zoom + 0.02))
            } else if (e.deltaY < 0) {
                this.dataService.cmd.floorplan.zoom = Math.max(1, Math.min(3, this.dataService.cmd.floorplan.zoom - 0.02))
            }
            this.popService.setInputBoxs()
            this.popService.setButtons()
            this.domService.mainSvg.emit()
        })
    }

    floorplanDataEmit() {
        this.dataService.cache.floorplan.dataEmit.subscribe((unUpdates: any = {}) => {
            if (!unUpdates.door)
                this.calService.door.listAbosrb()
            if (!unUpdates.win)
                this.calService.win.listAbosrb()
            if (!unUpdates.LWin)
                this.calService.LWin.listAbosrb()
            if (!unUpdates.UWin)
                this.calService.UWin.listAbosrb()
            if (!unUpdates.cornerWin)
                this.calService.cornerWin.listAbsorb()
        })
    }

    /**  */
    setRight() {

    }
}