import { JDesign } from "./JDesign";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JPoint } from "./JPoint";
import { JCowall } from "./JCowall";
import { JRoom } from "./JRoom";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { JDoor } from "./JDoor";
import { JWin } from "./JWin";
import { JFurniture } from "./JFurniture";
import { JCornerWin } from "./JCornerWin";
import { JGeometry } from "./JGeometry";

export class JWall extends JDesign {
    /**  */
    private _coedgeID: string
    /**  */
    get coedgeID() { return this._coedgeID }
    set coedgeID(obj) {
        this._coedgeID = obj
        // if (this.coedgeID != undefined && this.coedgeID != null) {
        //     let cowall: JCowall = this.getCowall()
        //     if (cowall.edgeID != this.id)
        //         cowall.edgeID = this.id
        //     //this.strongUpdate()
        // }

    }

    /**  */
    private _thickness: number
    /**  */
    get thickness() { return this._thickness }
    set thickness(obj) {
        this._thickness = obj
        //this.strongUpdate()
    }

    /**  */
    private _doorIDs: string[] = []
    /**  */
    get doorIDs() { return this._doorIDs }
    set doorIDs(obj: string[]) {
        this._doorIDs = obj
        //this.strongUpdate()
    }

    /**  */
    private _winIDs: string[] = []
    /**  */
    get winIDs() { return this._winIDs }
    set winIDs(obj: string[]) { this._winIDs = obj }

    /**  */
    private _geometryIDs: string[] = []
    /**  */
    get geometryIDs() { return this._geometryIDs }
    set geometryIDs(data) { this._geometryIDs = data }

    /**  */
    private _outLine: DC.Pos[][] = null
    /**  ,: [[,],[,]] */
    get outLine() { return this._outLine }
    set outLine(data) { this._outLine = data }

    /** () */
    private _drawOutLine: DC.Pos[][] = undefined
    /**
     * ()
     * @returns undefined,outLine
     * @returns ,: [[,],[,]]
      */
    get drawOutLine() {
        if (!this._drawOutLine)
            return this.outLine
        return this._drawOutLine
    }
    set drawOutLine(data) { this._drawOutLine = data }


    /**  */
    private _arcDistance: number
    /** (arcPer) */
    get arcDistance() { return this._arcDistance }
    set arcDistance(data) {
        this.arcPer = data / this.getDistance()
        this._arcDistance = data
    }

    /**  */
    private _arcRadius: number = undefined
    /**  */
    get arcRadius() { return this._arcRadius }
    set arcRadius(data) { this._arcRadius = data }

    /**  */
    private _circlePos: DC.Pos = undefined
    /**  */
    get circlePos() { return this._circlePos }
    set circlePos(data) { this._circlePos = data }

    /**  */
    private _arcPer: number
    /**  */
    get arcPer() { return this._arcPer }
    set arcPer(data) { this._arcPer = data }

    private _cornWinIDs: string[]
    /** IDs */
    get cornWinIDs() { return this._cornWinIDs }
    set cornWinIDs(data) { this._cornWinIDs = data }

    private _wallType: number;
    /**  */
    get wallType() { return this._wallType }
    set wallType(data) { this._wallType = data }

    /**  */
    private _isVeryShort: boolean = false
    /**  */
    get isVeryShort() { return this._isVeryShort }
    set isVeryShort(data) { this._isVeryShort = data }

    /**  */
    private _shadowRadian: number = undefined
    private _shadowDistance: number = undefined


    constructor(
        obj: JWallInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService,
    ) {
        super({ type: 'wall', id: obj.id }, dataService);
        this.thickness = obj.thickness ? obj.thickness : this.dataService.cmd.wall.thickness
        this.coedgeID = obj.coedgeID ? obj.coedgeID : null
        this.doorIDs = obj.doorIDs ? obj.doorIDs : []
        this.winIDs = obj.winIDs ? obj.winIDs : []
        this.geometryIDs = obj.furnitureIDs ? obj.furnitureIDs : []
        this.arcDistance = obj.arcDistance ? obj.arcDistance : 0
        this.cornWinIDs = obj.cornWinIDs ? obj.cornWinIDs : []
        this.wallType = obj.wallType ? obj.wallType : 0
        this.arcPer = obj.arcPer ? obj.arcPer : 0
        this.dataService.walls[this.id] = this
    }

    /**  */
    getPoint(num: number): JPoint {
        if (num != 0 && num != 1)
            return null
        let cowall: JCowall = this.getCowall()
        if (!cowall)
            return undefined

        /** , */
        if (!cowall.reversed) {
            let point: JPoint = this.dataService.points[cowall.pointIDs[num]]
            return point
        }
        else {
            let point: JPoint = this.dataService.points[cowall.pointIDs[num == 0 ? 1 : 0]]
            return point
        }

    }

    /**  */
    isBigArc() {
        if (this.arcDistance > this.arcRadius)
            return true
        else
            return false
    }

    /** path */
    getPathPos(): DC.Pos[][] {
        if (this.isSamePos()) {
            return null
        }
        else {
            /** new */
            let pathArr: DC.Pos[][] = []
            return pathArr
        }
    }

    /**  */
    getDistance(reCal: boolean = false): number {
        if (reCal || this._shadowDistance == undefined) {
            let posArr: DC.Pos[] = this.getPosArr()
            let distance: number = this.DUService.pos.getDistance(posArr[0], posArr[1])
            this._shadowDistance = distance
            return distance
        }
        return this._shadowDistance

    }

    /**   */
    getPointArr(): JPoint[] {
        let pointStart: JPoint = this.getPoint(0)
        let pointEnd: JPoint = this.getPoint(1)
        if (!pointStart || !pointEnd)
            return undefined
        return [pointStart, pointEnd]
    }

    /** ID() */
    getPointIDArr(): string[] {
        let cowall: JCowall = this.dataService.cowalls[this.coedgeID]
        return cowall.pointIDs
    }

    /**  */
    getCenterPos(): DC.Pos {
        let posArr: DC.Pos[] = this.getPosArr()
        let pos: DC.Pos = this.DUService.pos.getCenterPos(posArr[0], posArr[1])
        return pos
    }


    /**  */
    getPosArr(): DC.Pos[] {
        let pointArr: JPoint[] = this.getPointArr()
        return [pointArr[0].pos, pointArr[1].pos]
    }

    /** 
     * @param reCal ,false,_shadowRadian
     */
    getRadian(reCal: boolean = false): number {
        if (this._shadowRadian == undefined || reCal) {
            let posArr: DC.Pos[] = this.getPosArr()
            let radian: number = this.DUService.pos.getRadian(posArr[0], posArr[1])
            this._shadowRadian = radian
            return radian
        }
        return this._shadowRadian

    }

    /**  */
    getRotate(): number {
        let cowall: JCowall = this.getCowall()
        return cowall.getRotate()
    }

    /**  */
    getCowallArr(): JCowall[] {
        let cowallArr: JCowall[] = []
        let obj: JCowall = this.dataService.cowalls[this.coedgeID]
        if (!obj)
            return []
        cowallArr.push(obj)
        if (obj.partnerID != null) {
            let objA: JCowall = this.dataService.cowalls[obj.partnerID]
            cowallArr.push(objA)
        }

        return cowallArr
    }

    /**  */
    getCowall(): JCowall {
        let cowall: JCowall = this.dataService.cowalls[this.coedgeID]
        return cowall
    }

    /**  */
    isSamePos(): boolean {
        let posArr: DC.Pos[] = this.getPosArr()
        if (posArr[0].x == posArr[1].x && posArr[0].y == posArr[1].y)
            return true
        else
            return false
    }

    /**  */
    getOppsiteCowall(): JCowall {
        let cowall: JCowall = this.getCowall()
        let newCowall: JCowall = this.dataService.cowalls[cowall.partnerID]
        return newCowall
    }

    /**  */
    getRoomArr(): JRoom[] {
        let cowallArr: JCowall[] = this.getCowallArr()
        let roomArr: JRoom[] = []
        for (let cowall of cowallArr) {
            if (cowall.roomID != null) {
                let room: JRoom = cowall.getRoom()
                roomArr.push(room)
            }
        }
        return roomArr
    }

    /**  */
    getDoorArr() {
        if (this.doorIDs == null)
            return []

        let doors: JDoor[] = []
        for (let i = 0; i < this.doorIDs.length; i++) {
            doors.push(this.dataService.doors[this.doorIDs[i]])
        }
        return doors
    }

    /**  */
    getWinArr() {
        if (this.winIDs == null)
            return []

        let wins: JWin[] = []
        for (let i = 0; i < this.winIDs.length; i++) {
            wins.push(this.dataService.wins[this.winIDs[i]])
        }
        return wins
    }

    /**  */
    getGeometryArr() {
        if (this.geometryIDs == null)
            return []
        let geometry: JGeometry[] = []
        for (let i = 0; i < this.geometryIDs.length; i++) {
            if (this.geometryIDs[i].indexOf('furniture') != -1)
                geometry.push(this.dataService.furnitures[this.geometryIDs[i]])
            else {
                geometry.push(this.dataService.builds[this.geometryIDs[i]])
            }
        }
        return geometry
    }

    /**  */
    getNearRoomArr(): JRoom[] {
        let pointArr: JPoint[] = this.getPointArr()
        let cowallArr: JCowall[] = []
        cowallArr.push(...pointArr[0].getCowallArr())
        cowallArr.push(...pointArr[1].getCowallArr())
        let roomArr: JRoom[] = []
        let cowall: JCowall = null
        let room: JRoom = null
        let same: boolean = false
        let newRoom: JRoom = null
        for (cowall of cowallArr) {
            if (cowall.roomID != null) {
                newRoom = cowall.getRoom()
                same = false
                for (room of roomArr) {
                    if (room.id == newRoom.id)
                        same = true
                    break
                }
                if (!same) {
                    roomArr.push(newRoom)
                }
            }
        }
        return roomArr
    }

    /** () */
    getNearWallArr(): JWall[] {
        let pointArr = this.getPointArr()
        let handWalls = pointArr[0].getWallArr()
        this.DUService.cal.removeByAttr(handWalls, 'id', this.id)
        let endWalls = pointArr[1].getWallArr()
        this.DUService.cal.removeByAttr(endWalls, 'id', this.id)
        handWalls.push(...endWalls)
        return handWalls
    }

    /**  */
    getCornWinArr() {
        let cornWins: JCornerWin[] = []
        for (let i = 0; i < this.cornWinIDs.length; i++) {
            cornWins.push(this.dataService.cornerWins[this.cornWinIDs[i]])
        }
        return cornWins
    }

    /**  */
    getInLine(): DC.Pos[] {
        return [this.outLine[0][1], this.outLine[1][0]]
    }
    /**  */
    getDrawInLine(): DC.Pos[] {
        return [this.drawOutLine[0][1], this.drawOutLine[1][0]]
    }
    /**  */
    getInLineDistance() {
        let posArr = this.getInLine()
        return this.DUService.pos.getDistance(posArr[0], posArr[1])
    }

    /**  */
    getOutLine(): DC.Pos[] {
        return [this.outLine[1][1], this.outLine[0][0]]
    }

    /**  */
    getDrawOutLine(): DC.Pos[] {
        return [this.drawOutLine[1][1], this.drawOutLine[0][0]]
    }

    /**  */
    getOutLineDistance() {
        let posArr = this.getOutLine()
        return this.DUService.pos.getDistance(posArr[0], posArr[1])
    }
    /**  */
    transArc(arcDistance?: number) {
        this.arcDistance = arcDistance == undefined ? this.arcDistance : arcDistance
        let distance = this.getDistance()
        let radius = (Math.pow(this.arcDistance, 2) + Math.pow(distance / 2, 2)) / (2 * this.arcDistance)
        this.arcRadius = radius
        let posArr = this.getPosArr()
        let radian = this.getRadian()
        let centerPos = this.DUService.pos.getCenterPos(posArr[0], posArr[1])
        let circlePos = this.DUService.pos.getRayPos(centerPos, radian - (Math.PI / 2), this.arcDistance - radius)
        this.circlePos = circlePos
    }
}

export class JWallInitType {
    id?: string;
    flag?: number;
    coedgeID?: string;
    thickness?: number;
    doorIDs?: string[];
    winIDs?: string[];
    furnitureIDs?: string[];
    arcDistance?: number;
    cornWinIDs?: string[];
    wallType?: number;
    arcPer?: number;
}