import { Component, OnInit, Input } from '@angular/core';
import { PopDivService } from '../../../services/pop-div.service';

@Component({
  selector: '[panelBottom]',
  templateUrl: './panel-bottom.component.html',
  styleUrls: ['./panel-bottom.component.css']
})
export class PanelBottomComponent implements OnInit {

  private _panelBottom;
  @Input() set panelBottom(data) {
    // this._panelBottom = data;
    this._panelBottom = data
  }
  get panelBottom() { return this._panelBottom }
  constructor(
    private popService: PopDivService
  ) { }

  ngOnInit() {
  }

  getArr(data) {
    let arr = []
    for (let key in data) {
      arr.push(data[key])
    }
    return arr
  }

}
