export class NewNewHotKey {
    /**  */
    private _saveKeysData: boolean[] = []
    /**  */
    private _currentDownKeyNum: number = 0
    /*  */
    private _isDoneArr: boolean[] = []
    /*  */
    designObj: any
    /*  */
    exkeys = []
    constructor(
        private shortKeys: ShortKey[]
    ) {
        document.addEventListener("keydown", (e) => {
            let key = this.getTransKey(e)
            this.addDownKey(key)
            let isDo = this.setDownKeys(this.shortKeys);
            if (isDo) {
                e.preventDefault();
                return false;
            }

        })

        document.addEventListener("keyup", (e) => {
            this.setUpKeys(this.shortKeys)
            let key = this.getTransKey(e)
            this.removeUpKey(key)
            e.preventDefault();
            return false;
        })
    }

    /*  */
    getTransKey(e) {
        // 
        if (e.keyCode >= 65 && e.keyCode <= 90) {
            return e.key.toLowerCase();
        }
        else {
            return e.key;
        }
        // return e.key.toLowerCase()
    }

    /*  */
    checkKeys(keys: string[]) {
        if (this._currentDownKeyNum >= keys.length) {
            let same = true
            for (let i = 0; i < keys.length; i++) {
                if (!this._saveKeysData[keys[i]]) {
                    same = false
                    break
                }
            }
            return same
        }
        return false
    }

    /**  */
    checkStatus(status?: () => boolean | (() => boolean)[]) {
        /* true */
        if (!status)
            return true
        /* (true,true) */
        if (status instanceof Array) {
            for (let i = 0; i < status.length; i++) {
                if (status[i]())
                    return true
            }
            return false
        }
        /*  */
        return status()
    }

    /*  */
    checkIsObj(isObj: (obj: any) => boolean) {
        /* true */
        if (!isObj)
            return true
        return (this.designObj != undefined && isObj(this.designObj))
    }

    /*  */
    addDownKey(key: string) {
        if (!this._saveKeysData[key])
            this._currentDownKeyNum++
        this._saveKeysData[key] = true;
    }

    /*  */
    removeUpKey(key: string) {
        this._currentDownKeyNum--
        this._saveKeysData[key] = false;
    }

    /*  */
    setDownKeys(shortKeys: ShortKey[]) {
        let isDo = false
        for (let i = 0; i < shortKeys.length; i++) {
            let a = this.setDownkey(shortKeys[i], i)
            if (a)
                isDo = true
        }
        return isDo
    }

    /*  */
    setUpKeys(shortKeys: ShortKey[]) {
        for (let i = 0; i < shortKeys.length; i++) {
            this.setUpKey(shortKeys[i], i)
        }
    }

    /*  */
    setDownkey(shortKey: ShortKey, index: number) {
        if (shortKey.getObj) {
            this.designObj = (typeof shortKey.getObj) == "function" ? shortKey.getObj() : shortKey.getObj
        }
        if (
            shortKey.downFunc &&
            this.checkKeys(shortKey.keys) &&
            this.checkIsObj(shortKey.isObj) &&
            this.checkStatus(shortKey.status)
        ) {
            /*  */
            if (!this._isDoneArr[index]) {
                if (!shortKey.isObj) {
                    shortKey.downFunc()
                }
                else {
                    shortKey.downFunc(this.designObj)
                }
                this._isDoneArr[index] = true
            }
            return true
        }
        return false
    }

    /*  */
    setUpKey(shortKey: ShortKey, index: number) {
        if (shortKey.getObj) {
            this.designObj = (typeof shortKey.getObj) == "function" ? shortKey.getObj() : shortKey.getObj
        }
        /* false */
        if (this.checkKeys(shortKey.keys)) {
            this._isDoneArr[index] = false
            if (
                shortKey.upFunc &&
                this.checkIsObj(shortKey.isObj) &&
                this.checkStatus(shortKey.status)
            ) {
                if (!shortKey.isObj)
                    shortKey.upFunc()
                else
                    shortKey.upFunc(this.designObj)
            }
        }
    }

}

export class ShortKey {
    keys: string[];
    isObj?: (obj: any) => boolean;
    status?: () => boolean | (() => boolean)[];
    downFunc?: (obj?: any) => void;
    upFunc?: (obj?: any) => void;
    getObj?: any;
}