import { JdesignDataService } from "../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../services/jdesign-util2d.service";
import { PopDivService } from "../../services/pop-div.service";
import { RightMenuService } from "../../services/right-menu.service";
import { WallRightMenu } from "./wall.rightMenu";
import { BackRightMenu } from "./back.rightMenu";
import { DoorRightMenu } from "./door.rightMenu";
import { WinRightMenu } from "./win.rightMenu";
import { FurnitureRightMenu } from "./furniture.rightMenu";
import { PointRightMenu } from "./point.rightMenu";
import { RoomRightMenu } from "./room.rightMenu";
import { CornWinRightMenu } from "./cornWin.rightMenu";
import { DesignStyleDataPanel } from "../panel/designStyleData.panel";
import { HttpService } from "../../services/http.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { DomSanitizer } from "../../../../node_modules/@angular/platform-browser";
import { CalculatorsService } from "../../services/calculators.service";

export class DesignStyleDataRightMenu {
    constructor(
        protected dataService: JdesignDataService,
        protected popService: PopDivService,
        protected rightService: RightMenuService,
        protected calService: CalculatorsService,
        private httpService: HttpService,
        private DUService: JdesignUtil2dService,
        private domService: DomUpdateService,
        private sanitizer: DomSanitizer

    ) {
        let wall = new WallRightMenu(dataService, popService, rightService)
        let back = new BackRightMenu(dataService, popService, rightService)
        let door = new DoorRightMenu(dataService, popService, rightService)
        let win = new WinRightMenu(dataService, popService, rightService)
        let furniture = new FurnitureRightMenu(dataService, popService, rightService)
        let point = new PointRightMenu(dataService, popService, rightService)
        let room = new RoomRightMenu(dataService, popService, rightService)
        let cornWin = new CornWinRightMenu(dataService, popService, rightService)
        let designStyleDataPanel = new DesignStyleDataPanel(dataService, popService, calService, httpService, DUService, domService, sanitizer)
    }
}