import { Component, OnInit, Input, DoCheck } from '@angular/core';
import { JPoint } from '../../../common/jdesign/JPoint';
import { PopDivService } from '../../../../services/pop-div.service';
import { JdesignDataService } from '../../../../services/jdesign-data.service';
import { RightMenuService } from '../../../../services/right-menu.service';
import { DomUpdateService } from '../../../../services/dom-update.service';

@Component({
  selector: '[point]',
  templateUrl: './point.component.html',
  styleUrls: ['./point.component.scss']

})
export class PointComponent implements OnInit, DoCheck {

  private _point: JPoint;
  isOver: boolean = false
  testPos: { x: number, y: number } = undefined
  @Input() set point(data) {
    this._point = data
  }
  get point() { return this._point }

  constructor(
    private popService: PopDivService,
    private dataService: JdesignDataService,
    private rightService: RightMenuService,
    private domService: DomUpdateService
  ) {

  }


  ngOnInit() {
  }

  ngDoCheck() {
    this.testPos = this.point.pos
    if (!this.testPos) {
      console.log(this.testPos)
      console.log(this.point.cowallIDs)
    }
  }

  mouseDown(e: MouseEvent) {
    if (e.which == 3) {
      this.rightService.point.emit({ e: e, point: this.point })
      this.dataService.cache.common.initUpdate(['rightMenu'])
    }
    else {
      this.dataService.cache.common.choose = this.point
      this.dataService.cache.common.firstPos = { x: e.offsetX, y: e.offsetY }
      this.dataService.cache.common.blockON = true
      this.dataService.cmd.cowall.hide = false
      this.domService.allSvg.emit()
      this.dataService.cache.common.initUpdate()
    }

  }

  mouseOver(e: MouseEvent) {
    if (this.dataService.cmd.floorplan.debugPos != null && this.dataService.cmd.floorplan.debugPos != undefined) {
      let data = {
        text: `${this.point.id}:{
         cos:${this.point.cowallIDs};
         x:${this.point.pos.x};
         y:${this.point.pos.y};
        }`,
        position: this.dataService.cmd.floorplan.debugPos
      }

      this.popService.message.emit(data)
    }
    this.isOver = true
  }

  mouseLeave(e: MouseEvent) {
    this.isOver = false
  }


}
