import { MouseEventService } from "../services/mouse-event.service";
import { JdesignDataService } from "../../../services/jdesign-data.service";

export class SvgMouseListen {
    private _event: MouseEvent | WheelEvent = null
    private _downFlag: boolean = false
    private _upFlag: boolean = false
    private _moveFlag: boolean = false

    constructor(
        private doc: Document,
        private mouseEventService: MouseEventService,
        private dataService: JdesignDataService) {
        this.mouseDown(doc)
        this.mouseMove(doc)
        this.mouseUp(doc)
        this.dataService.cache.AnimationFuncs.push(() => {
            this.mouseListen()
        })

    }

    /**  */
    mouseDown(doc: Document) {
        doc.addEventListener('mousedown', (e) => {
            // this.mouseEventService.doDownEvents(e)
            this._downFlag = true
            this._event = e
        })
    }

    /**  */
    mouseMove(doc: Document) {
        doc.addEventListener('mousemove', (e) => {
            // this.mouseEventService.doMoveEvents(e)
            this._moveFlag = true
            this._event = e
        })
    }

    /**  */
    mouseUp(doc: Document) {
        doc.addEventListener("mouseup", (e) => {
            // this.mouseEventService.doUpEvents(e)
            this._upFlag = true
            this._event = e
        })
    }

    /**  */
    mouseListen() {
        if (this._downFlag) {
            this.mouseEventService.doDownEvents(this._event)
            this._downFlag = false
        }
        if (this._moveFlag) {
            this.mouseEventService.doMoveEvents(this._event)
            this._moveFlag = false
        }
        if (this._upFlag) {
            this.mouseEventService.doUpEvents(this._event)
            this._upFlag = false
        }
    }
}