import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { JUWinTypeInit, JUWin } from "../jdesign/JUWin";
import { JUWin3D } from "../../../three/jDesign3D/JUWin3D";
import { JWall } from "../jdesign/JWall";

export class UWinCal {
    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService,
    ) {

    }

    createNew(obj: JUWinTypeInit) {
        let uWin = new JUWin3D(obj, this.dataService, this.DUService)
        this.dataService.cache.UWin.createFunc(uWin)
        return uWin
    }

    /**  */
    unionWall(obj: JUWin, wall: JWall) {
        obj.centerWallID = wall.id
        let posArr = wall.getPosArr()
        obj.pos = { x: posArr[0].x, y: posArr[0].y }
        obj.radian = wall.getRadian()
    }

    /**  */
    disconnectWall(obj: JUWin) {
        obj.centerWallID = undefined
    }


    /* U */
    getUWinAbsorbWalls() {
        let absorbWalls: JWall[] = []
        let walls = this.calService.transData.getUnArcWalls()
        for (let i = 0; i < walls.length; i++) {
            let roomArr = walls[i].getRoomArr()
            if (roomArr && roomArr.length == 1) {
                let pointArr = walls[i].getPointArr()
                if (pointArr[0].getWallArr().length == 2 && pointArr[1].getWallArr().length == 2) {
                    absorbWalls.push(walls[i])
                }
            }

        }
        return absorbWalls
    }

    /*  */
    listAbosrb() {
        for (let key in this.dataService.UWins) {
            let uWin = this.dataService.UWins[key]
            let wall = uWin.getCenterWall()
            if (wall) {
                let pointArr = wall.getPointArr()
                if (pointArr[0].getWallArr().length != 2 || pointArr[1].getWallArr().length != 2) {
                    this.disconnectWall(uWin)
                }
                else {
                    this.unionWall(uWin, wall)
                }
            }
            else {
                this.disconnectWall(uWin)
            }
        }
    }

}