import { Injectable } from '@angular/core';
import * as designData from '../data/design.data'
import { JFloorplan3D } from '../three/jDesign3D/JFloorplan3D';
import { JCmd } from '../svg/common/jdesign/JCmd';
import { JDesign } from '../svg/common/jdesign/JDesign';
import { JCamera3D } from '../three/jDesign3D/JCamera3D';
import { JIcon } from '../svg/common/jdesign/JIcon';
import { JJSON } from '../svg/common/jdesign/JJSON';

@Injectable()
export class JdesignDataService {

  constructor() { }

  private _floorplan: JFloorplan3D
  /** floorPlan */
  get floorplan() {
    return this._floorplan
  }

  set floorplan(data) {
    this._floorplan = data
  }

  private _camera: JCamera3D = null
  get camera() {
    return this._camera
  }
  set camera(data) {
    this._camera = data
  }

  private _cmd: JCmd
  /** cmd */
  get cmd() {
    return this._cmd
  }

  set cmd(data) {
    this._cmd = data
  }

  private _icon: JIcon
  /** icon */
  get icon() { return this._icon }
  set icon(data) { this._icon = data }

  /**  */
  get cache() { return designData.cache }

  private _json: JJSON = new JJSON
  /* json */
  get json() { return this._json }
  set json(data) { this._json = data }


  // private _common: JCommon
  // /** common */
  // get common() { return this._common }
  // set common(data) {
  //   this._common = data
  // }


  /** ================================================== */
  get points() {
    return designData.points
  }

  /** points */
  doPoints() {
    let type = 'points'
    let obj = []
    /* cowall */
    obj['twoCowalls'] = () => {
      let points = []
      for (let key in this.points) {
        if (this.points[key].cowallIDs.length == 2) {
          points.push(this.points[key])
        }
      }
      return points
    }
    let func = this.getFunc(type, obj)
    return func
  }


  /** =========================================== */
  get cowalls() {
    return designData.cowalls
  }

  /** cowall */
  doCowalls() {
    let type = 'cowalls'
    let func = this.getFunc(type)
    return func
  }


  /** ==================================================== */
  get walls() {
    return designData.walls
  }

  /** wall */
  doWalls() {
    let type = 'walls'
    let func = this.getFunc(type)
    return func
  }


  /** ========================================================== */
  get rooms() {
    return designData.rooms
  }

  /** room */
  doRooms() {
    let type = 'rooms'
    let func = this.getFunc(type)
    return func
  }


  /** ================================================================ */
  get floors() {
    return designData.floors
  }

  /**  */
  doFloors() {
    let type = 'floors'
    let func = this.getFunc(type)
    return func
  }

  /*  */
  get roofs() {
    return designData.roofs
  }

  /*  */
  doRoofs() {
    let type = 'roofs'
    let func = this.getFunc(type)
    return func
  }

  /** ===================================================================== */
  get doors() {
    return designData.doors
  }

  /**  */
  doDoors() {
    let type = 'doors'
    let func = this.getFunc(type)
    return func
  }

  /** ====================================================================== */
  get wins() {
    return designData.wins
  }

  /**  */
  doWins() {
    let type = 'wins'
    let func = this.getFunc(type)
    return func
  }

  /** ==================================================================== */
  get furnitures() {
    return designData.furnitures
  }

  /**  */
  doFurnitures() {
    let type = 'furnitures'
    let func = this.getFunc(type)
    return func
  }

  /** =========================================================== */
  get builds() {
    return designData.builds
  }

  /**  */
  doBuilds() {
    let type = 'builds'
    let func = this.getFunc(type)
    return func
  }

  /**  */
  get cornerWins() {
    return designData.cornerWins
  }

  /**  */
  doCornerWins() {
    let type = 'cornerWins'
    let func = this.getFunc(type)
    return func
  }

  /* L */
  get LWins() {
    return designData.LWins
  }
  /* L */
  doLWins() {
    let type = 'LWins'
    let func = this.getFunc(type)
    return func
  }

  /* U */
  get UWins() {
    return designData.UWins
  }

  /* U */
  doUWins() {
    let type = 'UWins'
    let func = this.getFunc(type)
    return func
  }

  get paramsModels() {
    return designData.paramsModels
  }

  doParamsModels() {
    let type = 'paramsModels'
    let func = this.getFunc(type)
    return func
  }


  /** ======================================================================== */
  /**  */
  private setClean(type: string) {
    let data: any[] = []
    let key: string = null
    for (key in designData[type]) {
      delete designData[type][key]
    }
  }

  /**  */
  private getArr(type: string, ...reObjs: JDesign[]) {
    let data: any[] = []
    let key: string = null
    for (key in designData[type]) {
      let same = false
      for (let i = 0; i < reObjs.length; i++) {
        /*  */
        if (reObjs[i].id == key) {
          same = true
          reObjs.splice(i, 1)
          break;
        }
      }
      /*  */
      if (!same)
        data.push(designData[type][key])
    }
    return data
  }

  /**  */
  private getFunc(type: string, funcPlus?: any) {
    let func = {
      /**  */
      clear: () => {
        this.setClean(type)
        return func
      },
      /**  */
      arr: (...reObj: JDesign[]) => {
        let data = this.getArr(type, ...reObj)
        return data
      },
      urgentUpdate: (...reObj: JDesign[]) => {
        let idArr: string[] = []
        for (let key in designData[type]) {
          designData[type][key].urgentUpdate.emit()
        }
      },
      /**  */
      other: funcPlus ? funcPlus : null
      /* id */

    }

    return func
  }


}




