import { Injectable } from '@angular/core';
import { JdesignDataService } from './jdesign-data.service';
import { WallCal } from '../svg/common/calculator/wall.cal';
import { CowallCal } from '../svg/common/calculator/cowall.cal';
import { JdesignUtil2dService } from './jdesign-util2d.service';
import { PointCal } from '../svg/common/calculator/point.cal';
import { RoomCal } from '../svg/common/calculator/room.cal';
import { PosCal } from '../svg/common/calculator/pos.cal';
import { AdsorbCal } from '../svg/common/calculator/adsorb.cal';
import { LineCal } from '../svg/common/calculator/line.cal';
import { CreateRoomCal } from '../svg/common/calculator/createRoom.cal';
import { DoorCal } from '../svg/common/calculator/door.cal';
import { WinCal } from '../svg/common/calculator/win.cal';
import { FurnitureCal } from '../svg/common/calculator/furniture.cal';
import { CornerWinCal } from '../svg/common/calculator/cornerWin.cal';
import { FloorplanCal } from '../svg/common/calculator/floorplan.cal';
import { TransDataCal } from '../svg/common/calculator/transData.cal';
import { GeometryCal } from '../svg/common/calculator/geometry.cal';
import { BuildCal } from '../svg/common/calculator/build.cal';
import { LWinCal } from '../svg/common/calculator/LWin.cal';
import { UndoRedoCal } from '../svg/common/calculator/undo.redo.cal';
import { UWinCal } from '../svg/common/calculator/uWin.cal';
import { ParamsModelCal } from '../svg/common/calculator/paramsModel.cal';

@Injectable()
export class CalculatorsService {

  private _wallCal = new WallCal(this, this.dataService, this.DUService)
  private _cowallCal = new CowallCal(this, this.dataService, this.DUService)
  private _pointCal = new PointCal(this, this.dataService, this.DUService)
  private _roomCal = new RoomCal(this.dataService, this.DUService, this)
  private _posCal = new PosCal(this.dataService, this.DUService, this)
  private _absorbCal = new AdsorbCal(this.dataService, this.DUService, this)
  private _lineCal = new LineCal(this, this.dataService, this.DUService)
  private _createRoomCal = new CreateRoomCal(this.dataService, this.DUService, this)
  private _doorCal = new DoorCal(this.dataService, this.DUService, this)
  private _winCal = new WinCal(this.dataService, this.DUService, this)
  private _furnitureCal = new FurnitureCal(this.dataService, this.DUService, this)
  private _cornerWinCal = new CornerWinCal(this, this.dataService, this.DUService)
  private _floorplanCal = new FloorplanCal(this.dataService, this.DUService, this)
  private _transDataCal = new TransDataCal(this, this.dataService, this.DUService)
  private _geometryCal = new GeometryCal(this.dataService, this.DUService, this)
  private _buildCal = new BuildCal(this.dataService, this.DUService, this)
  private _LWinCal = new LWinCal(this.dataService, this.DUService, this)
  private _UWinCal = new UWinCal(this.dataService, this.DUService, this)
  private _undoRedoCal = new UndoRedoCal(this.dataService, this.DUService, this, 20)
  private _paramsModelCal = new ParamsModelCal(this.dataService, this.DUService, this)


  constructor(
    private dataService: JdesignDataService,
    private DUService: JdesignUtil2dService,
  ) { }

  /**  */
  get wall() {
    return this._wallCal
  }

  /** cowall */
  get cowall() {
    return this._cowallCal
  }

  /** point */
  get point() {
    return this._pointCal
  }

  /**  */
  get room() {
    return this._roomCal
  }

  /**  */
  get pos() {
    return this._posCal
  }

  /**  */
  get absorb() {
    return this._absorbCal
  }

  /*  */
  get line() {
    return this._lineCal
  }

  /*  */
  get createRoom() {
    return this._createRoomCal
  }

  /*  */
  get door() {
    return this._doorCal
  }

  /*  */
  get win() {
    return this._winCal
  }

  /*  */
  get furniture() {
    return this._furnitureCal
  }

  /*  */
  get geometry() {
    return this._geometryCal
  }

  /*  */
  get build() {
    return this._buildCal
  }

  /*  */
  get cornerWin() {
    return this._cornerWinCal
  }

  /*  */
  get floorplan() {
    return this._floorplanCal
  }

  /* L */
  get LWin() {
    return this._LWinCal
  }

  /* u */
  get UWin() {
    return this._UWinCal
  }

  /*  */
  get transData() {
    return this._transDataCal
  }

  /*  */
  get undoRedo() {
    return this._undoRedoCal
  }

  /*  */
  get paramModel() {
    return this._paramsModelCal
  }

}
