import { JdesignDataService } from "../../../../services/jdesign-data.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { JPoint } from "../../jdesign/JPoint";
import { JCowall } from "../../jdesign/JCowall";

export class MovePointInput {
    constructor(
        private dataService: JdesignDataService,
        private calService: CalculatorsService,
        private DUService: JdesignUtil2dService
    ) {

    }

    /*  */
    checkQualification(point: JPoint) {
        let walls = point.getWallArr()
        if (
            walls.length == 2 &&
            walls[0].arcDistance == 0 &&
            walls[1].arcDistance == 0 &&
            this.DUService.pos.isParallelRadian(walls[0].getRadian(), walls[1].getRadian())
        )
            return true
        return false
    }

    /*  */
    getAllCowallInput(point: JPoint, cb?: (distance: number, cowall: JCowall) => void) {
        let a = this.checkQualification(point)
        if (!a)
            return []
        let datas = []
        let cowalls = point.getCowallArr()
        for (let i = 0; i < cowalls.length; i++) {
            cowalls[i].labelDisplay = false
            let value = parseFloat(this.dataService.cache.cowall.inputData[cowalls[i].id].text)
            let displayValue = parseFloat(this.dataService.cache.cowall.inputData[cowalls[i].id].displayText).toFixed(0)
            let data = {
                pos: this.dataService.cache.cowall.inputData[cowalls[i].id].pos,
                value: displayValue,
                index: 15,
                func: (params: any) => {
                    let distance = parseFloat(params) - value
                    cb(distance, cowalls[i])
                }
            }
            datas.push(data)
        }
        return datas
        // let point
    }
}