import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { DomUpdateService } from '../../../services/dom-update.service';
import { JWall } from '../../common/jdesign/JWall';

@Component({
  selector: '[walls]',
  templateUrl: './walls.component.html',
  styleUrls: ['./walls.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WallsComponent implements OnInit {

  constructor(
    private DUService: JdesignUtil2dService,
    private dataService: JdesignDataService,
    private domUpdateService: DomUpdateService,
    private ref: ChangeDetectorRef
  ) {
    this.domUpdateService.svgWall.subscribe(() => {
      this.displayTest()
      this.ref.detectChanges()
    })
  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('walls is ok!!')
    }
  }

  getWallArr() {
    let walls: JWall[] = this.DUService.cal.getStringArrByNum(this.dataService.walls)
    // let newWalls: JWall[] = []
    // for (let i = 0; i < walls.length; i++) {
    //   if (walls[i].wallType != 1)
    //     newWalls.push(walls[i])
    // }
    // for (let i = 0; i < walls.length; i++) {
    //   if (walls[i].wallType == 1)
    //     newWalls.push(walls[i])
    // }
    return walls
  }

  displayTest() {
    for (let key in this.dataService.walls) {
      let wall = this.dataService.walls[key]
      wall.isVeryShort = false
    }
  }

}
