import { Redo3D } from "../../../../three/save3D/Redo3D";
import { JdesignDataService } from "../../../../services/jdesign-data.service";
import { JDesign } from "../../jdesign/JDesign";
import { JPoint3D } from "../../../../three/jDesign3D/JPoint3D";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { JCowall3D } from "../../../../three/jDesign3D/JCowall3D";
import { JWall3D } from "../../../../three/jDesign3D/JWall3D";
import { JRoom3D } from "../../../../three/jDesign3D/JRoom3D";
import { JFloor3D } from "../../../../three/jDesign3D/JFloor3D";
import { JWin3D } from "../../../../three/jDesign3D/JWin3D";
import { JDoor3D } from "../../../../three/jDesign3D/JDoor3D";
import { JFurniture3D } from "../../../../three/jDesign3D/JFurniture3D";
import { JCornerWin3D } from "../../../../three/jDesign3D/JCornerWin3D";
import { JRoof3D } from "../../../../three/jDesign3D/JRoof3D";
import { JBayWin3D } from "../../../../three/jDesign3D/JBayWin3D";
import { JBuild3D } from "../../../../three/jDesign3D/JBuild3D";
import { SaveType } from "../../save/SaveType";
import { SaveType3D } from "../../../../three/save3D/SaveType3D";
import { JLWin3D } from "../../../../three/jDesign3D/JLWin3D";
import { JUWin3D } from "../../../../three/jDesign3D/JUWin3D";

export class OpenAct {

    private _redo3D: Redo3D = new Redo3D()
    private _oldObjIDs: number[] = []
    private _differType: string[][] = []
    private _maxNum: number = 0

    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService
    ) {
        this._differType = this.getAllObjDiffer2DAnd3D()
    }
    /**  */
    openData(data: any[], wallDoCb?: () => void, roomDoCb?: () => void, allDoCb?: (maxNum?) => void) {
        this._oldObjIDs = []
        this._maxNum = 0
        /*  */
        for (let i = 0; i < data.length; i++) {
            switch (data[i].type) {
                case 'floorplan':
                    this.reFloorPlan(data[i])
                    break;
                /** cmdtpye */
                case undefined:
                    this.reCmd(data[i])
                    break;
                case 'point':
                    //this.rePoint(data[i])
                    this.redoObjBase(this.dataService.points, data[i])
                    // console.log(data[i])
                    break;
                case 'cowall':
                    //this.reCowall(data[i])
                    this.redoObjBase(this.dataService.cowalls, data[i])
                    // console.log(data[i])
                    // this.dataService.doWalls().urgentUpdate()
                    break;
                case 'wall':
                    //this.reWall(data[i])
                    this.redoObjBase(this.dataService.walls, data[i])
                    // console.log(data[i])
                    // this.dataService.doWalls().urgentUpdate()
                    break;

            }
        }
        if (wallDoCb)
            wallDoCb()
        /*  */
        for (let i = 0; i < data.length; i++) {
            switch (data[i].type) {
                case 'room':
                    //this.reRoom(data[i])
                    this.redoObjBase(this.dataService.rooms, data[i])
                    break;
                case 'floor':
                    //this.reFloor(data[i])
                    this.redoObjBase(this.dataService.floors, data[i])
                    break;
                case 'roof':
                    this.redoObjBase(this.dataService.roofs, data[i])
                    break;
            }
        }
        if (roomDoCb)
            roomDoCb()
        /*  */
        for (let i = 0; i < data.length; i++) {
            switch (data[i].type) {
                case 'furniture':
                    //this.reFurniture(data[i])
                    this.redoObjBase(this.dataService.furnitures, data[i])
                    this.dataService.furnitures[data[i].id].act3D.move()
                    break;
                case "build":
                    this.redoObjBase(this.dataService.builds, data[i])
                    break;
                case 'door':
                    //this.reDoor(data[i])
                    this.redoObjBase(this.dataService.doors, data[i])
                    break;
                case 'win':
                    //this.reWin(data[i])
                    this.redoObjBase(this.dataService.wins, data[i])
                    break;
                case 'cornerWin':
                    this.redoObjBase(this.dataService.cornerWins, data[i])
                    break;
                case "LWin":
                    this.redoObjBase(this.dataService.LWins, data[i])
                    break
                case "UWin":
                    this.redoObjBase(this.dataService.UWins, data[i])
                    break

            }
        }
        if (allDoCb)
            allDoCb(this._maxNum)
        /*  */
        for (let i = 0; i < data.length; i++) {
            switch (data[i].type) {
                case 'floor':
                    let floor = this.dataService.floors[data[i].id]
                    this._redo3D.doneFloor(floor)
                    break;
            }
        }
    }

    /**  */
    redoBase(reDataArr: JDesign[], reData: JDesign, cb?: (obj: JDesign) => void) {
        reDataArr[reData.id] = reData
        reData.strongUpdate()
        // reData.strongUpdate3D()
        if (cb != undefined)
            cb(reData)
    }


    getDiffer2DAnd3D(type2D, type3D) {
        // let newType3D = new type3D()
        // let newType2D = new type2D()
        // let key2DArr: boolean[] = []
        // let key3DArr: string[] = []
        // for (let key2D in newType2D) {
        //     if (newType2D[key2D] == undefined && key2D != 'constructor') {
        //         key2DArr[key2D] = true
        //     }
        // }
        // for (let key3D in newType3D) {
        //     if (!key2DArr[key3D] && key3D != 'constructor') {
        //         key3DArr.push(key3D)
        //     }
        // }
        // return key3DArr
        return this.DUService.obj.getDifferClassType(type2D, type3D, { ignoreTypes: ["constructor"] })
    }

    /** 3d2d */
    getAllObjDiffer2DAnd3D() {
        let arr: string[][] = []
        arr['point'] = this.getDiffer2DAnd3D(SaveType.JPoint, SaveType3D.JPoint3D)
        arr['cowall'] = this.getDiffer2DAnd3D(SaveType.JCowall, SaveType3D.JCowall3D)
        arr['wall'] = this.getDiffer2DAnd3D(SaveType.JWall, SaveType3D.JWall3D)
        arr['room'] = this.getDiffer2DAnd3D(SaveType.JRoom, SaveType3D.JRoom3D)
        arr['floor'] = this.getDiffer2DAnd3D(SaveType.JFloor, SaveType3D.JFloor3D)
        arr['roof'] = this.getDiffer2DAnd3D(SaveType.JRoof, SaveType3D.JRoof3D)
        arr['win'] = this.getDiffer2DAnd3D(SaveType.JWin, SaveType3D.JWin3D)
        arr['bayWin'] = this.getDiffer2DAnd3D(SaveType.JWin, SaveType3D.JBayWin3D)
        arr['door'] = this.getDiffer2DAnd3D(SaveType.JDoor, SaveType3D.JDoor3D)
        arr['build'] = this.getDiffer2DAnd3D(SaveType.JBuild, SaveType3D.JBuild3D)
        arr['furniture'] = this.getDiffer2DAnd3D(SaveType.JFurniture, SaveType3D.JFurniture3D)
        arr['cornerWin'] = this.getDiffer2DAnd3D(SaveType.JCornerWin, SaveType3D.JCornerWin3D)
        arr['LWin'] = this.getDiffer2DAnd3D(SaveType.JLWin, SaveType3D.JLWin3D)
        arr['UWin'] = this.getDiffer2DAnd3D(SaveType.JUWin, SaveType3D.JUWin3D)
        return arr
    }

    Completion3DData(differType: string[], jsonData, obj) {

        for (let i = 0; i < differType.length; i++) {
            // console.log(differType[i])
            // console.log(jsonData[differType[i]])
            let newObj = this.DUService.obj.cloneArr(jsonData[differType[i]])
            // console.log(obj.id)
            // console.log(newObj)
            // console.log('==================')
            obj[differType[i]] = jsonData[differType[i]]
        }
    }

    /**  */
    redoObjBase(reDataArr: JDesign[], reJson) {
        if (reJson.id) {
            let num = this.DUService.cal.getNumFromString(reJson.id)
            if (num > this._maxNum)
                this._maxNum = num
        }
        this._oldObjIDs[reJson.id] = 1
        /** =============================================================== */
        if (reDataArr[reJson.id] == undefined) {
            let reData: any = null
            switch (reJson.type) {
                case 'point':
                    reData = new JPoint3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['point'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createPoint(reData)
                    return reData
                case 'cowall':
                    reData = new JCowall3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['cowall'], reJson, reData)
                    reData.strongUpdate()
                    // this._redo3D.createCowall(reData)
                    return reData
                case 'wall':
                    reData = new JWall3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['wall'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createWall(reData)
                    return reData
                case 'room':
                    reData = new JRoom3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['room'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createRoom(reData)
                    return reData
                case 'floor':
                    reData = new JFloor3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['floor'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createFloor(reData)
                    return reData
                case 'roof':
                    reData = new JRoof3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['roof'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createRoof(reData)
                    return reData
                case 'furniture':
                    reData = new JFurniture3D(reJson, this.dataService, this.DUService)
                    this.Completion3DData(this._differType['furniture'], reJson, reData)
                    this.dataService.cache.furniture.createFunc(reData)
                    reData.strongUpdate()
                    this._redo3D.createFurniture(reData)
                    return reData
                case "build":
                    reData = new JBuild3D(reJson, this.dataService, this.DUService)
                    this.Completion3DData(this._differType['build'], reJson, reData)
                    this.dataService.cache.build.createFunc(reData)
                    reData.strongUpdate()
                    this._redo3D.createBuild(reData)
                    return reData
                case 'door':
                    reData = new JDoor3D(reJson, this.dataService, this.DUService)
                    this.Completion3DData(this._differType['door'], reJson, reData)
                    this.dataService.cache.door.createFunc(reData)
                    reData.strongUpdate()
                    this._redo3D.createDoor(reData)
                    return reData
                case 'win':
                    if (reJson.isBay) {
                        reData = new JBayWin3D(reJson, this.dataService, this.DUService)
                        this.Completion3DData(this._differType['bayWin'], reJson, reData)
                        this.dataService.cache.win.createFunc(reData)

                    }
                    else {
                        reData = new JWin3D(reJson, this.dataService, this.DUService)
                        this.Completion3DData(this._differType['win'], reJson, reData)
                        this.dataService.cache.win.createFunc(reData)
                    }
                    reDataArr[reData.id] = reData
                    reData.strongUpdate()
                    this._redo3D.createWin(reData)
                    return reData
                case 'cornerWin':
                    reData = new JCornerWin3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['cornerWin'], reJson, reData)
                    this.dataService.cache.cornerWin.createFunc(reData)
                    reData.strongUpdate()
                    this._redo3D.createCornerWin(reData)
                    return reData
                case 'LWin':
                    reData = new JLWin3D(reJson, this.dataService, this.DUService)
                    console.log(reJson)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['LWin'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createLWin(reData)
                    return reData
                case 'UWin':
                    reData = new JUWin3D(reJson, this.dataService, this.DUService)
                    reDataArr[reData.id] = reData
                    this.Completion3DData(this._differType['UWin'], reJson, reData)
                    reData.strongUpdate()
                    this._redo3D.createUWin(reData)
                    return reData
            }
        }
        /** ========================================================= */
        else {
            let fixData: any = undefined
            /**  */
            let copyFunc = (dataArr: any[], cb: (data: JDesign) => void) => {
                for (let key in reJson) {
                    dataArr[reJson.id][key] = reJson[key]
                }
                dataArr[reJson.id].strongUpdate()
                cb(dataArr[reJson.id])
                return dataArr[reJson.id]
            }
            switch (reJson.type) {
                case 'point':
                    fixData = copyFunc(this.dataService.points, this._redo3D.rePoint)
                    return fixData
                case 'cowall':
                    fixData = copyFunc(this.dataService.cowalls, this._redo3D.reCowall)
                    return fixData
                case 'wall':
                    fixData = copyFunc(this.dataService.walls, this._redo3D.reWall)
                    return fixData
                case 'room':
                    fixData = copyFunc(this.dataService.rooms, this._redo3D.reRoom)
                    return fixData
                case 'floor':
                    fixData = copyFunc(this.dataService.floors, this._redo3D.reFloor)
                    return fixData
                case 'roof':
                    fixData = copyFunc(this.dataService.roofs, this._redo3D.reRoof)
                    return fixData
                case 'furniture':
                    fixData = copyFunc(this.dataService.furnitures, this._redo3D.reFurniture)
                    return fixData
                case "build":
                    fixData = copyFunc(this.dataService.builds, this._redo3D.reBuild)
                    return fixData
                case 'door':
                    fixData = copyFunc(this.dataService.doors, this._redo3D.reDoor)
                    return fixData
                case 'win':
                    fixData = copyFunc(this.dataService.wins, this._redo3D.reWin)
                    return fixData
                case "cornerWin":
                    fixData = copyFunc(this.dataService.cornerWins, this._redo3D.reCornWin)
                    return fixData
                case "LWin":
                    fixData = copyFunc(this.dataService.LWins, this._redo3D.reLWin)
                    return fixData
                case "UWin":
                    fixData = copyFunc(this.dataService.UWins, this._redo3D.reUWin)
                    return fixData
            }
        }
        return undefined
    }

    /** (,) */
    cleanData() {
        let func = (obj) => {
            for (let key in obj) {
                obj[key].destory3D()
            }
        }

        /**  */
        func(this.dataService.points)
        this.dataService.doPoints().clear()
        func(this.dataService.cowalls)
        this.dataService.doCowalls().clear()
        func(this.dataService.walls)
        this.dataService.doWalls().clear()
        func(this.dataService.rooms)
        this.dataService.doRooms().clear()
        func(this.dataService.floors)
        this.dataService.doFloors().clear()
        func(this.dataService.roofs)
        this.dataService.doRoofs().clear()

    }

    /** floorPlan */
    private reFloorPlan(readJson) {
        for (let key in readJson) {
            this.dataService.floorplan[key] = readJson[key]
        }
    }

    /** cmd */
    private reCmd(readJson) {
        this.DUService.obj.redo(this.dataService.cmd, readJson)
        // for (let key in readJson) {
        //     this.dataService.cmd[key] = readJson[key]
        // }
    }

    /** cache */
    private reCache(readJson) {
        this.DUService.obj.redo(this.dataService.cache, readJson)
        // for (let key in readJson) {
        //     this.dataService.cache[key] = readJson[key]
        // }
    }

    /** (,) */
    delObj(obj: JDesign, cb?: () => void) {
        switch (obj.type) {
            case 'door':
                //let door=<JDoor>DesignData.floorPlan.getChooseObj()
                this.dataService.cache.shortFuncs['delDoor'](this.dataService.doors[obj.id], false)
                break;
            case 'win':
                this.dataService.cache.shortFuncs["delWin"](this.dataService.wins[obj.id], false)
                break;
            case 'furniture':
                this.dataService.cache.shortFuncs["delFurn"](this.dataService.furnitures[obj.id], false)
                break;
            case "build":
                this.dataService.cache.shortFuncs["delBuild"](this.dataService.builds[obj.id], false)
                break;
            case "cornerWin":
                this.dataService.cache.shortFuncs['delCornWin'](this.dataService.cornerWins[obj.id], false)
                break;
            case "LWin":
                this.dataService.cache.shortFuncs['delLUWin'](this.dataService.LWins[obj.id])
                break
            case "UWin":
                this.dataService.cache.shortFuncs['delLUWin'](this.dataService.UWins[obj.id])
                break
        }
        if (cb != undefined)
            cb()
    }

    /**  ,,*/
    delRedundanceObj(data: any[], cb?: () => void) {
        /**  */
        let key: string = null
        let func = (obj) => {
            for (key in obj) {
                if (this._oldObjIDs[key] == undefined) {
                    this.delObj(obj[key])
                }
            }
        }
        /**  */
        func(this.dataService.doors)
        /** */
        func(this.dataService.wins)
        /**  */
        func(this.dataService.furnitures)
        /**  */
        func(this.dataService.builds)
        /**  */
        func(this.dataService.cornerWins)
        /* L */
        func(this.dataService.LWins)
        func(this.dataService.UWins)
        if (cb != undefined)
            cb()
    }

}