import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class DomUpdateService {

  constructor() {
    let key: string = null
    let ingore = ['allSvg', 'constructor', 'three', 'svgInit', 'threeInit', "mainSvg"]
    /*  */
    let checkDom = (key, ingoreArr) => {
      for (let i = 0; i < ingoreArr.length; i++) {
        if (key == ingoreArr[i])
          return false
      }
      return true
    }
    /*  */
    let arr: string[] = []
    for (key in this) {
      if (checkDom(key, ingore)) {
        arr.push(key)
      }
    }
    /*  */
    this.allSvg.subscribe(() => {
      for (let i = 0; i < arr.length; i++) {
        this[arr[i]].emit()
      }
    })
  }

  /* svg */
  allSvg: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgBack: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgPoint: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  mainSvg: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgWall: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgWallShadow: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgCowall: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgCowallShadow: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgRoom: EventEmitter<object> = new EventEmitter<object>()

  /** svg */
  svgRoomShadow: EventEmitter<object> = new EventEmitter<object>()

  /**  */
  svgBlock: EventEmitter<object> = new EventEmitter<object>()

  /**  */
  absorbLine: EventEmitter<object> = new EventEmitter<object>()

  /* svg */
  testSvg: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgDoor: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgLabels: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgCamera: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgWin: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgGeometry: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgCornerWin: EventEmitter<object> = new EventEmitter<object>()

  /* LU */
  svgLUWin: EventEmitter<object> = new EventEmitter<object>()

  /*  */
  svgParamsModel: EventEmitter<object> = new EventEmitter<object>()

  /* 3d */
  three: EventEmitter<object> = new EventEmitter<object>()

  /* svg */
  svgInit: EventEmitter<object> = new EventEmitter<object>()

  /* three */
  threeInit: EventEmitter<object> = new EventEmitter<object>()
}
