export class ConsoleUtil {

    private _logOn = true
    get logOn() { return this._logOn }
    set logOn(data) { this._logOn = data }

    /**  */
    logArr(arr: string[], offLine?: string) {
        if (this.logOn) {
            for (let i = 0; i < arr.length; i++) {
                console.log(arr[i])
            }
            this.logOffLine(offLine)
        }

    }

    /**  */
    logOffLine(str: string = '=') {
        if (this.logOn) {
            this.logCopy(str, 20)
        }

    }

    /**  */
    logCopy(str: string, int: number) {
        if (this.logOn) {
            let text: string = ''
            for (let i = 0; i < int; i++) {
                text += str
            }
            console.log(text)
        }

    }

    colorLog(text: string, color: string = 'red') {
        if (this.logOn) {
            console.log("%c" + text, "color:" + color)
        }

    }

    /**  */
    redLog(text) {
        if (this.logOn) {
            this.colorLog(text, "red")
        }

    }

    /**  */
    attrLog(attr: number | string, arr: any[], offLine?: string) {
        if (this.logOn) {
            for (let i = 0; i < arr.length; i++) {
                console.log(arr[i][attr])
            }
            this.logOffLine(offLine)
        }

    }

}