import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { DoorWinCal } from "./doorWin.cal";
import { JDoor, JDoorInitType } from "../jdesign/JDoor";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JDoor3D } from "../../../three/jDesign3D/JDoor3D";
import { JWall } from "../jdesign/JWall";

export class DoorCal extends DoorWinCal {

    constructor(
        protected dataService: JdesignDataService,
        protected DUService: JdesignUtil2dService,
        protected calService: CalculatorsService
    ) {
        super(dataService, DUService, calService)
    }

    /** path */
    getNormalPath(door: JDoor, pos?: DC.Pos) {
        pos = pos == undefined ? door.pos : pos
        let size = door.getPixSize()
        let rectPosArr: DC.Pos[] = this.DUService.pos.getRectPosByCenter(pos, size)
        return rectPosArr
    }

    /** path */
    getNormalPathString(door: JDoor, pos?: DC.Pos) {
        let path = this.getNormalPath(door, pos)
        let pathString = this.DUService.svg.getPath(true, ...path)
        return pathString
    }

    /**  */
    createNewNormal(obj: JDoorInitType) {
        this.dataService.cache.floorplan.createAndMove = true
        let door = new JDoor3D(obj, this.dataService, this.DUService)
        this.dataService.cache.door.createFunc(door)
        return door
    }

    /**  */
    del(door: JDoor) {
        door.destory3D()
        delete this.dataService.doors[door.id]
    }

    protected wallChange(door: JDoor, wall: JWall) {
        wall.doorIDs.push(door.id)
        wall.doorIDs = this.DUService.cal.unique(wall.doorIDs)
    }

    /**  */
    listAbosrb() {
        this.commmonListAbosrb('door')
    }

}