import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ThreeComponent } from './three.component';
import { MouseEventService } from '../svg/common/services/mouse-event.service';
import { RightMenuService } from '../services/right-menu.service';
import { ConsoleService } from '../svg/common/services/console.service';
import { ToggleWindowModule } from '../design-pop/toggle-window/toggle-window.module';

@NgModule({
    imports: [
        CommonModule,
        ToggleWindowModule
    ],
    declarations: [ThreeComponent],
    exports: [ThreeComponent],
    providers: [MouseEventService, RightMenuService, ConsoleService]
})
export class ThreeModule { }
