import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { DomUpdateService } from '../../../services/dom-update.service';
import { JCowall } from '../../common/jdesign/JCowall';

@Component({
  selector: '[cowalls]',
  templateUrl: './cowalls.component.html',
  styleUrls: ['./cowalls.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CowallsComponent implements OnInit {

  constructor(
    private ref: ChangeDetectorRef,
    public dataService: JdesignDataService,
    private DUService: JdesignUtil2dService,
    private domSerive: DomUpdateService,
  ) {
    this.domSerive.svgCowall.subscribe(() => {
      this.ref.detectChanges()
    })
  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('cowalls is ok!!')
    }
  }

  getCowallArr() {
    let cowalls: JCowall[] = this.DUService.cal.getStringArrByNum(this.dataService.cowalls)
    return cowalls
  }

  isDisplayOutLine() {
    // let a = (
    //   this.dataService.cache.floorplan.display.outLabel_inLine || this.dataService.cache.floorplan.display.outLabel_outLine ||
    //   this.dataService.cache.floorplan.display.outLabel_thickness)
    let a = this.dataService.cache.floorplan.display.outLabel
    return a
  }

}
