import { MouseEventService } from "../../services/mouse-event.service";
import { JdesignDataService } from "../../../../services/jdesign-data.service";

export abstract class EventAbstract {
    private _isMouseMove: boolean = false
    /**   */
    get isMouseMove() { return this._isMouseMove }
    set isMouseMove(data) { this._isMouseMove = data }

    constructor(
        public mouseEventService: MouseEventService
    ) {
        this.mouseEventService.getDownEvents().push((e) => {
            this.isMouseMove = false
            if (this.isCondition())
                this.mouseDown(e)
        })

        this.mouseEventService.getMoveEvents().push((e) => {
            if (this.isCondition()) {
                this.mouseMove(e)
                this.isMouseMove = true
            }
        })

        this.mouseEventService.getUpEvents().push((e) => {
            if (this.isCondition()) {
                this.mouseUp(e)
                this.isMouseMove = false
            }
        })
    }

    /**  */
    abstract mouseDown(e: MouseEvent)

    /**  */
    abstract mouseMove(e: MouseEvent)

    /**  */
    abstract mouseUp(e: MouseEvent)

    /**  */
    abstract isCondition(): boolean

}