import { JdesignDataService } from "../../services/jdesign-data.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { JDesign } from "../../svg/common/jdesign/JDesign";
import { JCornerWin3D } from "../../three/jDesign3D/JCornerWin3D";
import { JCornerWin } from "../../svg/common/jdesign/JCornerWin";
import { PopDivService } from "../../services/pop-div.service";

export class CornWinShortFunc {
    constructor(
        protected dataService: JdesignDataService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService,
        protected popService:PopDivService
    ) {
        /*  */
        this.dataService.cache.shortFuncs['delCornWin'] = (obj) => {
            this.delCornWin(obj)
        }
        /*  */
        this.dataService.cache.shortFuncs["isCornWin"] = (obj: JDesign) => {
            return obj.type == 'cornerWin'
        }
        /*  */
        this.dataService.cache.shortFuncs["addCornerWin"] = () => {
            this.addCornerWin()
        }
    }

    /*  */
    delCornWin(cornerWin: JCornerWin) {
        this.calService.cornerWin.del(cornerWin)
        this.domService.allSvg.emit()
    }

    /*  */
    addCornerWin() {
        let data = {
            LDepth: 1600,
            LWidth: 1600,
            DDepth: 1600,
            DWidth: 1600,
            outThickness: 60,
            outWallThickness: this.dataService.cmd.wall.thickness,
            height3D: 1500,
            floorHeight: 500
        }
        let cornerWin = this.calService.cornerWin.createNew(data)
        this.dataService.cache.common.choose = cornerWin
        this.setNormal()
    }

    setNormal() {
        this.dataService.cache.common.mode = 'normal'
        this.dataService.cache.common.blockON = true
        this.dataService.cache.common.firstPos = null
        this.dataService.cmd.cowall.hide = true
        this.domUpdate()
    }

    /** dom */
    domUpdate() {
        this.domService.allSvg.emit()
        this.popService.inputBoxs.emit([])
        this.popService.buttons.emit(null)
    }


}