import { HttpService } from "../../services/http.service";

export class RightDataPanel {
    private rightURL = './init/panelRightWallImg.json'
    constructor(
        private httpService: HttpService,
    ){
        this.getRightData(this.rightURL)
    }

    getRightData(url){
        url = this.httpService.getRealUrl(url)
        this.httpService.getRes(url, 3, true).subscribe((datas: any[]) => {
            return datas;
        })
    }
}