import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JPoint } from "../jdesign/JPoint";
import { JWall } from "../jdesign/JWall";
import { JDesign } from "../jdesign/JDesign";
import { TransPointPosArr, TransWallLines, TransPointLines, TransArcWallLines } from "./transData.cal";


export class AdsorbCal {

    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService
    ) {

    }

    /**
     * 
     * @param pos 
     * @param minDistance 
     * @param isStraight
     * @param wallsMsg 
     * @returns undefined
     */
    posAndWalls(pos: DC.Pos, minDistance: number, isStraight: boolean, wallsMsg: TransWallLines) {
        /** ,undefined */
        if (wallsMsg.lines.length == 0)
            return undefined
        let absorbMsg = this.DUService.absorb.pointAndLines(pos, wallsMsg.lines, isStraight, minDistance)
        /** ,undefined */
        if (absorbMsg == undefined)
            return undefined

        return {
            wall: wallsMsg.walls[absorbMsg.index],
            pos: absorbMsg.pos,
            distance: absorbMsg.distance
        }
    }

    /**
     * 
     * @param pos 
     * @param minDistance 
     * @param wallsMsg 
     * @returns undefined
     */
    posAndWallOutLines(pos: DC.Pos, minDistance: number, wallsMsg: TransWallLines) {
        /** ,undefined */
        if (wallsMsg.lines.length == 0)
            return undefined
        let absorbMsg = this.DUService.absorb.pointAndLines(pos, wallsMsg.lines, false, minDistance)
        /** ,undefined */
        if (absorbMsg == undefined)
            return undefined
        return {
            wall: wallsMsg.walls[absorbMsg.index],
            distance: absorbMsg.distance,
            pos: absorbMsg.pos,
            /** ()  */
            isOutLine: absorbMsg.index % 2 == 1 ? true : false
        }
    }

    /**
     * 
     * @param lineStart 
     * @param lineEnd 
     * @param minDistance 
     * @returns mode'h','v'
     */
    lineAndVerticalLine(lineStart: DC.Pos, lineEnd: DC.Pos, minDistance: number) {
        /**  */
        let hPos = this.DUService.absorb.lineAndRadian(lineStart, lineEnd, 0, minDistance)
        if (hPos != undefined) {
            let distance = this.DUService.pos.getDistance(hPos, lineEnd)
            return {
                pos: hPos,
                mode: 'h',
                distance: distance
            }
        }
        /**  */
        let vPos = this.DUService.absorb.lineAndRadian(lineStart, lineEnd, Math.PI / 2, minDistance)
        if (vPos != undefined) {
            let distance = this.DUService.pos.getDistance(vPos, lineEnd)
            return {
                pos: vPos,
                mode: "v",
                distance: distance
            }
        }
        return undefined
    }

    /**
     * 
     * @param pos 
     * @param minDistance 
     * @param pointsMsg 
     */
    posAndVerticalPoints(pos: DC.Pos, minDistance: number, pointsMsg: TransPointLines) {
        /** ,undefined */
        if (pointsMsg.lines.length == 0)
            return undefined
        let absorbMsg = this.DUService.absorb.pointAndLines(pos, pointsMsg.lines, true, minDistance)
        /** ,undefined */
        if (absorbMsg == undefined)
            return undefined
        return {
            point: pointsMsg.points[absorbMsg.index],
            distance: absorbMsg.distance,
            pos: absorbMsg.pos
        }

    }

    /**
     * 
     * @param pos 
     * @param minDistance 
     * @param pointPosArr 
     * @returns undefined
     */
    posAndPointPosArr(pos: DC.Pos, minDistance: number, pointPosArr: TransPointPosArr) {
        /** ,undefined */
        if (pointPosArr.posArr.length == 0)
            return undefined
        let absorbMsg = this.DUService.absorb.pointAndPoints(pos, pointPosArr.posArr, minDistance)
        /** ,undefined */
        if (absorbMsg == undefined)
            return undefined
        return {
            point: pointPosArr.points[absorbMsg.index],
            pos: absorbMsg.pos,
            distance: absorbMsg.distance
        }
    }

    /**
     * 
     * @param pos 
     * @param minDistance 
     * @param wallsMsg 
     * @returns undefined
     */
    posAndVerticalWallAllLines(pos: DC.Pos, minDistance: number, wallsMsg: TransWallLines) {
        let absorbMsg = this.DUService.absorb.pointAndLines(pos, wallsMsg.lines, true, minDistance)
        if (absorbMsg) {
            let data = {
                wall: wallsMsg.walls[absorbMsg.index],
                distance: absorbMsg.distance,
                pos: absorbMsg.pos,
                line: wallsMsg.lines[absorbMsg.index]
            }
            return data
        }
        return undefined
    }

    /*  */
    lineAndWalls(line: DC.Pos[], rectDistance: number, wallsMsg: TransWallLines) {
        let absorbMsg = this.DUService.absorb.lineAndLine(line, wallsMsg.lines, rectDistance)
        if (absorbMsg) {
            let data = {
                wall: wallsMsg.walls[absorbMsg.index],
                line: wallsMsg.lines[absorbMsg.index],
                distance: absorbMsg.distance,
                pos: absorbMsg.pos,
            }
            return data
        }
        return undefined
    }

}

class WallAbsorb {
    wall: JWall;
    /**  */
    pos: DC.Pos;
    out: boolean;
    radian: number;
    distance: number;
}

export class AbosrbMsg {
    pos: DC.Pos;
    obj: JDesign;
    absorbLineData: any[];
    mode: string;
    radius?: number;
}

export class AbsorbLine {
    pos: DC.Pos;
    absorbObj: JDesign;
    isContinuous?: boolean;
}