import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { JdesignUtil2dService } from '../../../../services/jdesign-util2d.service';
import { DesignCommon as DC } from '../../../common/class/DesignCommon'

@Component({
  selector: '[absorbLines]',
  templateUrl: './absorb-lines.component.html',
  styleUrls: ['./absorb-lines.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AbsorbLinesComponent implements OnInit {

  absorbLine={
    data:null,
    ref:this.ref
  }

  @Output() ABEvent: EventEmitter<object> = new EventEmitter<object>()

  constructor(
    private DUService: JdesignUtil2dService,
    private ref: ChangeDetectorRef,
  ) {

  }

  ngOnInit() {
    this.ABEvent.emit(this.absorbLine)
  }

  /** path */
  getPath(line: { pos: DC.Pos, radian: number, radius?: number }) {
    !line.radius ? line.radius = 100000 : null
    let rayPosA = this.DUService.pos.getRayPos(line.pos, line.radian, line.radius)
    let rayPosB = this.DUService.pos.getRayPos(line.pos, line.radian, -line.radius)
    let path: string = this.DUService.svg.getPath(false, rayPosA, rayPosB)
    return path
  }

}
