import { Component, OnInit,Input } from '@angular/core';

@Component({
  selector: '[cueInfo]',
  templateUrl: './cue-info.component.html',
  styleUrls: ['./cue-info.component.css']
})
export class CueInfoComponent implements OnInit {
  private _cueInfo;
  timer;
  @Input() set cueInfo(data){
    if(data != null){
      this._cueInfo = data;
      clearInterval(this.timer);
      if(this._cueInfo != undefined){
        this.timer = setInterval(() => {
          this.setMessage(this._cueInfo)
        }, 1000);
      }
    } 
  }
  get cueInfo(){return this._cueInfo}
  constructor() { 
    
    
  }

  ngOnInit() {
    // console.log(this._cueInfo)
    
  }

  setMessage(obj){
    obj.time -= 1;
    if (this._cueInfo.time == 0) {
      clearInterval(this.timer);
    }
  }
  hidde(){
    this._cueInfo.time = 0;
    clearInterval(this.timer);
  }
}
