import { JWin } from "../../svg/common/jdesign/JWin";
import { ModelType } from '../classes/dataStruct';
import { BaywinModel } from "../classes/BaywinModel";
import { PopDivService } from '../../services/pop-div.service';
import { THREEDataServer } from "../classes/THREEDataServer";
import { JdesignDataService } from '../../services/jdesign-data.service';
import { JCowall3D } from "./JCowall3D";

declare var THREE: any;

export class JBayWin3D extends JWin {
    public SetDataServerAndOthers(_dataServer: THREEDataServer, _jDataServer: JdesignDataService, _popServer: PopDivService) {
        if (this.dataServer != undefined) {
            return;
        }
        this.dataServer = _dataServer;
        this.popServer = _popServer;
        this.jDataServer = _jDataServer;
        this.OnModelMove.push(this.OnBayWinBeMove.bind(this));
    }


    //=========================================================
    private OnBayWinBeMove() {
        //--------------------------
        if (this.formerWidth != this.size.width||
            this.formerDeep != this.size.height
            ) {
            this.MakeWin();
            this.UpdateDoorWinPos(this.GetWinDoorPos(this.floorHeight));
            let _cowalls: JCowall3D[] = <JCowall3D[]>this.getwall().getCowallArr();
            for (let i = 0; i < _cowalls.length; i++) {
                _cowalls[i].MakeWall(true);
            }
        }
    }

    public MakeWin() {
        //------------------------------------------
        this.DestoryWinDoor(true);
        //this.floorHeight = 500;
        //------------------------------------
        this.Init(ModelType.normalBayWindow);
        //-------------------------------------
        this.dataServer.classesObj.modelTransformer.SetVerticalMovable(true);
        this.MakeBaywindow();

    }

    //=================================================================
    private MakeBaywindow() {
        if (this.baywin == null) {
            this.baywin = new BaywinModel(this.dataServer, this.jDataServer, this.size.width, this.size.height, this.size.height3D, this.outThickness, this.outBoxThickness);
            //---------------------------------------
            this.baywin.paramsValues.windowsillMaterialIndex[ModelType.normalBayWindow] = 0;
        }
        this.baywin.MakeBayWin();
    }

    //=====================================================
    public SetBaywindowStillTexture(_path) {
        this.baywin.SetBaywindowStillTexture(_path);
    }
    //=========================================
    public getBaywindowStillMatIndex() {
        return this.baywin.paramsValues.windowsillMaterialIndex[ModelType.normalBayWindow];
    }
    //=========================================
    public SetBaywindowStillMatIndex(_index) {
        this.baywin.paramsValues.windowsillMaterialIndex[ModelType.normalBayWindow] = _index;
    }
}