/*
 
 
*/
import { Component } from '@angular/core';
import { THREEDataServer } from './classes/THREEDataServer';
import { JdesignDataService } from '../services/jdesign-data.service';
import { DomUpdateService } from '../services/dom-update.service';
import { SceneMaker } from './classes/SceneMaker';
import { JCowall3D } from './jDesign3D/JCowall3D';
import { JCamera3D } from './jDesign3D/JCamera3D';
import { CanvasEventMaster } from './classes/canvasEventMaster';
import { HGDataServer } from './classes/HGModel/HGDataServer';
import { JRoom3D } from './jDesign3D/JRoom3D';
import { JFloor3D } from './jDesign3D/JFloor3D';
import { Rayhitter } from './classes/Rayhitter';
import { PopDivService } from '../services/pop-div.service';
import { JRoof3D } from './jDesign3D/JRoof3D';
import { JdesignUtil2dService } from '../services/jdesign-util2d.service';
import { JDoor3D } from './jDesign3D/JDoor3D';
import { JWin3D } from './jDesign3D/JWin3D';
import { JFurniture3D } from './jDesign3D/JFurniture3D';
import { ModelTransformer } from './classes/ModelTransformer';
import { JWall3D } from './jDesign3D/JWall3D';
import { JCornerWin3D } from './jDesign3D/JCornerWin3D';
import { JBuild3D } from './jDesign3D/JBuild3D';
declare var THREE: any;

@Component({
  selector: '[threeCanvas]',
  templateUrl: './three.component.html',
  providers: [THREEDataServer, HGDataServer],
  styleUrls: ['./three.component.scss']
})
export class ThreeComponent {
  constructor(private dataServer: THREEDataServer,
    private jDataServer: JdesignDataService,
    private domServer: DomUpdateService,
    private popServer: PopDivService,
    private JDUServer: JdesignUtil2dService,
    private hgModelServer: HGDataServer
  ) {
    //===============================
    this.domServer.three.subscribe(() => {
      //
      this.ResizeCanvas();
    });

    //svg==================================
    this.domServer.svgInit.subscribe(() => {
      //seneMaker-----------------------------
      this.dataServer.classesObj.sceneMaker = new SceneMaker(this.dataServer, this.jDataServer, this.JDUServer);
      //-----------------------------
      this.dataServer.classesObj.sceneMaker.StartScene();
      //-----------------------------------
      this.dataServer.classesObj.canvasEventMaster = new CanvasEventMaster(this.dataServer);
      //----------------------------------
      this.dataServer.classesObj.rayHitter = new Rayhitter(this.dataServer, this.jDataServer, this.popServer);
      //-------------------------------------
      this.dataServer.classesObj.modelTransformer = new ModelTransformer(this.dataServer, this.jDataServer);

       
      //3d----------------------------
      this.jDataServer.camera.SetDataServerAndOthers(this.dataServer, this.domServer, this.jDataServer);
      this.jDataServer.camera.AddCameraToScene();
      //-------------------------------
      this.ResizeCanvas();
      // this.dataServer.classesObj.sceneMaker.AnimationScene();

      //-----------------------
      this.jDataServer.camera.updateArr3D.push((_target) => {
        let _currentCamera: JCamera3D = _target;
        _currentCamera.UpdateCameraPosAndRotation();

      });
      //---------------------------------------
      // this.dataServer.classesObj.profileLoader = new ProfileLoader(this.dataServer);
      // this.dataServer.classesObj.profileLoader.StartLoadData(() => {
      //   // console.log(this.dataServer.classesObj.profileLoader.GetDoorFrameData())
      //   //--------------------
      //   this.domServer.threeInit.emit();
      // });
      // 
    });




    //cowall===========================================
    this.jDataServer.cache.cowall.update3D.subscribe(() => {
      //cowall----------------------------------
      let _allCowalls: JCowall3D[] = this.jDataServer.doCowalls().arr();
      for (let i = 0; i < _allCowalls.length; i++) {
        let _theWall: JWall3D = <JWall3D>_allCowalls[i].getWall();
        _theWall.SetDataServerAndOthers(this.dataServer);
        _allCowalls[i].SetDataServerAndOthers(this.dataServer, this.popServer, this.jDataServer);
        _allCowalls[i].MakeWall(false);
      }
      //room------------------------------------
      let _allRooms = this.jDataServer.doRooms().arr();
      for (let i = 0; i < _allRooms.length; i++) {
        let _theRoom: JRoom3D = _allRooms[i];
        _theRoom.SetDataServerAndOthers(this.dataServer);
        //--------------------------------------------------
        let _theFloor3D: JFloor3D = <JFloor3D>_theRoom.getFloor();
        _theFloor3D.SetDataServerAndOthers(this.dataServer, this.domServer, this.popServer, this.jDataServer);
        _theFloor3D.MakeFloor();
        //------------------------------------------------
        let _theRoof3D: JRoof3D = <JRoof3D>_theRoom.getRoof();
        _theRoof3D.SetDataServerAndOthers(this.dataServer, this.domServer, this.popServer, this.jDataServer);
        _theRoof3D.MakeRoof();
      }
      //=================================================
      //-------------------------------------------------
      //this.dataServer.classesObj.wireSelectFrame.destory3D();
      //-----------------------------------
      //this.dataServer.classesObj.currentSelectModel.DeselectAll();
      //-------------------------------
      //this.popServer.panelRight.emit([]);
    });

    //----------------------------------------------------
    this.jDataServer.cache.door.createFunc = (_obj: JDoor3D) => {
      _obj.SetDataServerAndOthers(this.dataServer, popServer, this.jDataServer);
      this.dataServer.creatingLongObj = _obj;
      //-------------------------------
      _obj.MakeDoor(true);
      //-----------------------
      _obj.StartEvent();
    }




    this.jDataServer.cache.cornerWin.createFunc = (_obj: JCornerWin3D) => {
      _obj.SetDataServerAndOthers(this.dataServer, this.jDataServer, popServer);
      this.dataServer.creatingLongObj = _obj;
      _obj.MakeCornerBaywin();
      _obj.StartEvent();
    }

    // this.jDataServer.cache.furniture.rotate(obj,9)

    //----------------------------------------------------
    this.jDataServer.cache.win.createFunc = (_obj: JWin3D) => {
      _obj.SetDataServerAndOthers(this.dataServer, this.jDataServer, popServer);
      this.dataServer.creatingLongObj = _obj;
      //-------------------------------
      _obj.MakeWin(true);
      //-----------------------
      _obj.StartEvent();
    }







    //------------------------------------------------
    this.jDataServer.cache.furniture.createFunc = (_obj: JFurniture3D) => {
      _obj.SetDataServerAndOthers(this.dataServer, this.hgModelServer, this.popServer);
      this.dataServer.creatingLongObj = _obj;
      _obj.MakeFurniture();
      _obj.StartEvent();
    }


    this.jDataServer.cache.build.createFunc = (_obj: JBuild3D) => {
      _obj.SetDataServerAndOthers(this.dataServer,this.jDataServer, this.popServer);
      this.dataServer.creatingLongObj = _obj;
      _obj.MakeBuild();
      _obj.StartEvent();
    }






  }

  private ResizeCanvas() {
    if (this.dataServer.classesObj.sceneMaker == undefined) {
      return;
    }
    if (this.jDataServer.cache.thumbnail.three.isZoom) {
      this.dataServer.canvasSize = this.jDataServer.cache.thumbnail.three.zoomSize
    }
    else {
      this.dataServer.canvasSize = this.jDataServer.cache.thumbnail.three.divSize
    }
    this.dataServer.classesObj.sceneMaker.ResizeScene(this.dataServer.canvasSize);
  }





}
