import { Component, OnInit, Input, ChangeDetectorRef, ElementRef, ChangeDetectionStrategy } from '@angular/core';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { DomUpdateService } from '../../../services/dom-update.service';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { MouseEventService } from '../../common/services/mouse-event.service';
import { PopDivService } from '../../../services/pop-div.service';
import { CalculatorsService } from '../../../services/calculators.service';
import { JCmd } from '../../common/jdesign/JCmd';
import { JFloorplan3D } from '../../../three/jDesign3D/JFloorplan3D';
import { SvgEvent } from '../../common/event/svg.event';
import { RightMenuService } from '../../../services/right-menu.service';
import { HttpService } from '../../../services/http.service';
import { DomSanitizer } from '../../../../../node_modules/@angular/platform-browser';

@Component({
  selector: 'app-block',
  templateUrl: './block.component.html',
  styleUrls: ['./block.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class BlockComponent implements OnInit {

  private _blockData;
  @Input() set block(data) {
    if (data) {
      this.setEvent()
    }
  }

  constructor(
    public dataService: JdesignDataService,
    private DUService: JdesignUtil2dService,
    private domUpdateService: DomUpdateService,
    private ref: ChangeDetectorRef,
    private el: ElementRef,
    private mouseEventService: MouseEventService,
    private popService: PopDivService,
    private calService: CalculatorsService,
    private rightEventService: RightMenuService,
    private httpService: HttpService,
    private sanitizer: DomSanitizer
  ) {
    /**  */
    this.domUpdateService.svgBlock.subscribe(() => {
      // this.dataService.cache.common.initUpdate(['cowallLabel'])
      // this.domUpdateService.mainSvg.emit()
      this.ref.detectChanges()
    })

  }

  ngOnInit() {
    if (this.dataService.cmd.common.initLog) {
      this.DUService.out.redLog('block is ok!!')
    }
    //this.setEvent()
  }

  /**  */
  setEvent() {
    let svgEvent = new SvgEvent(
      this.mouseEventService,
      this.rightEventService,
      this.dataService,
      this.domUpdateService,
      this.DUService,
      this.calService,
      this.popService,
      this.el,
      'svg_block',
      this.httpService,
      this.sanitizer
    )

  }

  /**  */
  isActive() {
    if (this.dataService.cache.common.mode != 'moveWall') {
      this.dataService.cache.common.blockON = true
    }
  }

}
