import { JWin } from "../../svg/common/jdesign/JWin";
import { ModelType } from '../classes/dataStruct';
import { MyUtil } from "../classes/MyUtil";
import { DoorWindowFrame } from "../classes/DoorWindowFrame";
import { PreloadBox } from "../classes/PreloadBox";
import { PopDivService } from '../../services/pop-div.service';
import { THREEDataServer } from "../classes/THREEDataServer";
import { JCowall3D } from "./JCowall3D";
import { JdesignDataService } from '../../services/jdesign-data.service';

declare var THREE: any;

export class JWin3D extends JWin {
    tempDaeModelUrl: string = "https://www.3dhome360.com/LZN/model/window/win001/ch02.DAE";

    public SetDataServerAndOthers(_dataServer: THREEDataServer, _jDataServer: JdesignDataService, _popServer: PopDivService) {
        if (this.dataServer != undefined) {
            return;
        }
        this.dataServer = _dataServer;
        this.popServer = _popServer;
        this.jDataServer = _jDataServer;
        this.OnModelMove.push(this.OnWinBeMove.bind(this));
    }


    /**
    * ==============================================
    */
    private OnWinBeMove() {
        //--------------------------
        if (this.formerWidth != this.size.width ||
            this.formerDeep != this.size.height
        ) {
            this.MakeWin(false);
            this.UpdateDoorWinPos(this.GetWinDoorPos(this.floorHeight));
            let _cowalls: JCowall3D[] = <JCowall3D[]>this.getwall().getCowallArr();
            for (let i = 0; i < _cowalls.length; i++) {
                _cowalls[i].MakeWall(true);
            }
        }
    }

    public MakeWin(_loadDae: boolean) {
        //------------------------------------------
        this.DestoryWinDoor(_loadDae);
        //--------------------------------------------------
        //this.floorHeight = 500;
        //------------------------------------
        this.Init(ModelType.window);
        //-------------------------------------
        this.dataServer.classesObj.modelTransformer.SetVerticalMovable(true);
        //----------------------------------------------------------------------------
        //----------------------------------------------------------------------------------------
        let _allDoorFrameDatas = this.jDataServer.json.winFrame;
        //----------------------------------
        let _firstWinFrameProfileData = _allDoorFrameDatas[0].vertexData;
        this.MakeWinFrame(_firstWinFrameProfileData);

        let _doorFrameWidth = 0;
        if (this.doorWinFrame != null) {
            _doorFrameWidth = this.doorWinFrame.frameWidth;
        }

        let _newWidth = this.size.width - 2 * _doorFrameWidth;
        let _newHeight = this.size.height3D - 2 * _doorFrameWidth;
        let _newDeep = 60;

        //DAEDAE-------------------------------------
        if (_loadDae == true) {
            //-----------------------------------------------------
            this.preloadBox = new PreloadBox(this.dataServer);
            this.preloadBox.MakeBox(_newWidth, _newHeight, _newDeep);
            this.preloadBox.SetYOffset(_doorFrameWidth);
            //---------------------------------------------
            MyUtil.LoadDaeClampInSize(
                this.dataServer,
                this.tempDaeModelUrl,
                {
                    width: _newWidth,
                    height: _newHeight,
                    deep: _newDeep
                },
                (_model) => {
                    if (this.preloadBox != null) {
                        this.doorWinModel = new THREE.Object3D();
                        this.dataServer.sceneCommonObj.scene.add(this.doorWinModel);
                        this.doorWinModel.add(_model);
                        _model.position.y = _doorFrameWidth;
                        this.doorWinModel.position.copy(this.GetWinDoorPos(this.floorHeight));
                        this.doorWinModel.rotation.y = -this.radian;
                        this.preloadBox.DisposePreloadBox();
                    }
                },
                (_progress) => {
                    if (this.preloadBox != null) {
                        this.preloadBox.SetProgress(_progress);
                    }
                }
            )
        }
        //--------------------------
        else {
            if (this.doorWinModel != undefined) {
                MyUtil.ClampModelInSize(this.doorWinModel.children[0], {
                    width: _newWidth,
                    height: _newHeight,
                    deep: _newDeep
                })
            }
        }
    }


    /**
       * 
       * @param _profileData [[1,2],[3,4],[5,6]]
       */
    public MakeWinFrame(_profileData) {
        if (this.doorWinFrame == null) {
            this.doorWinFrame = new DoorWindowFrame(this.dataServer, this.jDataServer);
            this.doorWinFrame.paramsValues.profileMaterialIndex[ModelType.window] = 0;
            this.doorWinFrame.paramsValues.profileShapeIndex[ModelType.window] = 0;
        }
        this.doorWinFrame.MakeFrame(ModelType.window, this.size.width, this.size.height3D, this.size.height, _profileData, true);
    }


}