import { JdesignDataService } from "../../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { DesignCommon as DC } from '../../class/DesignCommon'
import { PopDivService } from "../../../../services/pop-div.service";

export class DrawWallInput {
    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService,
        private popService: PopDivService
    ) {

    }

    /**
     * 
     * @param line 
     * @param distance 
     * @param cb ,
     */
    drawInput(line: DC.Pos[], cb: (pos: DC.Pos) => void) {
        this.popService.inputBoxsCache = []
        let moveLine = this.DUService.pos.getPosTrans(line[0], line[1], -(this.dataService.cmd.cowall.height + (this.dataService.cmd.wall.thickness / 2)))
        let center = this.DUService.pos.getCenterPos(moveLine[0], moveLine[1])
        let distance = this.DUService.pos.getDistance(line[0], line[1])
        distance = Math.round(distance)
        let data = {
            pos: center,
            value: distance,
            index: 15,
            func: (params: number) => {
                let radian = this.DUService.pos.getRadian(line[0], line[1])
                let newPos = this.DUService.pos.getRayPos(this.dataService.cache.wall.ShadowPosArr[0], radian, params)
                cb(newPos)
                this.popService.inputBoxsCache = []
                this.popService.inputBoxs.emit([])
            },
            focus: true
        }
        this.popService.inputBoxsCache.push(data)
        this.popService.setInputBoxs()
    }
}