import { EventAbstract } from "./EventAbstract";
import { MouseEventService } from "../../services/mouse-event.service";
import { JdesignDataService } from "../../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../../services/dom-update.service";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { PopDivService } from "../../../../services/pop-div.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { CornerWinCal } from "../../calculator/cornerWin.cal";
import { DesignCommon as DC } from '../../class/DesignCommon'
import { JCornerWin } from "../../jdesign/JCornerWin";

export class SvgMoveCWin extends EventAbstract {

    private _obj: JCornerWin = null
    private _pos: DC.Pos = null
    private _distancePos: DC.Pos = { x: 0, y: 0 }
    private _absortMsg: DC.AbsorbMsg = null
    private _cachePos: DC.Pos = null

    constructor(
        public mouseEventService: MouseEventService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService,
        private DUService: JdesignUtil2dService,
        private popService: PopDivService,
        private calService: CalculatorsService
    ) {
        super(mouseEventService)
        this.dataService.cache.cornerWin.move = (obj: JCornerWin, pos: DC.Pos) => {
            this.moveObj(obj, pos)
            obj.act3D.move()
            this.domService.svgCornerWin.emit()
        }
        this.dataService.cache.cornerWin.moveEnd = (obj: JCornerWin) => {
            this.dataService.cache.floorplan.createAndMove = false
            this.dataService.cache.common.blockON = false
            this.dataService.cache.common.firstPos = null
            switch (obj.mode) {
                case "move":
                    this.dataService.cache.cornerWin.hideAllCircle = false
                    break;
            }
            // obj.act3D.end()
            this.strongUpdate()
            this.calService.undoRedo.save()
            obj.strongUpdate3D()
            this.cleanData()
        }
    }

    mouseDown() {
    }

    mouseMove(e: MouseEvent) {
        this._obj = <JCornerWin>this.dataService.cache.common.choose
        /** ,,() */
        if (this.dataService.cache.common.firstPos == null) {
            this._pos = {
                x: e.offsetX + this._distancePos.x,
                y: e.offsetY + this._distancePos.y
            }
            switch (this._obj.mode) {
                case "move":
                    this.moveObj(this._obj, this._pos)
                    this._obj.act3D.move()
                    break
                case "leftMove":
                    this.moveLeftSide(this._obj, this._pos)
                    this._obj.act3D.change()
                    break
                case "downMove":
                    this.moveDownSide(this._obj, this._pos)
                    this._obj.act3D.change()
                    break
                case "rightMove":
                    this.moveRightSide(this._obj, this._pos)
                    this._obj.act3D.change()
                    break
                case "upMove":
                    this.moveUpSide(this._obj, this._pos)
                    this._obj.act3D.change()
                    break
            }

        }

        /** , */
        else {
            this._distancePos = {
                x: this._obj.pos.x - e.offsetX,
                y: this._obj.pos.y - e.offsetY
            }
            this.dataService.cache.common.firstPos = null
        }
        this.CWUpdate()
    }

    mouseUp() {
        this._obj = <JCornerWin>this.dataService.cache.common.choose
        this.dataService.cache.cornerWin.moveEnd(this._obj)
    }

    isCondition() {
        return this.dataService.cache.common.mode == 'normal' && this.dataService.cache.common.choose != null && this.dataService.cache.common.choose.type == "cornerWin"
    }

    /**  */
    strongUpdate() {
        this.domService.svgBlock.emit()
        this.domService.svgPoint.emit()
        this.domService.svgCowall.emit()
        this.domService.svgWall.emit()
        this.domService.svgRoom.emit()
        this.domService.svgCornerWin.emit()
    }

    /**  */
    CWUpdate() {
        this.domService.svgCornerWin.emit()
    }

    /**  */
    cleanData() {
        this._pos = null
        this._distancePos = { x: 0, y: 0 }
        this._cachePos = null
        this._obj = undefined
    }

    /**  */
    moveObj(obj: JCornerWin, pos: DC.Pos) {
        this.dataService.cache.cornerWin.hideAllCircle = true
        let msg = this.getAbsorb(pos)
        if (msg.point != null) {
            this.calService.cornerWin.unionPoint(obj, msg.point)
        }
        else {
            this.calService.cornerWin.disconnectPoint(obj)
            obj.pos = msg.pos
        }
    }

    /**  */
    moveSide(obj: JCornerWin, pos: DC.Pos, radian: number, cb: (obj: JCornerWin, distance: number) => void) {
        if (this._cachePos == null) {
            this._cachePos = pos
            return false
        }
        let distance = this.calService.pos.getMoveSideDistance(this._cachePos, pos, radian)
        cb(obj, distance)
        this._cachePos = pos
        return true
    }

    /**  */
    moveLeftSide(obj: JCornerWin, pos: DC.Pos) {
        return this.moveSide(obj, pos, obj.LRadian, (obj, distance) => {
            obj.LDepth += distance
        })
    }

    /**  */
    moveDownSide(obj: JCornerWin, pos: DC.Pos) {
        return this.moveSide(obj, pos, obj.DRadian, (obj, distance) => {
            obj.DDepth += distance
        })
    }

    /**  */
    moveRightSide(obj: JCornerWin, pos: DC.Pos) {
        return this.moveSide(obj, pos, obj.DRadian - (Math.PI / 2), (obj, distance) => {
            obj.DWidth += distance
        })
    }

    /**  */
    moveUpSide(obj: JCornerWin, pos: DC.Pos) {
        return this.moveSide(obj, pos, obj.LRadian + (Math.PI / 2), (obj, distance) => {
            obj.LWidth += distance
        })
    }

    /**  */
    getAbsorb(pos: DC.Pos) {
        let distance = this.dataService.cmd.floorplan.absorb.minDistance * 2
        let pointArr = this.calService.point.getPointsByOnlyNumWalls(2)
        let pointsMsg = this.calService.transData.getPointPosArr(pointArr)
        let absorbMsg = this.calService.absorb.posAndPointPosArr(pos, distance, pointsMsg)
        return {
            pos: absorbMsg ? absorbMsg.point.pos : pos,
            point: absorbMsg ? absorbMsg.point : null
        }
    }

}