import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: '[repeatPathDatas]',
  templateUrl: './repeat-path.component.html',
  styleUrls: ['./repeat-path.component.scss']
})
export class RepeatPathComponent implements OnInit {

  private _pathDatas: LUPathType[]
  private _ColorData: string[] = ['red', 'blue', "green"]
  private _colorInt: number = 0

  @Input() set repeatPathDatas(data: LUPathType[]) {
    this._pathDatas = data
  }
  get repeatPathDatas() { return this._pathDatas }

  constructor() { }

  ngOnInit() {
  }

  getColor() {
    if (this._colorInt > this._ColorData.length) {
      this._colorInt = 0
    }
    this._colorInt += 1
    return this._ColorData[this._colorInt - 1]
  }

}

export class LUPathType {
  paths?: string[];
  transform?: string;
  children?: LUPathType[];
}
