import { JDesign } from "./JDesign";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JRoom } from "./JRoom";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";

export class JFloor extends JDesign {

    /** id */
    private _roomID: string
    /** id */
    get roomID() { return this._roomID }
    set roomID(data) { this._roomID = data }

    /**  */
    private _radian: number
    /**  */
    get radian() { return this._radian }
    set radian(data) { this._radian = data }

    /**  */
    private _size: DC.Size
    /**  */
    get size() { return this._size }
    set size(data) { this._size = data }

    /**  */
    private _imgUrl: string
    /**  */
    get imgUrl() { return this._imgUrl }
    set imgUrl(data) { this._imgUrl = data }

    constructor(
        obj: JFloorInitType,
        public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService
    ) {
        super({ id: obj.id, type: 'floor' }, dataService)
        this.roomID = obj.roomID
        this.radian = obj.radian ? obj.radian : 0
        this.size = obj.size ? obj.size : null
        this.imgUrl = obj.imgUrl ? obj.imgUrl : null
        this.dataService.floors[this.id] = this
    }

    /**  */
    getRoom() {
        let room: JRoom = this.dataService.rooms[this.roomID]
        return room
    }

    /**  */
    getInLine() {
        let room = this.getRoom()
        return room.getInLine()
    }
}

export class JFloorInitType {
    roomID: string;
    radian?: number;
    size?: DC.Size;
    imgUrl?: string;
    id?: string;
}