import { DesignCommon as DC } from '../class/DesignCommon'
import { JPoint } from '../jdesign/JPoint';
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { JPoint3D } from '../../../three/jDesign3D/JPoint3D';
import { JdesignUtil2dService } from '../../../services/jdesign-util2d.service';
import { JWall } from '../jdesign/JWall';
import { JCowall } from '../jdesign/JCowall';
import { JCowall3D } from '../../../three/jDesign3D/JCowall3D';
import { JWall3D } from '../../../three/jDesign3D/JWall3D';
import { CalculatorsService } from '../../../services/calculators.service';
import { JRoom, JRoomInitType } from '../jdesign/JRoom';
import { JRoom3D } from '../../../three/jDesign3D/JRoom3D';
import { JFloor, JFloorInitType } from '../jdesign/JFloor';
import { JFloor3D } from '../../../three/jDesign3D/JFloor3D';
import { JRoof, JRoofInitType } from '../jdesign/JRoof';
import { JRoof3D } from '../../../three/jDesign3D/JRoof3D';
import { SaveType3D } from '../../../three/save3D/SaveType3D';

export class RoomCal {
    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService
    ) {

    }

    /**  */
    createRoomByPosArr(...posArr: DC.Pos[]) {
        let points: JPoint[] = []
        let point: JPoint = null
        /**  */
        for (let i = 0; i < posArr.length; i++) {
            point = new JPoint3D({ pos: posArr[i] }, this.dataService, this.DUService)
            points.push(point)
        }

        /** wallcowall */
        let walls: JWall[] = []
        let cowalls: JCowall[] = []
        let wall: JWall = null
        let cowall: JCowall = null
        for (let i = 0; i < points.length - 1; i++) {
            cowall = new JCowall3D({ pointIDs: [points[i].id, points[i + 1].id] }, this.dataService, this.DUService)
            wall = new JWall3D({ coedgeID: cowall.id }, this.dataService, this.DUService)
            cowall.edgeID = wall.id
            cowalls.push(cowall)
            walls.push(wall)
        }
        /**  */
        cowall = new JCowall3D({ pointIDs: [points[points.length - 1].id, points[0].id] }, this.dataService, this.DUService)
        wall = new JWall3D({ coedgeID: cowall.id }, this.dataService, this.DUService)
        cowall.edgeID = wall.id
        cowalls.push(cowall)
        walls.push(wall)

        /** 
         *  */
        let cowallIDs: string[] = []
        let area: number = this.DUService.pos.getArea(...posArr)
        if (area > 0) {
            for (let i = 0; i < cowalls.length; i++) {
                cowallIDs.push(cowalls[i].id)
            }
            this.calService.cowall.setSort(true, true, ...cowalls)
        } else {
            for (let i = 0; i < cowalls.length; i++) {
                cowallIDs.splice(0, 0, cowalls[i].id)
                cowalls[i].pointIDs = [cowalls[i].pointIDs[1], cowalls[i].pointIDs[0]]
            }
            this.calService.cowall.setSort(false, true, ...cowalls)
        }

        /**  */
        let room: JRoom = this.createRoom({ cowallIDs: cowallIDs }).room
        return {
            points: points,
            cowalls: cowalls,
            walls: walls,
            room: room
        }
    }

    /**  
     *  */
    createRoomByRect(posStart: DC.Pos, posEnd: DC.Pos) {
        let posArr: DC.Pos[] = this.DUService.pos.getRectByTwoPos(posStart, posEnd)
        return this.createRoomByPosArr(...posArr)
    }

    /**  */
    getInPosArr(room: JRoom) {
        let cowalls: JCowall[] = room.getCowallArrBySort()
        let posArr: DC.Pos[] = []
        let inPos: DC.Pos = null
        for (let i = 0; i < cowalls.length; i++) {
            inPos = this.calService.cowall.getInPosArr(cowalls[i])[0]
            if (posArr.length == 0 || !this.DUService.pos.isSamePos(inPos, posArr[posArr.length - 1]))
                posArr.push(inPos)
        }
        return posArr
    }

    /**  */
    getArea(room: JRoom) {
        let inPosArr: DC.Pos[] = this.getInPosArr(room)
        let area: number = this.DUService.pos.getArea(...inPosArr)
        return area
    }

    /** 
     *   */
    createRoom(roomObj: JRoomInitType, floorObj?: JFloorInitType, roofObj?: JRoofInitType) {
        let room: JRoom = new JRoom3D(roomObj, this.dataService, this.DUService)
        let floor: JFloor = new JFloor3D(floorObj ? floorObj : { roomID: room.id }, this.dataService, this.DUService)

        let roof: JRoof = new JRoof3D(roofObj ? roofObj : { roomID: room.id }, this.dataService, this.DUService)

        room.roofID = roof.id
        room.floorID = floor.id
        floor.roomID = room.id
        roof.roomID = room.id

        return {
            room: room,
            floor: floor,
            roof: roof
        }
    }

    /** cowall cowall */
    delCowallID(room: JRoom) {
        let cowalls = room.getCowallArr()
        for (let i = 0; i < cowalls.length; i++) {
            if (cowalls[i])
                cowalls[i].roomID = null
        }
        room.cowallIDs = []
    }

    /**  */
    resetRoomsCowallIDs() {
        for (let key in this.dataService.rooms) {
            let room = <JRoom>this.dataService.rooms[key]
            let cowalls = room.getCowallArr()
            let newCowallIDs: string[] = []
            for (let i = cowalls.length - 1; i >= 0; i--) {
                if (cowalls[i]) {
                    newCowallIDs.splice(0, 0, cowalls[i].id)
                }
            }
            room.cowallIDs = newCowallIDs
        }

    }

    /**  */
    del(room: JRoom) {
        this.dataService.floors[room.floorID].destory3D()
        this.dataService.roofs[room.roofID].destory3D()
        this.dataService.rooms[room.id].destory3D()
        delete this.dataService.floors[room.floorID]
        delete this.dataService.roofs[room.roofID]
        delete this.dataService.rooms[room.id]
    }

    /** floorplan */
    getFloorplanPoints(room: JRoom) {
        let points: JPoint[] = room.getPointArr()
        let floorplanPoints: JPoint[] = []
        for (let i = 0; i < points.length; i++) {
            let cowalls: JCowall[] = points[i].getCowallArr()
            for (let j = 0; j < cowalls.length; j++) {
                if (cowalls[j].roomID == null || cowalls[j].roomID == undefined) {
                    floorplanPoints.push(points[i])
                    break;
                }
            }
        }
        return points
    }

    /**  */
    getCenter(room: JRoom, distance: number) {
        let posArr = this.getInPosArr(room)
        let firstPos: DC.Pos = null
        for (let i = 0; i < posArr.length++; i++) {

        }
    }

    /**  */
    setOppsite(room: JRoom) {
        let cowallArr = room.getCowallArrBySort()
        for (let i = 0; i < cowallArr.length; i++) {
            cowallArr[i].pointIDs = [cowallArr[i].pointIDs[1], cowallArr[i].pointIDs[0]]
            let oldPrev = cowallArr[i].prevID
            let oldNext = cowallArr[i].nextID
            cowallArr[i].prevID = oldNext
            cowallArr[i].nextID = oldPrev
        }
    }

    /**   */
    delParallel(room: JRoom) {
        let cowallArr = room.getCowallArrBySort()
        cowallArr.push(cowallArr[0])
        for (let i = 0; i < cowallArr.length - 1; i++) {
            if ((cowallArr[i].partnerID != null && cowallArr[i + 1].partnerID != null) || (cowallArr[i].partnerID == null && cowallArr[i + 1].partnerID == null)) {
                let pointA = cowallArr[i].getPointEnd()
                let pointWallArr = pointA.getWallArr()
                if (pointWallArr.length == 2) {
                    let radianA = cowallArr[i].getRadian()
                    let radianB = cowallArr[i + 1].getRadian()
                    /*  */
                    if (
                        this.DUService.pos.isSameRadian(radianA, radianB) &&
                        cowallArr[i].getWall().thickness == cowallArr[i + 1].getWall().thickness &&
                        cowallArr[i].getWall().wallType == cowallArr[i + 1].getWall().wallType
                    ) {
                        let wallA = cowallArr[i].getWall()
                        let wallB = cowallArr[i + 1].getWall()
                        let msg = this.calService.wall.overlay.combineWalls(wallA, wallB)
                        if (msg) {
                            return this.delParallel(room)
                        }
                    }
                }
            }
        }
    }

    copyRoom(room: JRoom) {
        let copyFunc = (oldObj, newObj) => {
            for (let key in newObj) {
                if (key != 'constructor' && key != 'id') {
                    newObj[key] = this.DUService.obj.clone(oldObj[key])
                }
            }
        }
        let roomObj = new SaveType3D.JRoom3D()
        copyFunc(room, roomObj)
        let floorObj = new SaveType3D.JFloor3D()
        copyFunc(room.getFloor(), floorObj)
        let roofObj = new SaveType3D.JRoof3D()
        copyFunc(room.getRoof(), roofObj)
        let data: RoomReadyType = {
            roomData: roomObj,
            floorData: floorObj,
            roofData: roofObj,
            roomID: room.id,
            cowallIDs: this.DUService.obj.clone(room.cowallIDs)
        }
        return data
    }

    /**  */
    cutRoom(walls: JWall[]) {

    }

    /* cowalls */
    resetCowalls(rooms?: JRoom[]) {
        if (!rooms)
            rooms = this.dataService.doRooms().arr()
        for (let i = 0; i < rooms.length; i++) {
            let room = rooms[i]
            room.cowallIDs = this.DUService.obj.clone(room.cowallIDs)
        }
    }

    /* room */
    delNoUseRooms(rooms?: JRoom[]) {
        let roomIDs: string[] = []
        if (!rooms)
            rooms = this.dataService.doRooms().arr()
        for (let i = 0; i < rooms.length; i++) {
            /*  */
            let check = false
            let cowalls = rooms[i].getCowallArr()
            if (rooms[i].id == 'room67') {
                console.log(this.DUService.cal.getNewAttrArray(cowalls, 'id'))
                console.log(this.DUService.cal.getNewAttrArray(cowalls, 'roomID'))
            }
            for (let j = 0; j < cowalls.length; j++) {
                if (cowalls[j] && cowalls[j].roomID == rooms[i].id) {
                    check = true
                    break
                }
            }
            if (!check) {
                roomIDs.push(rooms[i].id)
                this.del(rooms[i])
            }
        }
        return roomIDs
    }

}
export class RoomReadyType {
    roomData: any;
    floorData?: any;
    roofData?: any;
    cowallIDs: string[];
    roomID: string;
}