export class SvgUtil {
    /** path */
    getArcPathString(x: number, y: number, r: number, a0: number, a1: number, ccw?: any): string {
        x = +x, y = +y, r = +r;
        let dx = r * Math.cos(a0),
            dy = r * Math.sin(a0),
            x0 = x + dx,
            y0 = y + dy,
            cw = 1 ^ ccw,
            da = ccw ? a0 - a1 : a1 - a0,
            pathString: string = "",
            tau: number = 2 * Math.PI,
            tauEpsilon: number = tau - 1e-6
        // Is the radius negative? Error.
        if (r < 0) throw new Error("negative radius: " + r);
        pathString += "M" + x0 + "," + y0;
        // Does the angle go the wrong way? Flip the direction.
        if (da < 0) da = da % tau + tau;
        // Is this a complete circle? Draw two arcs to complete the circle.
        if (da > tauEpsilon) {
            pathString += "A" + r + "," + r + ",0,1," + cw + "," + (x - dx) + "," + (y - dy) + "A" + r + "," + r + ",0,1," + cw + "," + (x0) + "," + (y0);
        }
        // Is this arc non-empty? Draw an arc!
        else if (da > 1e-6) {
            pathString += "A" + r + "," + r + ",0," + (+(da >= Math.PI)) + "," + cw + "," + (x + r * Math.cos(a1)) + "," + (y + r * Math.sin(a1));
        }
        return pathString

    }

    /**  path*/
    getPosByPath(pathString: string, num): { x: number, y: number } {
        if (pathString == null || pathString == '')
            return null
        let pathArray: string[] = pathString.split(',')
        let mainStr: string = pathArray[num]
        if (mainStr[0] == 'Z')
            return null
        mainStr = mainStr.substr(1, mainStr.length - 1)
        let arr = mainStr.split(' ')
        let pos = { x: parseFloat(arr[0]), y: parseFloat(arr[1]) }
        return pos

    }

    /**  path z*/
    getPath(z: boolean, ...posArr) {
        if (posArr.length == 0 || posArr[0] == null) {
            return ''
        }
        let path = 'M' + posArr[0].x + ' ' + posArr[0].y
        for (let i = 1; i < posArr.length; i++) {
            path += ',' + 'L' + posArr[i].x + ' ' + posArr[i].y
        }
        if (z)
            path += ',' + 'Z'
        return path
    }

    /** path */
    getPaths(...obj: { posArr: any[], z: boolean }[]) {
        let path: string = ''
        for (let i = 0; i < obj.length; i++) {
            path += this.getPath(obj[i].z, ...obj[i].posArr)
        }
        return path
    }

    /** path */
    getAddPath(pos: { x: number, y: number }, isStart: boolean = false) {
        let string = `${pos.x} ${pos.y}`
        if (isStart) {
            let path = `M${string}`
            return path
        }
        else {
            let path = `,L${string}`
            return path
        }
    }

    /**
     * 
     * @param radius 
     * @param lineEnd 
     * @param isBigArc (false,)
     * @param isReverse (false,)
     * @returns {path:,radius:}
     */
    getArcPath(radius: number, lineEnd, isBigArc: boolean = false, isReverse: boolean = false) {
        let bigArc = isBigArc ? 1 : 0
        let reverse = isReverse ? 1 : 0
        let AString = `A${radius} ${radius},0 ${bigArc} ${reverse},${lineEnd.x} ${lineEnd.y}`
        return AString
    }
}