import { JDesignType, JDesign } from "./JDesign";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JWall } from "./JWall";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { JRoom } from "./JRoom";
import { JCowall } from "./JCowall";
import { JDW3D } from "../../../three/jDesign3D/JDW3D";

export abstract class JDWAbstract extends JDesign {

    /** ID */
    protected _wall: string = null;
    /**  id  */
    abstract get wallID()
    abstract set wallID(obj: string)

    /**  */
    private _pos: DC.Pos
    /**  */
    get pos() { return this._pos }
    set pos(obj: DC.Pos) {
        this._pos = obj
    }

    /**  */
    private _size: DC.Size;
    /**  :,mm */
    get size() { return this._size }
    set size(obj: DC.Size) {
        this._size = obj
        //this.strongUpdate()
    }

    /**  */
    private _mirror: boolean;
    /**  */
    get mirror() { return this._mirror }
    set mirror(obj: boolean) {
        this._mirror = obj
    }

    /**  */
    private _out: boolean;
    /**  */
    get out() { return this._out }
    set out(obj: boolean) {
        this._out = obj
    }

    /**  */
    private _distancePer: number;
    /**  */
    get distancePer() { return this._distancePer }
    set distancePer(obj: number) {
        this._distancePer = obj
    }

    /** oldwall */
    private _oldWallID: string;
    /** oldwall */
    get oldWallID() { return this._oldWallID }
    set oldWallID(obj: string) { this._oldWallID = obj }

    /**  */
    private _radian: number;
    /**  */
    get radian() { return this._radian }
    set radian(obj: number) { this._radian = obj }

    /** 3d */
    private _url3D: string
    /** 3d */
    get url3D() { return this._url3D }
    set url3D(obj: string) { this._url3D = obj }

    /**  */
    private _style: string;
    /**  */
    get style() { return this._style }
    set style(obj) { this._style = obj }

    /** svg */
    private _svgImg: string;
    /**  */
    get svgImg() { return this._svgImg }
    set svgImg(data) { this._svgImg = data }

    /**  */
    private _plankType: string;
    /**  */
    get plankType() { return this._plankType }
    set plankType(data) { this._plankType = data }

    /**  */
    protected _digFunc: any = null;
    /**  */
    get digFunc() { return this._digFunc }
    set digFunc(obj: any) { this._digFunc = obj }

    /**  */
    private _floorHeight: number;
    /**  */
    get floorHeight() { return this._floorHeight }
    set floorHeight(data) { this._floorHeight = data }

    /**  */
    private _modelUrl: string;
    /**  */
    get modelUrl() { return this._modelUrl }
    set modelUrl(data) { this._modelUrl = data }

    /**  */
    private _isHaveFrame: boolean
    /**  */
    get isHaveFrame() { return this._isHaveFrame }
    set isHaveFrame(data) { this._isHaveFrame = data }

    /**  */
    dataInit(obj: JDWInitType) {
        this.wallID = obj.wallID ? obj.wallID : null;
        this.size = obj.size ? obj.size : null
        this.distancePer = obj.distancePer ? obj.distancePer : 0.5
        this.mirror = obj.mirror != undefined ? obj.mirror : false
        this.out = obj.out != undefined ? obj.out : true
        this.pos = obj.pos ? obj.pos : { x: 0, y: 0 }
        this.radian = obj.radian ? obj.radian : 0
        this.url3D = obj.url3D ? obj.url3D : null
        this.style = obj.style ? obj.style : ""
        this.svgImg = obj.svgImg ? obj.svgImg : null
        this.floorHeight = obj.floorHeight ? obj.floorHeight : 0
        this.plankType = obj.plankType ? obj.plankType : "normal"
        this.modelUrl = obj.modelUrl ? obj.modelUrl : undefined
        this.isHaveFrame = obj.isHaveFrame != undefined ? obj.isHaveFrame : true
    }

    constructor(design: JDesignType, public dataService: JdesignDataService,
        public DUService: JdesignUtil2dService) {
        super(design, dataService)
    }

    /**  */
    getwall(): JWall {
        let wall: JWall = <JWall>this.dataService.walls[this.wallID]
        return wall
    }


    /**  */
    getPixSize(): DC.Size {
        let newSize: DC.Size = {
            width: this.dataService.cmd.getLengthTransPixel(this.size.width, 'mm'),
            height: this.dataService.cmd.getLengthTransPixel(this.size.height, 'mm'),
        }
        return newSize
    }

    /**  */
    getOriginPos(centerPos = { x: 0, y: 0 }): DC.Pos {
        let b = this.out ? 1 : -1
        let pixSize = this.getPixSize()
        let newPos = { x: centerPos.x - (pixSize.width / 2), y: centerPos.y - (pixSize.height / 2 * b) }
        return newPos
    }

    /**  */
    getRoomArr(): JRoom[] {
        let wall: JWall = this.getwall()
        if (wall == null)
            return null
        let roomArr: JRoom[] = wall.getRoomArr()
        return roomArr
    }

    /**  */
    getRealRadian() {
        if (this.out)
            return this.radian
        else
            return this.radian + Math.PI
    }

    /** ID */
    abstract getWallDWIDs(): string[]

    /**  */
    getCowallArr(): JCowall[] {
        let wall: JWall = this.getwall()
        if (wall == null)
            return null
        let cowallArr: JCowall[] = wall.getCowallArr()
        return cowallArr
    }


    /**  */
    getRectPos(): DC.Pos[] {
        if (this.out)
            this.radian += Math.PI
        let size = this.getPixSize()
        let posArr: DC.Pos[] = this.DUService.pos.getRectPosByCenter(this.pos, size, this.radian)
        return posArr
    }

    /** () */
    getRadian(): number {
        if (this.out)
            return this.radian + Math.PI
        else
            return this.radian
    }

    /**  */
    getRotate(): number {
        let rotate: number = this.getRadian() * 180 / Math.PI
        return rotate
    }

    /**  */
    getOldWall(): JWall {
        let wall: JWall = <JWall>this.dataService.walls[this.oldWallID]
        return wall
    }

    /**  */
    setDig() {
        this.digFunc()
    }

    /**  */
    getPosByWall() {
        let wall = this.getwall()
        let distance = wall.getDistance()
        distance = distance * this.distancePer
        let posArr = wall.getPosArr()
        let radian = wall.getRadian()
        let pos = this.DUService.pos.getRayPos(posArr[0], radian, distance)
        return pos
    }

}

export class JDWInitType {
    wallID?: string;
    size?: DC.Size;
    mirror?: boolean;
    out?: boolean;
    distancePer?: number;
    flag?: number;
    id?: string;
    pos?: DC.Pos;
    radian?:
        number;
    url3D?: string;
    style?: string;
    svgImg?: string;
    floorHeight?: number;
    plankType?: string;
    modelUrl?: string;
    isHaveFrame?: boolean
}