import { JdesignDataService } from "../../../services/jdesign-data.service";

export class AnimationEventListen {



    constructor(
        private dataService: JdesignDataService) {

        this.setAnimation()
    }


    /**  */
    setAnimation() {
        for (let i = 0; i < this.dataService.cache.AnimationFuncs.length; i++) {
            this.dataService.cache.AnimationFuncs[i]()
        }
        // this._actType = null
        requestAnimationFrame(() => {
            this.setAnimation()
        })
    }


}