import { Injectable } from '@angular/core';

@Injectable()
export class MouseEventService {

  private _mouseDownEvents: ((e: MouseEvent) => void)[] = []
  private _mouseMoveEvents: ((e: MouseEvent) => void)[] = []
  private _mouseUpEvents: ((e: MouseEvent) => void)[] = []
  private _mouseWheelEvents: ((e: WheelEvent) => void)[] = []

  constructor() {

  }

  /**  */
  getDownEvents() {
    return this._mouseDownEvents
  }

  /**  */
  doDownEvents(e: MouseEvent) {
    for (let i = 0; i < this._mouseDownEvents.length; i++) {
      this._mouseDownEvents[i](e)
    }
  }

  /**  */
  getMoveEvents() {
    return this._mouseMoveEvents
  }

  /**  */
  doMoveEvents(e: MouseEvent) {
    for (let i = 0; i < this._mouseMoveEvents.length; i++) {
      this._mouseMoveEvents[i](e)
    }
  }

  /**  */
  getUpEvents() {
    return this._mouseUpEvents
  }

  /**  */
  doUpEvents(e: MouseEvent) {
    for (let i = 0; i < this._mouseUpEvents.length; i++) {
      this._mouseUpEvents[i](e)
    }
  }
}
