import { EventAbstract } from "./EventAbstract";
import { MouseEventService } from "../../services/mouse-event.service";
import { JdesignDataService } from "../../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../../services/dom-update.service";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { PopDivService } from "../../../../services/pop-div.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { JFurniture } from "../../jdesign/JFurniture";
import { DesignCommon as DC } from '../../class/DesignCommon'
import { JWall } from "../../jdesign/JWall";
import { JGeometry } from "../../jdesign/JGeometry";
import { JBuild } from "../../jdesign/JBuild";

export class SvgMoveGeometry extends EventAbstract {

    private _pos: DC.Pos = null
    private _distancePos: DC.Pos = { x: 0, y: 0 }
    private _absortMsg
    private _radian: number = null

    constructor(
        public mouseEventService: MouseEventService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService,
        private DUService: JdesignUtil2dService,
        private popService: PopDivService,
        private calService: CalculatorsService
    ) {
        super(mouseEventService)
        this.setMove()
        this.setMoveEnd()

    }

    /**  */
    setMove() {

        let rotateFunc = (obj: JGeometry, radian: number) => {
            obj.radian = radian
            obj.act3D.move()
            this.geometryUpdate()
        }

        let moveFunc = (obj: JFurniture, pos: DC.Pos) => {
            switch (obj.mode) {
                case "move":
                    this.moveAct(obj, pos)
                    break;
                case "rotate":
                    this.rotateAct(obj, pos)
                    break;
            }
            // console.log(obj.pos)
            obj.act3D.move()
            this.geometryUpdate()
        }

        this.dataService.cache.furniture.rotate = (obj: JFurniture, radian: number) => {
            rotateFunc(obj, radian)
        }
        this.dataService.cache.build.rotate = (obj: JBuild, radian: number) => {
            rotateFunc(obj, radian)
        }

        this.dataService.cache.furniture.move = (obj: JFurniture, pos: DC.Pos) => {
            moveFunc(obj, pos)
        }
        this.dataService.cache.build.move = (obj: JBuild, pos: DC.Pos) => {
            moveFunc(obj, pos)
        }
    }

    setMoveEnd() {
        let moveEndFunc = (obj: JGeometry) => {
            this.dataService.cache.floorplan.createAndMove = false
            this.dataService.cache.common.blockON = false
            this.dataService.cache.common.firstPos = null
            obj.act3D.end()
            this.strongUpdate()
            this.calService.undoRedo.save()
            this.cleanData()
        }
        this.dataService.cache.furniture.moveEnd = (obj: JFurniture) => {
            moveEndFunc(obj)
        }
        this.dataService.cache.build.moveEnd = (obj: JBuild) => {
            moveEndFunc(obj)
        }
    }


    mouseDown() { }

    mouseUp(e: MouseEvent) {
        let obj = <JGeometry>this.dataService.cache.common.choose
        switch (obj.type) {
            case "furniture":
                this.dataService.cache.furniture.moveEnd(obj)
                break;
            case "build":
                this.dataService.cache.build.moveEnd(obj)
                break;
        }

    }

    mouseMove(e: MouseEvent) {
        let obj = <JGeometry>this.dataService.cache.common.choose
        let pos = { x: e.offsetX, y: e.offsetY }
        switch (obj.type) {
            case "furniture":
                this.dataService.cache.furniture.move(obj, pos)
                break;
            case "build":
                this.dataService.cache.build.move(obj, pos)
                break;
        }

    }

    isCondition() {
        return this.dataService.cache.common.mode == 'normal' && this.dataService.cache.common.choose && (this.dataService.cache.common.choose.type == "furniture" || this.dataService.cache.common.choose.type == "build")
    }

    /**  */
    getAbsorb(obj: JGeometry, pos: DC.Pos) {
        let distance = obj.getPixR()
        let walls = this.calService.transData.getUnArcWalls()
        let wallsMsg = this.calService.transData.getWallOutLines(walls)
        let msg = this.calService.absorb.posAndWallOutLines(pos, distance, wallsMsg)
        if (msg == null)
            return null

        return {
            pos: msg.pos,
            wall: msg.wall,
            isOutLine: msg.isOutLine
        }
    }

    /**  */
    strongUpdate() {
        this.domService.svgBlock.emit()
        this.domService.svgPoint.emit()
        this.domService.svgCowall.emit()
        this.domService.svgWall.emit()
        this.domService.svgRoom.emit()
    }

    /**  */
    cleanData() {
        this._pos = null
        this._distancePos = { x: 0, y: 0 }
        this._radian = null
    }

    /**  */
    geometryUpdate() {
        this.domService.svgGeometry.emit()
    }

    /**  */
    moveAct(obj: JGeometry, pos: DC.Pos) {
        /**  */
        if (this.dataService.cache.common.firstPos == null) {
            this._pos = {
                x: pos.x + this._distancePos.x,
                y: pos.y + this._distancePos.y
            }
            let absorbMsg: {
                pos: DC.Pos;
                wall: JWall;
                isOutLine: boolean;
            } = undefined
            if (this.dataService.cache.common.isAbosrb)
                absorbMsg = this.getAbsorb(obj, this._pos)
            /**  */
            if (absorbMsg == null) {
                obj.pos = this._pos
            }
            else {
                let posArr = absorbMsg.wall.getPosArr()
                let newPos = this.DUService.pos.getRightAngleIntersect(absorbMsg.pos, posArr[0], posArr[1])
                obj.pos = this.calService.geometry.getPosByWall(obj, absorbMsg.wall, newPos, absorbMsg.isOutLine)
                this.calService.geometry.unionWall(obj, absorbMsg.wall, absorbMsg.isOutLine)
            }
        }
        /**  */
        else {
            this._distancePos = {
                x: obj.pos.x - pos.x,
                y: obj.pos.y - pos.y
            }
            this.dataService.cache.common.firstPos = null
        }
    }

    /**  */
    rotateAct(obj: JGeometry, pos: DC.Pos) {
        if (this.dataService.cache.common.firstPos != null) {
            this._radian = this.DUService.pos.getRadian(obj.pos, { x: pos.x, y: pos.y })
            this.dataService.cache.common.firstPos = null
        }
        let radian = this.DUService.pos.getRadian(obj.pos, { x: pos.x, y: pos.y })
        obj.radian += radian - this._radian
        this._radian = radian
    }
}
