import { Injectable } from '@angular/core';
import { HGWardrobeLoader } from './HGWardrobeLoader';
import { HGX3dLoader } from './HGX3dLoader';
import { HGMapResourcesPreloader } from './HGMapResourcesPreloader';
import { HGShaderPreloader } from './HGShaderPreloader';
declare var THREE: any;
declare var X2JS: any;
 

@Injectable()
export class HGDataServer {
    //three.js
    public sceneObj = {
        lightObjs: [],                          //shader
        cubeMap: null                            //cubeMap.
    }

    //Json=================
    public commonPath = {
        basePath: "https://www.3dhome360.com/ZJ/QuanYouData/",
        x2dPath: "https://www.3dhome360.com/ZJ/QuanYouData/BaseGraph/",
        mt2Path: "https://www.3dhome360.com/ZJ/QuanYouData/Material/",
        texturePath: "https://www.3dhome360.com/ZJ/QuanYouData/Textures/",
        startModelPath: "https://www.3dhome360.com/ZJ/QuanYouData/models/",     //
        startModelName: ""     //
    }

    //
    public ignorNameList = [""];

    //shadershader
    public shaderPreloader: HGShaderPreloader = new HGShaderPreloader(this);
     
   
    
    //x3d
    public x3dLoader: HGX3dLoader = new HGX3dLoader();
    // public clickModelDiandID=null;  //DIID

    public x2js = new X2JS();  //xmljson

 
    //
    wardrobeLoader: HGWardrobeLoader = new HGWardrobeLoader(this);
    //
    mapResourcesPreloader: HGMapResourcesPreloader = new HGMapResourcesPreloader(this);

    //mapjson
    public jsonData;
   
    //
    public loader = {
        fileLoader_GBK: new THREE.FileLoader(),          //gbk
        fileLoader: new THREE.FileLoader(),               //utf8
        textureLoader: new THREE.TextureLoader(),
        cubeTextureLoader: new THREE.CubeTextureLoader()      //cubemap
    }

    constructor() {
        this.loader.fileLoader_GBK.setMimeType("text/html;charset=gb2312");

    }


    








 

}