/**  */
export class CalUtil {
    /**  */
    getStringArrByNum(obj) {
        let newArr: any[] = []
        let key: string = null
        for (key in obj) {
            newArr.push(obj[key])
        }
        return newArr
    }

    /** () */
    unique<T>(arr: T[]): T[] {
        let result: T[] = [], hash = {};
        for (let i = 0, elem; (elem = arr[i]) != null; i++) {
            if (!hash[elem]) {
                result.push(elem);
                hash[elem] = true;
            }
        }
        return result;
    }

    /** () */
    uniqueByAttr<T>(arr: T[], attr: string): T[] {
        let result: T[] = [], hash = {};
        for (let i = 0, elem; (elem = arr[i]) != null; i++) {
            if (!hash[elem[attr]]) {
                result.push(elem);
                hash[elem[attr]] = true;
            }
        }
        return result;
    }

    /*  */
    removeUndefined(arr: any[]) {
        for (let i = arr.length - 1; i >= 0; i--) {
            if (arr[i] == undefined) {
                arr.splice(i, 1)
            }
        }
    }

    /**  i,null */
    isInclude(arr: any[], text: string) {

        for (let i = 0; i < arr.length; i++) {
            if (arr[i] == text)
                return i
        }

        return null
    }

    /**  null, */
    isIncludeByAttr(attr: string | number, text: string | number, ...arr: any[]) {
        for (let i = 0; i < arr.length; i++) {
            if (arr[i][attr] == text)
                return i
        }

        return null
    }

    /** () */
    removeByValue(arr: any[], value) {
        for (let i = arr.length - 1; i >= 0; i--) {
            if (arr[i] == value) {
                arr.splice(i, 1)
            }
        }
    }

    /** () */
    removeByAttr(arr: any[], attr: string, value) {
        for (let i = arr.length - 1; i >= 0; i--) {
            if (arr[i][attr] == value) {
                arr.splice(i, 1)
            }
        }
    }

    /**  */
    isSameArr(arr: any[]) {
        let value = arr[0]
        for (let i = 1; i < arr.length; i++) {
            if (value != arr[i])
                return false
        }
        return true
    }

    /**  */
    isSameArrByAttr(arr: any[], attr: string) {
        let value = arr[0][attr]
        for (let i = 0; i < arr.length; i++) {
            if (value != arr[i][attr]) {
                return false
            }
        }
        return true
    }



    /**     */
    bubbleSort(arr) {
        let len = arr.length;
        for (let i = 0; i < len; i++) {
            for (let j = 0; j < len - 1 - i; j++) {
                if (arr[j] > arr[j + 1]) {        // 
                    let temp = arr[j + 1];        // 
                    arr[j + 1] = arr[j];
                    arr[j] = temp;
                }
            }
        }
    }

    /**   */
    bubbleSortByAttr(arr, attr: string) {
        let len = arr.length
        for (let i = 0; i < len; i++) {
            for (let j = 0; j < len - 1 - i; j++) {
                if (arr[j][attr] > arr[j + 1][attr]) {        // 
                    let temp = arr[j + 1];        // 
                    arr[j + 1] = arr[j];
                    arr[j] = temp;
                }
            }
        }
    }

    /**    */
    bubbleSortBySingleAttr(arr, attr: string) {
        let newArr = this.getNewAttrArray(arr, attr)
        this.bubbleSort(newArr)
        return newArr
    }


    /** ID  */
    bubbleSortID(arr) {
        let arrID: number[] = []
        let len = arr.length;
        for (let i = 0; i < len; i++)
            arrID.push(i);
        for (let i = 0; i < len; i++) {
            for (let j = 0; j < len - 1 - i; j++) {
                if (arr[j] > arr[j + 1]) {        // 
                    let temp = arr[j + 1];        // 
                    arr[j + 1] = arr[j];
                    arr[j] = temp;
                    let tempID = arrID[j + 1];
                    arrID[j + 1] = arrID[j];
                    arrID[j] = tempID;
                }
            }
        }
        return arrID
    }

    /**    */
    quickSort(array) {
        //let array = [8,4,6,2,7,9,3,5,74,5];
        //let array = [0,1,2,44,4,324,5,65,6,6,34,4,5,6,2,43,5,6,62,43,5,1,4,51,56,76,7,7,2,1,45,4,6,7];
        let i = 0;
        let j = array.length - 1;
        let Sort = function (i, j) {

            // 
            if (i == j) {
                return
            };

            let key = array[i];
            let stepi = i; // 
            let stepj = j; // 
            while (j > i) {
                // j <<-------------- 
                if (array[j] >= key) {
                    j--;
                } else {
                    array[i] = array[j]
                    //i++ ------------>>
                    while (j > ++i) {
                        if (array[i] > key) {
                            array[j] = array[i];
                            break;
                        }
                    }
                }
            }

            //  key 
            if (stepi == i) {
                Sort(++i, stepj);
                return;
            }

            //  key
            array[i] = key;

            // 
            Sort(stepi, i);
            Sort(j, stepj);
        }

        Sort(i, j);

        //cb(array)
    }

    /**     */
    quickSortBySingleAttr(array, attr: string) {

        let newArray = this.getNewAttrArray(array, attr)
        this.quickSort(newArray)
        return newArray

    }


    /**  */
    getObjAttrs(obj, attrs: string[]) {
        let newObj = null;
        for (let i = 0; i < attrs.length; i++) {
            if (newObj == null)
                newObj = obj[attrs[0]];
            else
                newObj = newObj[attrs[i]];
        }
        return newObj
    }

    /**   */
    getNewAttrArray(array, attr: string) {
        let a = []
        for (let i = 0; i < array.length; i++) {
            a.push(array[i][attr])
        }
        return a
    }


    /** key */
    getArrayKeys(stringArray, name?) {
        let objKeys = []
        for (let key in stringArray) {
            objKeys.push(key)
        }
        return objKeys
    }

    /*  */
    getNumFromString(str: string) {
        let newStr = str.replace(/[^0-9]/ig, "")
        let num = parseInt(newStr)
        return num
    }
}