import { PopDivService } from "../../../services/pop-div.service";
import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JRoof3D } from "../../../three/jDesign3D/JRoof3D";

export class PlatFondPanel{

    constructor(
        private popService:PopDivService,
        private dataService:JdesignDataService
    ){
        popService.panelRight.subscribe((data) => {
            // console.log(data)
            if (data == null) {
                this.popService.panelRightData.emit(null)
            }
            else{
                if (data.type == 'roof'){
                    this.setRoomData(data)
                }
            }
        })
    }
    //
    setRoomData(obj:JRoof3D){
        let data = [
            {
                type: "slider",
                data: [
                    {
                        name: "",
                        img: this.callBackRoofImgArr(obj, this.dataService.json.roofPics)
                    },
                ]
            },
        ]
        this.popService.panelRightData.emit(data)
    }

    private callBackRoofImgArr(obj: JRoof3D, arr) {
        let argument = {};
        let imgArr = []
        for (let i = 0; i < arr.length; i++) {
            if (i == obj.GetMaterialSelectIndex()) {
                argument = {
                    path: arr[i],
                    details: '',
                    borderClass: true,
                    func: (e, params) => {
                        obj.SetModelTexture(params.path);
                        obj.SetMaterialSelectIndex(i)
                        this.dataService.cache.shortFuncs['saveAction']()
                    }
                }
            }
            else {
                argument = {
                    path: arr[i],
                    details: '',
                    func: (e, params) => {
                        obj.SetModelTexture(params.path);
                        obj.SetMaterialSelectIndex(i)
                        this.dataService.cache.shortFuncs['saveAction']()
                    }
                }
            }
            imgArr.push(argument);
        }
        return imgArr;
    }


}