import { JdesignDataService } from "../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../services/calculators.service";
import { DomUpdateService } from "../../services/dom-update.service";
import { JPoint } from "../../svg/common/jdesign/JPoint";

export class PointShortFunc {
    constructor(
        protected dataService: JdesignDataService,
        protected DUService: JdesignUtil2dService,
        protected calService: CalculatorsService,
        protected domService: DomUpdateService
    ) {
        this.dataService.cache.shortFuncs["combineWall"] = (obj) => {
            this.combineWalls(obj)
        }
        this.dataService.cache.shortFuncs['isCombineWall'] = (obj) => {
            return this.isCombineWalls(obj)
        }
    }

    /**  */
    combineWalls(point: JPoint) {
        try {
            this.calService.point.combineWalls(point)
        }
        catch (e) {
            console.log(e)
        }

        this.domService.allSvg.emit()
    }

    /*  */
    isCombineWalls(point: JPoint) {
        let wallArr = point.getWallArr()
        if (wallArr.length == 2) {
            let radianA = wallArr[0].getRadian()
            let radianB = wallArr[1].getRadian()
            if (this.DUService.pos.isParallelRadian(radianA, radianB)) {
                return true
            }
            else {
                return false
            }
        }
        /**  */
        else {
            return false
        }
    }
}