import { Component, OnInit, Input, ElementRef, AfterViewChecked } from '@angular/core';

@Component({
  selector: '[labelCircle]',
  templateUrl: './label-circle.component.html',
  styleUrls: ['./label-circle.component.scss']
})
export class LabelCircleComponent implements OnInit, AfterViewChecked {

  private _data: {
    r: number;
    overR: number;
    data: {
      pos: { x: number; y: number };
      func: (e: MouseEvent) => void;
    }[];
    transform?: string;
    otherTransform?:string;
  }

  circleClass: string = 'label_circle'

  @Input() set labelCircle(data) {
    this._data = data
    // this.setMouseOver()
    // this.ref.detectChanges()
  }
  get labelCircle() { return this._data }

  constructor(private el: ElementRef) { }

  /**  */
  setRadius(ele: Element, r: number) {
    ele.setAttribute('r', r.toString())
  }


  setMouseOver() {
    let dom = <Document>this.el.nativeElement
    let circles = <any>dom.getElementsByClassName(this.circleClass)
    for (let i = 0; i < circles.length; i++) {
      circles[i].onmouseover = () => {
        this.setRadius(circles[i], this.labelCircle.overR)
      }

      circles[i].onmouseleave = () => {
        this.setRadius(circles[i], this.labelCircle.r)
      }

      circles[i].onmousedown = (e) => {
        this.labelCircle.data[i].func(e)
      }

    }
  }


  ngOnInit() {
  }

  ngAfterViewChecked() {
    this.setMouseOver()
  }

}
