import { JDesign } from './JDesign'
import { JdesignDataService } from '../../../services/jdesign-data.service';
import { JCowall } from './JCowall';
import { DesignCommon as DC } from '../class/DesignCommon'

/**  */
export class JFloorplan extends JDesign {

    /** roomID */
    private _roomsID: string[];
    /** roomID */
    get roomsID() { return this._roomsID }
    set roomsID(obj: string[]) { this._roomsID = obj }

    /** room */
    private _cowallIDs: string[];
    /** room */
    get cowallIDs() { return this._cowallIDs }
    set cowallIDs(obj: string[]) {
        this._cowallIDs = obj
    }

    /**  */
    private _backImg: BackImg
    /**  */
    get backImg() {
        return this._backImg
    }
    set backImg(data) {
        this._backImg = data
    }

    constructor(obj: JFloorplanInitType, public dataService: JdesignDataService) {
        super({ type: "floorplan", mode: obj.mode }, dataService)
        this.roomsID = obj.roomsID != undefined ? obj.roomsID : []
        this.cowallIDs = obj.cowallIDs != undefined ? obj.cowallIDs : []
        this.backImg = obj.backImg != undefined ? obj.backImg : null
    }

    /** cowall */
    getCowallArr() {
        let cowallArr: JCowall[] = []
        for (let i = 0; i < this.cowallIDs.length; i++) {
            let cowall = this.dataService.cowalls[this.cowallIDs[i]]
            cowallArr.push(cowall)
        }
        return cowallArr
    }

    /*  */
    setCenter() {
        // console.log(1111)
        let viewSIze = this.getBackPoint(this.dataService.points)
        if (viewSIze.width != 0 && viewSIze.height != 0) {
            let zoom = this.getBackZoom(viewSIze, {
                width: window.innerWidth - 500,
                height: window.innerHeight - 300
            })
            this.dataService.cmd.floorplan.zoom = zoom
            let left = this.dataService.cache.floorplan.centerData.left - (viewSIze.center.x - Math.abs(this.dataService.cache.floorplan.centerData.left)) * this.dataService.cmd.floorplan.zoom
            let top = this.dataService.cache.floorplan.centerData.top - (viewSIze.center.y - Math.abs(this.dataService.cache.floorplan.centerData.top)) * this.dataService.cmd.floorplan.zoom
            this.dataService.cmd.floorplan.left = left
            this.dataService.cmd.floorplan.top = top
        }
    }


    private getBackZoom(viewSize: DC.Size, rectSize: DC.Size) {
        if (viewSize.width / viewSize.height > rectSize.width / rectSize.height) {
            return rectSize.width / viewSize.width
        }
        else {
            return rectSize.height / viewSize.height
        }
    }
    private getBackPoint(obj) {
        let maxX = 0;
        let maxY = 0
        for (let i in obj) {
            if (obj[i].pos.x > maxX) {
                maxX = obj[i].pos.x
            }
            if (obj[i].pos.y > maxY) {
                maxY = obj[i].pos.y
            }
        }
        let minX = maxX;
        let minY = maxY
        for (let i in obj) {
            if (obj[i].pos.x < minX) {
                minX = obj[i].pos.x
            }
            if (obj[i].pos.y < minY) {
                minY = obj[i].pos.y
            }
        }
        return {
            width: maxX - minX,
            height: maxY - minY,
            center: {
                x: (minX + maxX) / 2,
                y: (minY + maxY) / 2
            }
        }
    }

}


export class JFloorplanInitType {
    id?: string;
    roomsID?: string[];
    flag?: number;
    cowallIDs?: string[];
    backImg?: BackImg;
    mode?: string;

}
class BackImg {
    url: string;
    top: number;
    left: number;
    width: number;
    height: number;
}