import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { JWall } from "../jdesign/JWall";
import { JCowall } from "../jdesign/JCowall";
import { JRoom } from "../jdesign/JRoom";

/**
 * 
 * @class calFlag:0,1,2,3
 */
export class CreateRoomDelCal {

    constructor(
        protected dataService: JdesignDataService,
        protected DUService: JdesignUtil2dService,
        protected calService: CalculatorsService
    ) {

    }

    /**  */
    createRoomByDelWall(delWall: JWall) {
        let roomArr = delWall.getRoomArr()
        let delWallCowalls: JCowall[] = delWall.getCowallArr()
        /** wall */
        let calWalls: JWall[] = []
        for (let i = 0; i < roomArr.length; i++) {
            calWalls.push(...roomArr[i].getWallArr())
        }
        /**  */
        calWalls = this.DUService.cal.uniqueByAttr(calWalls, 'id')
        /** wall */
        this.DUService.cal.removeByAttr(calWalls, 'id', delWall.id)
        /**  */
        for (let i = 0; i < delWallCowalls.length; i++) {
            this.calService.cowall.delRoom(delWallCowalls[i])
        }
        /**  */
        this.calService.wall.absDel(delWall)
        /** cowall */
        let calCowalls = this.calService.transData.getCowallsByWalls(calWalls)
        /** cowall,,wall */
        this.calService.cowall.delNoUse(calCowalls)
        /**  */
        let newRooms = this.calService.createRoom.cutRoom(calWalls)
        /**  */
        for (let i = 0; i < newRooms.length; i++) {
            this.calService.room.delParallel(newRooms[i])
        }
        /** () */
        this.calService.point.delNoUse()
    }

}