import { SaveType3D } from '../../../../three/save3D/SaveType3D'
import { JdesignUtil2dService } from '../../../../services/jdesign-util2d.service';
import { JdesignDataService } from '../../../../services/jdesign-data.service';
import { SaveType } from '../../save/SaveType';
import { JWin } from '../../jdesign/JWin';

export class SaveAct {

    constructor(
        private DUService: JdesignUtil2dService,
        private dataService: JdesignDataService
    ) {

    }

    /**  */
    getSaveData() {
        let obj: any = null
        let svg_data: any[] = []
        /**  */
        let save_func = (data, obj, cb?: (data) => void) => {
            let key: string = null
            for (key in obj) {
                if (key != 'constructor') {
                    obj[key] = this.DUService.obj.strongClone(data[key])
                }
            }
            svg_data.push(obj)
            if (cb != undefined)
                cb(obj)

        }
        /** floorplan */
        obj = new SaveType3D.JFloorPlan3D()
        save_func(this.dataService.floorplan, obj)
        /** cmd */
        obj = new SaveType.JCmd()
        save_func(this.dataService.cmd, obj)
        /**  */
        this.saveTypeData(this.dataService.points, (data) => {
            obj = new SaveType3D.JPoint3D()
            save_func(data, obj)
            // console.log(obj)
        })
        /**  */
        this.saveTypeData(this.dataService.cowalls, (data) => {
            obj = new SaveType3D.JCowall3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.walls, (data) => {
            obj = new SaveType3D.JWall3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.floors, (data) => {
            obj = new SaveType3D.JFloor3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.roofs, (data) => {
            obj = new SaveType3D.JRoof3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.rooms, (data) => {
            obj = new SaveType3D.JRoom3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.doors, (data) => {
            obj = new SaveType3D.JDoor3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.wins, (data: JWin) => {
            obj = data.isBay ? new SaveType3D.JBayWin3D : new SaveType3D.JWin3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.furnitures, (data) => {
            obj = new SaveType3D.JFurniture3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.builds, (data) => {
            obj = new SaveType3D.JBuild3D()
            save_func(data, obj)
        })
        /**  */
        this.saveTypeData(this.dataService.cornerWins, (data) => {
            obj = new SaveType3D.JCornerWin3D()
            save_func(data, obj)
        })
        /* L */
        this.saveTypeData(this.dataService.LWins, (data) => {
            obj = new SaveType3D.JLWin3D()
            save_func(data, obj)
        })
        this.saveTypeData(this.dataService.UWins, (data) => {
            obj = new SaveType3D.JUWin3D()
            save_func(data, obj)
        })
        // /**  */
        // this.saveTypeData(this.dataService.cameras, (data) => {
        //     obj = new SaveType3D.JCamera3D()
        //     save_func(data, obj)
        // })

        // console.log(JSON.stringify(svg_data))
        // console.log(svg_data)
        return svg_data

    }

    /**  */
    private saveTypeData(typeData, cb: (a) => void) {
        for (let key in typeData) {
            cb(typeData[key])
        }
    }
}