import { Component, OnInit, Input } from '@angular/core';
import { PopDivService } from '../../../../services/pop-div.service';

@Component({
  selector: '[sliderBox]',
  templateUrl: './slider-box.component.html',
  styleUrls: ['./slider-box.component.css']
})
export class SliderBoxComponent implements OnInit {
  private _sliderData;
  @Input() set sliderBox(data) {
    // console.log(data)
    for (let i = 0; i < data.length; i++) {
      for (let j = 0; j < data[i].img.length; j++) {
        data[i].img[j].newFunc = (e, params, list) => {
          for (let i = 0; i < list.img.length; i++) {
            list.img[i].borderClass = false;
          }
          params.borderClass = true;
          list.FloorImg = params.path;
          data[i].img[j].func(e, params, list)
        }
      }
    }
    this._sliderData = data;
    // console.log(data)
  }
  get sliderBox() { return this._sliderData; }
  constructor(
    private popService: PopDivService
  ) { }

  ngOnInit() {
  }
  sendMaterial(e, img) {
    this.popService.getMaterial.emit(img)
  }

}
