import { Component, OnInit } from '@angular/core';
import { JdesignDataService } from '../../../../services/jdesign-data.service';
import { JRoom } from '../../../common/jdesign/JRoom';
import { JdesignUtil2dService } from '../../../../services/jdesign-util2d.service';
import { PopDivService } from '../../../../services/pop-div.service';
import { RightMenuService } from '../../../../services/right-menu.service';
import { DomUpdateService } from '../../../../services/dom-update.service';

@Component({
  selector: '[roomsText]',
  templateUrl: './rooms-text.component.html',
  styleUrls: ['./rooms-text.component.scss']
})
export class RoomsTextComponent implements OnInit {

  constructor(
    public dataService: JdesignDataService,
    private DUService: JdesignUtil2dService,
    private popService: PopDivService,
    private rightService: RightMenuService,
    private domService: DomUpdateService,
  ) { }

  ngOnInit() {
  }

  /**  */
  getRoomArr() {
    let rooms: JRoom[] = this.DUService.cal.getStringArrByNum(this.dataService.rooms)
    return rooms
  }

  /**  */
  getTransform(room: JRoom) {
    if (!room.displayPos) {
      return undefined
    }
    else {
      return this.getTextTransform(room.displayPos)
    }
  }

  /**  */
  getArea(room: JRoom) {
    let text = room.areaCache.toFixed(2) + 'm'
    return text
  }

  getClassName(room: JRoom) {
    let className = "room_area font_size "
    className += room.id
    return className
  }

  mouseDown(e, room: JRoom) {
    this.dataService.cache.common.blockON = true
    this.dataService.cache.common.firstPos = { x: e.offsetX, y: e.offsetY }
    // this.dataService.cmd.cowall.hide = true
    this.dataService.cache.common.choose = room
    this.dataService.cache.common.chooseOtherMsg = 'room'
    this.dataService.cache.common.initUpdate()
    this.domService.allSvg.emit()
    if (e.which == 3) {
      this.rightService.room.emit({ e: e, room: room })
    }
    this.dataService.cache.common.chooseOtherMsg = 'text'
  }

  /** textTransform */
  getTextTransform(centerPos: { x: number, y: number }) {
    let transform = this.DUService.pos.getTransfromString(1, 0, 0, 1, centerPos.x, centerPos.y)
    return transform
  }

}
