/**  */
export class CommonCache {
    /**  */
    private _choose: JDesign = null
    /**  */
    get choose() { return this._choose }
    set choose(data) {
        let oldChoose = this._choose
        this._choose = data
        /**  */
        if (oldChoose) {
            oldChoose.choose = false
            oldChoose.chooseUpdate()
        }
        /**  */
        if (data) {
            data.choose = true
            data.chooseUpdate()
        }
        this.chooseUpdate.emit(data)
    }
    chooseOtherMsg: any = undefined
    /** () */
    chooseUpdate: EventEmitter<object> = new EventEmitter<object>()
    /** (normal,drawWall,drawRoom) */
    mode: string = 'normal'
    /**  */
    blockON: boolean = false
    /**  */
    firstPos: DC.Pos = null
    /** 3d */
    threeOn: number = 1
    /**  */
    isAbosrb: boolean = true
    /**  */
    mainUrl: string = ''
    /**  */
    modelMainUrl: string = ''
    /**  */
    getAbsUrl(url: string) {
        let absUrl = url[0] == '/' ? this.mainUrl + url : this.mainUrl + '/' + url
        return absUrl
    }
    /** (,) */
    initUpdate = (noUseKeyArr?: string[]) => {
        noUseKeyArr = noUseKeyArr ? noUseKeyArr : []
        for (let key in this.initFuncs) {
            let same = false
            for (let i = 0; i < noUseKeyArr.length; i++) {
                if (key == noUseKeyArr[i]) {
                    same = true
                    break;
                }
            }
            if (!same) {
                this.initFuncs[key]()
            }
        }
    }
    /**  */
    initFuncs: (() => void)[] = []
    /**  */
    allInit: EventEmitter<object> = new EventEmitter<object>()
}

