import { MouseEventService } from "../../services/mouse-event.service";
import { JdesignDataService } from "../../../../services/jdesign-data.service";
import { DomUpdateService } from "../../../../services/dom-update.service";
import { JdesignUtil2dService } from "../../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../../services/calculators.service";
import { PopDivService } from "../../../../services/pop-div.service";
import { SvgDrawWall } from "./SvgDrawWall";
import { SvgMoveWall } from "./SvgMoveWall";
import { SvgDrawRoom } from "./SvgDrawRoom";
import { SvgMoveBack } from "./SvgMoveBack";
import { SvgMoveDW } from "./SvgMoveDW";
import { SvgMoveGeometry } from "./SvgMoveGeometry";
import { SvgMoveCWin } from "./SvgMoveCWin";
import { SvgMoveCamera } from "./svgMoveCamera";
import { SvgAbsorbEvent } from "../absorb/svg.absorb.event";
import { SvgInputEvent } from "../input/svg.input.event";
import { SvgMovePoint } from "./SvgMovePoint";
import { SvgMoveLUWin } from "./SvgMoveLUWin";
import { SvgMoveParamsModel } from "./paramsModel/SvgMoveParamsModel";

export class SvgMouseEvent {

    constructor(
        private mouseEventService: MouseEventService,
        private dataService: JdesignDataService,
        private domService: DomUpdateService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService,
        private popService: PopDivService,
        private svgAbsorbEvent: SvgAbsorbEvent,
        private svgInputEvent: SvgInputEvent
    ) {
        let moveParamsModel = new SvgMoveParamsModel(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService)
        let drawWall = new SvgDrawWall(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService, svgAbsorbEvent.drawWallAbsorb, svgInputEvent.wallInput)
        let moveWall = new SvgMoveWall(this.mouseEventService, this.dataService, this.calService, this.domService, this.popService, this.DUService, this.svgAbsorbEvent.moveWallAbosrb)
        let drawRoom = new SvgDrawRoom(this.mouseEventService, this.dataService, this.domService, this.calService, this.DUService, this.popService)
        let moveBack = new SvgMoveBack(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService)
        let moveDW = new SvgMoveDW(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService, this.svgAbsorbEvent.dwAbsorb)
        let moveGeometry = new SvgMoveGeometry(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService)
        let moveCornerWin = new SvgMoveCWin(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService)
        let moveCamera = new SvgMoveCamera(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService)
        let movePoint = new SvgMovePoint(this.mouseEventService, this.dataService, this.calService, this.domService, this.popService, this.DUService)
        let moveLUWin = new SvgMoveLUWin(this.mouseEventService, this.dataService, this.domService, this.DUService, this.popService, this.calService)
    }

}