import { JdesignDataService } from "../../../services/jdesign-data.service";
import { JdesignUtil2dService } from "../../../services/jdesign-util2d.service";
import { CalculatorsService } from "../../../services/calculators.service";
import { JGeometry, JGeometryInit } from "../jdesign/JGeometry";
import { JGeometry3D } from "../../../three/jDesign3D/JGeometry3D";
import { JWall } from "../jdesign/JWall";
import { DesignCommon as DC } from '../class/DesignCommon'
import { JFurniture } from "../jdesign/JFurniture";
import { JFurniture3D } from "../../../three/jDesign3D/JFurniture3D";
import { JBuild } from "../jdesign/JBuild";
import { JBuild3D } from "../../../three/jDesign3D/JBuild3D";

export class GeometryCal {
    constructor(
        private dataService: JdesignDataService,
        private DUService: JdesignUtil2dService,
        private calService: CalculatorsService,
    ) {

    }

    /** , */
    createNew(type: string, obj: JGeometryInit) {
        switch (type) {
            case 'furniture':
                this.dataService.cache.floorplan.createAndMove = true
                let furniture: JFurniture = new JFurniture3D(obj, this.dataService, this.DUService)
                this.dataService.cache.furniture.createFunc(furniture)
                return furniture
            case "build":
                this.dataService.cache.floorplan.createAndMove = true
                let build: JBuild = new JBuild3D(obj, this.dataService, this.DUService)
                this.dataService.cache.build.createFunc(build)
                return build
        }

    }

    /**  */
    getBox(geometry: JGeometry, pos?: DC.Pos) {
        let radian = pos == undefined ? 0 : geometry.radian
        pos == undefined ? pos = { x: 0, y: 0 } : null
        let posArr = this.DUService.pos.getRectPosByCenter(pos, geometry.getPixSize(), radian)
        return posArr
    }

    /** path */
    getPath(geometry: JGeometry) {
        let posArr = this.getBox(geometry)
        let path = this.DUService.svg.getPath(true, ...posArr)
        return path
    }

    /**  */
    wallChange(geometry: JGeometry, wall: JWall) {
        wall.geometryIDs.push(geometry.id)
        wall.geometryIDs = this.DUService.cal.unique(wall.geometryIDs)
    }

    /**  */
    unionWall(geometry: JGeometry, wall: JWall, out: boolean) {
        let a = out ? -1 : 1
        geometry.wallID = wall.id
        geometry.radian = wall.getRadian()
        this.wallChange(geometry, wall)
        let distance = wall.getDistance()
        /**  */
        let pos = this.DUService.pos.getRayPos(geometry.pos, geometry.radian + Math.PI, a * (geometry.size.height / 2 + wall.thickness / 2))
        let newDistance = this.DUService.pos.getDistance(pos, wall.getCowall().getPointStart().pos)
        geometry.distancePer = newDistance / distance
        geometry.radian = wall.getRadian() + (a == 1 ? 0 : Math.PI)
        geometry.wallOut = out
    }

    /**  (,) */
    getPosByWall(geometry: JGeometry, wall: JWall, pos: DC.Pos, out: boolean) {
        let a = out ? -1 : 1
        let radian = wall.getRadian()
        let newPos = this.DUService.pos.getRayPos(pos, radian + (Math.PI / 2), a * ((wall.thickness / 2) + (geometry.getPixSize().height / 2)))
        return newPos
    }

    del(obj: JGeometry) {
        obj.destory3D()
        console.log(obj.type)
        switch (obj.type) {
            case 'furniture':
                delete this.dataService.furnitures[obj.id]
                break;
            case "build":
                delete this.dataService.builds[obj.id]
        }

    }
}