import { Component, OnInit,Input } from '@angular/core';

@Component({
  selector: '[inlineText]',
  templateUrl: './inline-text.component.html',
  styleUrls: ['./inline-text.component.css']
})
export class InlineTextComponent implements OnInit {
  private _inlineText;
  @Input() set inlineText(data) {
    // console.log(data)
    this._inlineText = data;
  }
  get inlineText(){return this._inlineText}
  constructor() { }

  ngOnInit() {
  }

}
