import { JRoom } from "../../svg/common/jdesign/JRoom";
import { THREEDataServer } from "../classes/THREEDataServer";
import { JFloor3D } from "./JFloor3D";

export class JRoom3D extends JRoom {
    public wallHeight: number = 2800;
    private dataServer: THREEDataServer;

    public SetDataServerAndOthers(_dataServer: THREEDataServer, ) {
        if (this.dataServer != undefined) {
            return;
        }
        this.dataServer = _dataServer;

        this.chooseFuncArr.push(() => {
            // //-------------------------------
            // this.dataServer.classesObj.currentSelectModel.Select(this.selector);
            let _floorObj: JFloor3D = <JFloor3D>this.getFloor();
            this.dataServer.classesObj.currentSelectModel.Select(_floorObj.floorMesh);
            _floorObj.OnSelected();

        })
        this.noChooseFuncArr.push(() => {
            // this.OnUnselected();
            this.dataServer.classesObj.rayHitter.OnNothingHit();
        })
    }

}